import React from 'react';
import { Platform, View, Text, StyleSheet, Image, ScrollView } from 'react-native';
import { useLanguage } from '../LanguageContext';

const Section3Page5 = () => {
    const { language } = useLanguage();

    return (
        <View style={Platform.OS === 'web' ? styles.webcontainer : styles.container}>
            <Image
                source={require('../images/parcham.png')}
                style={styles.backgroundImage}
                resizeMode="contain"
            />
            <View style={styles.overlay} />

            <ScrollView contentContainerStyle={styles.scrollContent}>
                <View style={styles.listContainer}>
                    <Text style={styles.listItem}>
                        {language === 'Тоҷики' ? 'Моддаи 28. ҳуқуқи умумӣ, баробар ва бевоситаи интихобот' : 'Статья 28. Право на всеобщие, равные и прямые выборы'}
                    </Text>
                    <Text style={styles.textItem}>
                        {language === 'Тоҷики' ? 'Интихоботи вакилон ба Ҷамоат умумӣ аст. Шаҳрвандони Ҷумҳурии Тоҷикистон қатъи назар аз миллат, нажод, ҷинс, забон, эътиқоди динӣ, мавқеи сиёсӣ, вазъи иҷтимоӣ, таҳсилот ва молу мулк аз синни 18 ҳуқуқи интихоб кардан ва аз синни 20 ҳуқуқи интихоб шудан ба Ҷамоатро доранд. (ҚҶТ аз 30.05.17с., №1431) \n Интихобкунандагон дар асоси баробар дар интихобот иштирок мекунанд ва ҳар кадоми онҳо як овоз доранд. \n Вакилони Ҷамоат аз тарафи шаҳрвандон бевосита интихоб карда мешаванд. \n Овоздиҳӣ дар интихоботи вакилони Ҷамоат озод ва пинҳонӣ сурат мегирад.' : ' Выборы депутатов в Джамоат являются всеобщими. Граждане Республики Таджикистан, независимо от национальности, расы, пола, языка, вероисповедания, политического статуса, социального положения, образования и имущества, имеют право избирать с 18 лет и быть избранными с 20 лет в Джамоат. (ЗРТ от 30.05.17 г., №1431) \n Избиратели участвуют в выборах на равных основаниях и каждый из них имеет один голос. \n Депутаты Джамоата избираются непосредственно гражданами. \n Голосование на выборах депутатов Джамоата осуществляется свободно и тайно.'}
                    </Text>
                    <Text style={styles.listItem}>
                        {language === 'Тоҷики' ? 'Моддаи 29. Ошкорбаёнӣ ҳангоми тайёрӣ ва гузаронидани интихобот' : 'Статья 29. Гласность при подготовке к выборам и их проведении'}
                    </Text>
                    <Text style={styles.textItem}>
                        {language === 'Тоҷики' ? ' Тайёрӣ ва гузаронидани интихоботи Ҷамоат аз тарафи комиссияи интихоботи Ҷамоат ва комиссияҳои ҳавзавии интихоботи вакилони Ҷамоат ошкоро сурат мегирад. \n Комиссияи интихоботи Ҷамоат ва комиссияҳои ҳавзавии интихоботи вакилони Ҷамоат ба шаҳрвандон аз фаъолияти худ хабар медиҳанд ва ҷамъбасти интихоботро эълон мекунанд.' : ' проведении \n Подготовка и проведение выборов в Джамоат осуществляются гласно комиссией по выборам в Джамоат и окружными избирательными комиссиями по выборам депутатов Джамоата. \n Избирательная комиссия Джамоата и избирательные комиссии по выборам депутатов Джамоата сообщают гражданам о своей деятельности и объявляют итоги выборов.'}
                    </Text>
                    <Text style={styles.listItem}>
                        {language === 'Тоҷики' ? 'Моддаи 30. Таъйини интихоботи Ҷамоат (ҚҶТ аз 30.05.17с., №1431)' : 'Статья 30. Назначение выборов Джамоата'}
                    </Text>
                    <Text style={styles.textItem}>
                        {language === 'Тоҷики' ? ' Интихоботи вакилони Ҷамоат на дертар аз 40 рўзи ба охир расидани мўҳлати ваколати вакилони Ҷамоати даъвати амалкунанда бо қарори Ҷамоат таъйин карда мешавад. (ҚҶТ аз 30.05.17с., №1431) \n Маблағгузории интихоботи Ҷамоат аз ҳисоби буҷети Ҷамоат сурат мегирад.' : ' Выборы депутатов Джамоата назначаются решением Джамоата не позднее чем за 40 дней до истечения срока полномочий депутатов Джамоата действующего созыва. \n Финансирование выборов Джамоата осуществляется за счет бюджета Джамоата.'}
                    </Text>
                    <Text style={styles.listItem}>
                        {language === 'Тоҷики' ? 'Моддаи 31. Ташкили ҳавзаҳои интихоботи Ҷамоат (ҚҶТ аз 30.05.17с., №1431)' : ' Статья 31. Организация избирательных округов Джамоата (ЗРТ от 30.05.17 г., №1431)'}
                    </Text>
                    <Text style={styles.textItem}>
                        {language === 'Тоҷики' ? ' Барои интихоботи вакилони Ҷамоат на кам аз 15 ва на зиёда аз 40 ҳавзаи интихобот ташкил карда мешавад. \n Шумораи ҳавзаҳои интихоботро Ҷамоат муқаррар менамояд. \n Аз ҳар ҳавзаи интихобот як вакили Ҷамоат интихоб карда мешавад.' : ' Для выборов депутатов Джамоата организуются не менее 15 и не более 40 избирательных округов. \n Количество избирательных округов устанавливается Джамоатом. \n От каждого избирательного округа избирается один депутат Джамоата.'}
                    </Text>
                    <Text style={styles.listItem}>
                        {language === 'Тоҷики' ? 'Моддаи 32. Ташкили комиссияи интихоботи Ҷамоат (ҚҶТ аз 30.05.17с., №1431)' : 'Статья 32. Формирование избирательной комиссии Джамоата'}
                    </Text>
                    <Text style={styles.textItem}>
                        {language === 'Тоҷики' ? ' Барои ташкил ва гузаронидани интихоботи Ҷамоат комиссияи интихоботи Ҷамоат аз тарафи Ҷамоат таъсис дода мешавад. \n Комиссияи интихоботи Ҷамоат на дертар аз панҷ рўзи баъди таъйин гардидани интихобот дар ҳайати 7-13 нафар ташкил карда мешавад. (ҚҶТ аз 30.05.17с., №1431) \n Комиссияи интихоботи Ҷамоат дар ҳайати раис, муовини раис, котиби Комиссия ва аъзои он интихоб карда мешавад.' : ' Для организации и проведения выборов Джамоата создается избирательная комиссия Джамоатом. \n Избирательная комиссия Джамоата создается не позднее пяти дней после назначения выборов в составе 7-13 человек. \n Избирательная комиссия Джамоата избирается в составе председателя, заместителя председателя, секретаря комиссии и ее членов.'}
                    </Text>
                    <Text style={styles.listItem}>
                        {language === 'Тоҷики' ? 'Моддаи 33. Ваколатҳои комиссияи интихоботи Ҷамоат' : 'Статья 33. Полномочия избирательной комиссии Джамоата'}
                    </Text>
                    <Text style={styles.textItem}>
                        {language === 'Тоҷики' ? ' Комиссияи интихоботи Ҷамоат дорои ваколатҳои зерин мебошад: \n 1) иҷрои Қонуни мазкурро дар бахши интихобот назорат менамояд, татбиқи якхелаи онро таъмин мекунад; \n 2) ҳавзаҳои интихоботиро бо назардошти шумораи интихобкунандагон дар ҳудуди шаҳрак ва деҳот мутобиқи талаботи Қонуни мазкур ташкил мекунад ва ба онҳо ному рақам мегузорад; \n 3) комиссияҳои ҳавзавии интихоботро ташкил мекунад; (ҚҶТ аз 16.04.12с., №827) \n 4) бюллетени намунааш муқарраршудаи интихоботро тасдиқ мекунад ва комиссияҳои ҳавзавии интихоботро бо бюллетенҳои интихобот таъмин менамояд; \n 5) масъалаҳои таъминоти моддию техникии интихоботро ҳал менамояд; \n 6) вакилони интихобшударо ба қайд мегирад, натиҷаи интихоботи Ҷамоатро ҷамъбаст мекунад, оид ба ҷамъбасти интихобот ба аҳолӣ, мақомоти иҷроияи маҳаллии ҳокимияти давлатӣ хабар медиҳад; \n 7) овоздиҳӣ ва интихоботи такрорӣ, ҳамчунин интихоботи вакилро ба ҷои вакили хориҷшуда мутобиқи талаботи Қонуни мазкур ташкил мекунад; \n 8) ҳуҷҷатҳои вобаста ба гузаронидан ва ташкили интихоботро ба бойгонӣ месупорад; \n 9) арзу шикоятҳоро доир ба масъалаҳои тайёрӣ ва гузаронидани интихобот баррасӣ карда, оид ба онҳо қарор қабул мекунад; \n 10) мутобиқи санадҳои меъёрии ҳуқуқӣ ваколатҳои дигарро вобаста ба интихобот анҷом медиҳад. \n Комиссияи интихоботи Ҷамоат дар доираи ваколатҳои худ қарор қабул мекунад' : ' Избирательная комиссия Джамоата имеет следующие полномочия: \n 1) контролирует исполнение настоящего Закона в части выборов, обеспечивает его единообразное применение; \n 2) в соответствии с требованием настоящего Закона формирует избирательные округа с учетом количества избирателей в пределах посёлков и сёл и устанавливает их наименование и нумерацию; \n 3) организует окружные избирательные комиссии; (ЗРТ от 16.04.12г., №827) \n 4) утверждает избирательный бюллетень установленного образца и обеспечивает избирательные окружные комиссии избирательными бюллетенями; \n 5) решает вопросы материально- технического обеспечения выборов; \n 6) регистрирует избранных депутатов, подводит итоги выборов Джамоата, информирует население, местный исполнительный орган государственной власти об итогах выборов; \n 7) организует повторные голосование и выборы, а также выборы депутата на место выбывшего депутата в соответствии с настоящим Законом; \n 8) обеспечивает сдачу документов, связанных с организацией и проведением выборов, в архив; \n 9) рассматривает заявления и жалобы по вопросам подготовки и проведения выборов, принимает по ним решение; \n 10) осуществляет другие полномочия, связанные с выборами, в соответствии с нормативными правовыми актами. \n Избирательная комиссия Джамоата принимает решение в пределах своих полномочий. (ЗРТ от 16.04.12г., №827)'}
                    </Text>
                    <Text style={styles.listItem}>
                        {language === 'Тоҷики' ? 'Моддаи 34. Комиссияи ҳавзавии интихобот (ҚҶТ аз 30.05.17с., №1431)' : 'Статья 34. Окружная избирательная комиссия (ЗРТ от 30.05.17 г., №1431)'}
                    </Text>
                    <Text style={styles.textItem}>
                        {language === 'Тоҷики' ? ' Комиссияи ҳавзавии интихоботро комиссияи интихоботи Ҷамоат на дертар аз 10 рўзи баъди таъйини интихоботи Ҷамоат дар ҳайати 5-11 нафар ташкил намуда, аз ҷумла раис, муовини раис, котиби комиссияро интихоб мекунад. (ҚҶТ аз 30.05.17с., №1431) \n Комиссияи ҳавзавии интихобот рўйхати интихобкунандагонро тартиб медиҳад; интихобкунандагонро ба рўйхати интихобкунандагони ҳавзаи интихоботӣ шинос менамояд; номзадҳоро ба қайд мегирад; аҳолиро дар бораи маҳалли ҷойгиршавии комиссияи ҳавзавии интихобот, вақти кори он, инчунин рўзи интихобот, ҷойи овоздиҳӣ хабардор мекунад; бинои овоздиҳӣ, қуттиҳои овоздиҳӣ ва дигар таҷҳизоти интихоботро тайёр мекунад; овоздиҳиро дар рўзи интихобот ташкил мекунад; овозҳои интихобкунандагонро дар ҳавзаҳои интихобот ҳисоб менамояд. \n Комиссияи ҳавзавии интихобот дар доираи ваколатҳои худ қарор қабул мекунад, ки он дар шакли протокол ба расмият дароварда мешавад. (ҚҶТ аз 30.05.17с., №1431)' : ' Окружная избирательная комиссия избирается избирательной комиссией Джамоата не позднее чем за 10 дней после назначения выборов Джамоата в составе 5-11 человек, в том числе председателя, заместителя председателя и секретаря комиссии. \n Окружная избирательная комиссия составляет список избирателей; ознакамливает избирателей со списком избирателей избирательного округа; регистрирует кандидатов, информирует население о местонахождении окружной избирательной комиссии, времени ее работы, а также о дне выборов, месте голосования; подготавливает избирательные здания, избирательные ящики и другое избирательное оборудование; организует голосование в день выборов; подсчитывает голоса избирателей в избирательных округах. Окружная избирательная комиссия в пределах своих полномочий принимает решение, которое оформляется в форме протокола. (ЗРТ от 30.05.17 г., №1431)'}
                    </Text>
                    <Text style={styles.listItem}>
                        {language === 'Тоҷики' ? 'Моддаи 35. Тартиби пешбарии номзадҳо ба вакили Ҷамоат' : 'Статья 35. Порядок выдвижения кандидатов в депутаты Джамоата'}
                    </Text>
                    <Text style={styles.textItem}>
                        {language === 'Тоҷики' ? ' Номзадҳо ба вакили Ҷамоат ба таври худпешбарӣ, бо ҷамъ намудани имзоҳои на камтар аз 5 фоизи интихобкунандагони ҳавзаи дахлдор бо пешниҳоди ариза ба комиссияи ҳавзавии интихобот сурат мегирад. (ҚҶТ аз 30.05.17с., №1431) \n Пешбарии номзадҳо 25 рўз пеш аз интихобот оғоз шуда, 15 рўз пеш аз рўзи овоздиҳӣ ба охир мерасад. (ҚҶТ аз 30.05.17с., №1431)' : 'Выдвижение кандидатов в депутаты Джамоата происходит путем самовыдвижения и сбором подписей не менее 5 процентов избирателей соответствующего округа по представлению заявления в окружную избирательную комиссию. (ЗРТ от 30.05.17 г., №1431) \n Выдвижение кандидатов начинается за 25 дней до начала выборов и заканчивается за 15 дней до голосования.'}
                    </Text>
                    <Text style={styles.listItem}>
                        {language === 'Тоҷики' ? 'Моддаи 36. Талабот нисбати номзадҳо ба вакили Ҷамоат' : 'Статья 36. Требования к кандидатам в депутаты Джамоата'}
                    </Text>
                    <Text style={styles.textItem}>
                        {language === 'Тоҷики' ? ' Ҳар як шаҳрванди Ҷумҳурии Тоҷикистон, ки синни ў аз 20 кам набошад ва дар ҳудуди шаҳрак ва деҳоти дахлдор на камтар аз 3 соли охир истиқомат дошта бошад ва забони давлатиро донад, метавонад номзад ба вакили Ҷамоат пешбарӣ шавад. (ҚҶТ аз 01.08.12с., №8994 (ҚҶТ аз 30.05.17с., №1431) \n Ходимони касбии ташкилотҳо ва иттиҳодияҳои динӣ, хизматчиёни ҳарбии Қувваҳои Муссаллаҳ, дигар қўшуну сохторҳои ҳарбӣ, судяҳо, прокурорҳо, шахсони ҳайати қаторӣ ва роҳбарикунандаи мақомоти корҳои дохилӣ, мақомоти гумрук, кормандони ҳифзи ҳуқуқи Агентии назорати давлатии молиявӣ ва мубориза бо коррупсия ва Агентии назорати маводи нашъаовари назди Президенти Ҷумҳурии Тоҷикистон дар ҳолати аз вазифа ба истеъфо рафтанашон метавонанд номзад ба вакили Ҷамоат пешбарӣ шаванд. \n Шаҳрвандони зерин ҳуқуқи ба вакили Ҷамоат номзад шуданро надоранд: \n 1) мутобиқи ҳалномаи суд ғайриқобили амал дониста шудаанд; \n 2) дар асоси ҳукми суд дар ҷойҳои маҳрумӣ аз озодӣ мебошанд; \n 3) бо қарори суд дар муассисаҳои табобатӣ маҷбурӣ қарор доранд; \n 4) ҷиноятҳои вазнин ва махсусан вазнини қасдона содир кардаанд (қатъи назар аз гузаштани мўҳлат ва бардошта шудани доғи судӣ); \n 5) барои содир кардани ҷиноятҳои вазнин ва махсусан вазнин айбдор шудаанд ё дар кофтуков мебошанд. \n Номзад ба вакили Ҷамоат узви комиссияи интихоботӣ Ҷамоат, инчунин комиссияи ҳавзавии интихобот, ки овоздиҳиро дар ҳавзаи дахлдори интихобот ташкил мекунад, буда наметавонад. Шахсе, ки номзадиаш ба вакили пешбарӣ шудааст ва узви яке аз ин комиссияҳо мебошад, аз вақти ҳамчун номзад ба вакилӣ ба қаӣд ирифта шуданаш аз узвияти хамин комиссия озод карда мешавад. ( ҚҶТ аз 16.04.12с., №827)' : ' Каждый гражданин Республики Таджикистан в возрасте не менее 20 лет, проживающий на соответствующей территории посёлка и села, не менее 3 последних лет и владеющий государственным языком может выдвигаться в кандидаты в депутаты Джамоата. (ЗРТ от 1.08.12г., №899; (ЗРТ от 30.05.17 г., №1431) \n Профессиональные деятели организаций и религиозных объединений, военнослужащие Вооруженных Сил, другие военные структуры и войска, судьи, прокуроры, лица рядового и командного состава органов внутренних дел, таможенные органы, правоохранительные сотрудники Агентства по финансовому контролю и борьбе с коррупцией и Агентства по контролю за наркотиками при Президенте Республики Таджикистан в случае их ухода в отставку могут выдвигаться кандидатами в депутаты Джамоата. (ЗРТ от 30.05.17 г., №1431) \n Следующие граждане не имеют права выдвигаться кандидатами в депутаты Джамоата: \n 1) признанные в соответсвии с решением суда недееспособными; (ЗРТ от 30.05.17 г., №1431) \n 2) находящиеся на основании решения суда в местах лишения свободы; \n 3) находящиеся на основании решения суда принудительно в лечебных учреждениях; \n 4) совершившие умышленные тяжкие и особо тяжкие преступления (независимо от прохождения срока и снятия судимости); \n 5) обвиняемые за совершение тяжких и особо тяжких преступлений или находящиеся в розыске. (ЗРТ от 30.05.17 г., №1431) \n Кандидат в депутаты Джамоата не может быть членом избирательной комиссии Джамоата, а также окружной избирательной комиссии, организующей голосование в соответствующем избирательном округе. Лицо, которое выдвинуто кандидатом в депутаты и является членом одной из этих комиссий, с момента его регистрации в качестве кандидата в депутаты освобождается от членства данной комиссии. (ЗРТ от 16.04.12г., №827)'}
                    </Text>
                    <Text style={styles.listItem}>
                        {language === 'Тоҷики' ? 'Моддаи 37. Бақайдгирии номзадҳо' : 'Статья 37. Регистрация кандидатов'}
                    </Text>
                    <Text style={styles.textItem}>
                        {language === 'Тоҷики' ? ' Барои бақайдгирии номзадҳо ба комиссияи ҳавзавии интихобот аризаи номзад ба вакилӣ ва рўйхати имзоҳои интихобкунандагон пешниҳод карда мешаванд. Комиссия ҳуҷҷатҳои пешниҳодшударо дар мўҳлати 5 рўз санҷида, дар бораи ба қайд гирифтани номзад ва ё рад намудани бақайдгирии номзад қарор қабул мекунад. \n Номзад ба вакилӣ метавонад оид ба қарори комиссияи ҳавзавии интихобот дар бораи рад намудани бақайдгирии номзадии ў ба комиссияи интихоботи Ҷамоат ё суд шикоят кунад. \n Бақайдгирии номзадҳо 15 рӯз пеш аз рӯзи, овоздиҳӣ оғоз гардида, 5 рӯз қабл аз он ба охир мерасад. ( ҚҶТ аз 16.04.12с., №827)' : ' Для регистрации кандидатов в окружную избирательную комиссию представляются заявление кандидата в депутаты и список собранных подписей избирателей. Комиссия, проверив представленные документы в пятидневный срок, выносит решение о регистрации кандидата или же об отказе в регистрации кандидата. \n Кандидат в депутаты может обратиться в избирательную комиссию Джамоата или в суд с жалобой на решение избирательной комиссии Джамоата об отказе регистрации его в качестве кандидата. \n Регистрация кандидатов начинается за 15 дней и заканчивается не позднее 5 дней до дня голосования. (ЗРТ от 16.04.12г., №827)'}
                    </Text>
                    <Text style={styles.listItem}>
                        {language === 'Тоҷики' ? 'Моддаи 38. Ташвиқоти пешазинтихоботӣ' : 'Статья 38. Предвыборная агитация'}
                    </Text>
                    <Text style={styles.textItem}>
                        {language === 'Тоҷики' ? ' Номзадҳо ба вакили Ҷамоат баъд аз бақайдгирӣ ҳуқуқи ташвиқот бурданро доранд. Ташвиқот дар рўзи овоздиҳӣ манъ аст. \n Комиссияи ҳавзавии интихобот барои бурдани ташвиқоти пешазинтихоботии номзад мусоидат менамояд. \n Дар вақти бурдани ташвиқот истифодаи методҳои маҷбуркунии рўҳӣ, ҷисмонӣ, динӣ, сўистифодаи озодии воситаи ахбори омма, ташвиқоте, ки ҳисси бадбинӣ ва хусумати иҷтимоӣ, нажодӣ, миллиро бедор мекунад, ба ғасби ҳокимият, зўран тағйир додани сохти конститутсионӣ, вайронкунии тамомияти давлат даъват менамояд, ташвиқоти ҷанг ва дигар шаклҳои сўистифодаи озодии воситаи ахбори омма, ки қонунгузории Ҷумҳурии Тоҷикистон манъ кардааст, иҷозат дода намешавад.' : ' Кандидаты в депутаты Джамоата после регистрации имеют право на агитацию. Агитация в день выборов запрещена. \n Окружная избирательная комиссия содействует кандидату в проведении предвыборной агитации. \n При проведении агитации не допускаются применение методов психологического, физического и религиозного принуждения, злоупотребление свободой средств массовой информации, агитация, возбуждающая социальную, расовую и национальную ненависть и вражду, призывы к захвату власти, насильственному изменению конституционного строя и нарушению целостности государства, пропаганда войны и иные формы злоупотребления свободой средств массовой информации, запрещенные законодательством Республики Таджикистан.'}
                    </Text>
                    <Text style={styles.listItem}>
                        {language === 'Тоҷики' ? 'Моддаи 39. Вақт ва ҷои овоздиҳӣ' : 'Статья 39. Время и место голосования'}
                    </Text>
                    <Text style={styles.textItem}>
                        {language === 'Тоҷики' ? ' Интихоботи вакилони Ҷамоат дар рўзи овоздиҳӣ аз соати 06-00 то 20-00 гузаронида мешавад. Комиссияи ҳавзавии интихобот вақт ва ҷои овоздиҳиро 5 рўз пеш аз рўзи овоздиҳӣ ба интихобкунандагон хабар медиҳад.' : ' Выборы депутатов Джамоата проводятся в день голосования с 06-00 до 20-00 часов. Окружная избирательная комиссия сообщает избирателям о месте и времени голосования за 5 дней до даты голосования.'}
                    </Text>
                    <Text style={styles.listItem}>
                        {language === 'Тоҷики' ? 'Моддаи 40. Ташкил ва тартиби овоздиҳӣ' : 'Статья 40. Организация и порядок голосования'}
                    </Text>
                    <Text style={styles.textItem}>
                        {language === 'Тоҷики' ? ' Интихобкунандагон дар биноҳои махсусе овоз медиҳанд, ки дар онҳо ҳуҷраҳо ва дигар ҷойҳои махсус барои овоздиҳии пинҳонӣ таҷҳизонида шудаанд. (ҚҶТ аз 30.05.17с., №1431) \n Раиси комиссияи ҳавзавии интихобот рўзи интихобот пеш аз оғози овоздиҳӣ дар ҳузури ҳамаи аъзои комиссия қуттиҳои овоздиҳиро аз назар гузаронида, онҳоро мўҳр мегузорад. (ҚҶТ аз 30.05.17с., №1431) \n Ҳар интихобкунанда шахсан худаш овоз медиҳад. Ба ҷои шахси дигар овоз додан манъ аст. Додани зиёда аз як бюллетен ба интихобкунанда манъ аст. Бюллетенҳои интихобот ба интихобкунандагон барои комиссияи ҳавзавии интихобот дар асоси рўйхати интихобкунандагони ҳавзаи интихобот баъди пешниҳоди шиноснома ё ҳуҷҷати дигари тасдиқкунандаи шахсияти интихобкунанда дода мешавад. Ҳар интихобкунанда бюллетени интихоботро гирифта, дар рӯйхати интихобкунандагон дар муқобили насаб, ном ва номи падари худ имзо мегузорад. (ҚҶТ аз 30.05.17с., №1431) \n Овоздиҳанда бюллетени интихоботро дар ҳуҷраи овоздиҳии пинҳонӣ пур мекунад. Ҳангоми пур кардани бюллетен ғайр аз овоздиҳанда ҳузур доштани шахси дигар манъ аст. Интихобкунандае, ки бюллетенро мустақилона пур карда наметавонад, ҳуқуқ дорад ба ҳуҷраи овоздиҳӣ бо салоҳдиди худ каси дигарро ғайр аз аъзои комиссияи ҳавзавии интихобот, номзад ба вакили Ҷамоат, мушоҳидони миллӣ ва байналмилалӣ ва намояндагони воситаҳои ахбори омма даъват намояд. (ҚҶТ аз 30.05.17с., №1431) \n Интихобкунандае, ки ба рӯйхати интихобкунандагон дохил карда нашудааст, дар асоси шиноснома ё ҳуҷҷати дигаре, ки шахсияти ӯро тасдиқ менамояд, ба рӯйхати иловагии интихобкунандагон дохил карда мешавад. (ҚҶТ аз 30.05.17с., №1431) \n Ҳар як интихобкунанда ба тарафдории як номзад овоз медиҳад. Бюллетенҳое, ки дар онҳо ба тарафдории ду ва ё зиёда номзадҳо ё ба ягон номзад овоз дода нашудааст, беэътибор дониста мешаванд. (ҚҶТ аз 30.05.17с., №1431) \n Қуттиҳои овоздиҳӣ аз тарафи комиссияи ҳавзавии интихобот дар ҳузури ҳамаи аъзои он баъди аз тарафи раиси комиссия тамомшуда эълон гардидани овоздиҳӣ кушода мешаванд. Кушодани қуттиҳои овоздиҳӣ то пурра ба итмом расидани вақти овоздиҳӣ манъ аст. (ҚҶТ аз 30.05.17с., №1431) \n Натиҷаи ҳисоби овозҳо дар маҷлиси комиссияи ҳавзавии интихобот баррасӣ гардида, ба протокол дохил карда мешавад, ки ба он раис, котиб ва аъзои комиссия имзо мегузоранд. Протокол ба комиссияи интихоботи Ҷамоат пешниҳод карда мешавад. (ҚҶТ аз 30.05.17с., №1431) \n Номзаде ба вакилии Ҷамоат интихобгардида дониста мешавад, ки дар интихобот аз 50 фоиз зиёди овозҳои интихобкунандагони дар овоздиҳӣ иштироккардаро гирифтааст. (ҚҶТ аз 30.05.17с., №1431) \n Комиссияи ҳавзавии интихобот аз сабаби дар рафти интихобот ё ҳангоми ҳисобкунии овозҳо роҳ додан ба вайрон карда шудани Қонуни мазкур метавонад интихоботро беэътибор ҳисобад. (ҚҶТ аз 30.05.17с., №1431) \n Интихобот барпонашуда дониста мешавад, агар дар он камтар аз 50 фоизи интихобкунандагони ба рўйхати интихобкунандагон дохилшуда иштирок карда бошанд. (ҚҶТ аз 30.05.17с., №1431)' : ' Избиратели голосуют в специально отведенных помещениях, в которых должны быть оборудованы в достаточном количестве кабины или комнаты для тайного голосования. \n Председатель окружной избирательной комиссии в присутствии всех членов комиссии в день выборов, перед началом голосования, проверяет избирательные ящики, опечатывает их. (ЗРТ от 30.05.17 г., №1431) \n Каждый избиратель голосует лично. Голосование за других лиц не допускается. Запрещается выдавать более одного бюллетеня избирателю. Избирательные бюллетени выдаются соответствующей избирательной комиссией на основании списка избирателей избирательного округа по предъявлении паспорта или иного документа, удостоверяющего личность. Каждый избиратель расписывается в получении избирательного бюллетеня против своей фамилии, имени и отчества в списке избирателей. (ЗРТ от 30.05.17 г., №1431) \n Избирательный бюллетень заполняется голосующим в кабине для тайного голосования. При заполнении бюллетеня запрещается присутствие других лиц, кроме голосующего. Избиратель, не имеющий возможности самостоятельно заполнить бюллетень, вправе пригласить в кабину другое лицо по своему усмотрению, кроме лиц, входящих в состав окружных избирательных комиссий, кандидата в депутаты Джамоата, национальных и международных наблюдателей и представителей средств массовой информации. (ЗРТ от 30.05.17 г., №1431) \n Избиратель, не включенный в список избирателей, на основании паспорта или иного документа, удостоверяющего его личность, включается в дополнительный список избирателей. (ЗРТ от 30.05.17 г., №1431) \n Каждый избиратель голосует за одного кандидата. Бюллетени, в которых отданы голоса за двух и более кандидатов или не отдан голос ни за одного кандидата, считаются недействительными. (ЗРТ от 30.05.17 г., №1431) \n Избирательные ящики вскрываются окружной избирательной комиссией в присутствии всех её членов после объявления председателем комиссии об окончании голосования. Вскрытие избирательных ящиков до окончания голосования запрещается. (ЗРТ от 30.05.17 г., №1431) \n Результаты подсчета голосов рассматриваются на заседании окружной избирательной комиссии и заносятся в протокол, который подписывается председателем, секретарем и членами комиссии. Протокол представляется в избирательную комиссию Джамоата. (ЗРТ от 30.05.17 г., №1431) \n Избранным считается кандидат в депутаты Джамоата, получивший на выборах более 50 процентов голосов избирателей, принявших участие в голосовании. (ЗРТ от 30.05.17 г., №1431) \n Окружная избирательная комиссия может признать выборы не действительными из-за допущенных в ходе выборов или при подсчете голосов нарушений настоящего Закона. (ЗРТ от 30.05.17 г., №1431) \n Выборы признаются несостоявшимися, если в них приняло участие менее 50 процентов избирателей, внесенных в списки избирателей. (ЗРТ от 30.05.17 г., №1431)'}
                    </Text>
                    <Text style={styles.listItem}>
                        {language === 'Тоҷики' ? 'Моддаи 41. Овоздиҳӣ ва интихоботи такрорӣ' : 'Статья 41. Повторное голосование и повторные выборы'}
                    </Text>
                    <Text style={styles.textItem}>
                        {language === 'Тоҷики' ? ' Агар дар ҳавзаи интихоботӣ номзадии беш аз ду нафар барои вакил шудан ба овоз монда шуда бошад ва ҳеҷ яке аз онҳо интихоб нагардида бошад, комиссияи ҳавзавии интихобот дар хусуси гузаронидани овоздиҳии такрорӣ дар ҳавза оид ба ду номзад ба вакилӣ, ки овозҳои бештарро гирифтаанд, қарор қабул менамояд. (ҚҶТ аз 30.05.17с., №1431) \n Овоздиҳии такрорӣ дар ҳавзаи интихоботӣ дар мўҳлати на дертар аз ду ҳафта, бо риояи талаботи Қонуни мазкур, дар ҳамон ҳавзаи интихоботӣ гузаронида мешавад. Ҳангоми овоздиҳии такрорӣ номзаде интихобшуда ҳисобида мешавад, ки овозҳои бештари интихобкунандагони дар овоздиҳӣ иштирокнамударо соҳиб шудааст. Агар ҳангоми овоздиҳии такрорӣ яке аз номзадҳо бо ягон сабаб хориҷ шавад, овоздиҳӣ барои як номзад гузаронида мешавад. Дар ин сурат ба номзад барои вакили Ҷамоат шудан зарур аст, ки зиёда аз нисфи овозҳои интихобкунандагони дар овоздиҳӣ иштирокдоштаро гирад. Агар дар ҳавзаи интихобот номзадии на бештар аз ду кас барои вакил шудан ба овоз монда шуда, ҳеч яке аз онҳо интихоб нагардида бошанд ё интихобот дар хавзаи интихобот барпонашуда ё беэътибор дониста шуда бошад ва ё овоздиҳии такрорӣ имкон надода бошад, ки вакили интихобшуда муайян гардад, комиссияи интихоботи Ҷамоат дар ни ҳавзаи интихобот интихоботи такрорӣ мегузаронад. Дар ин холат он метавонад дар бораи гузаронидани интихоботи такрорӣ ва ташкили комиссияи ҳавзавӣ дар ҳайати нав қарор қабул намояд. Овоздиҳӣ дар ҳамон хавзаи интихобот мувофиқи руйхати интнхобкунандагон, ки барои интихоботи асоси тартиб дода шуда буд, гузаронида мешавад. \n Интихоботи такрорӣ дар муҳлати на дертар аз ду моҳ баъд аз интихоботи асосӣ гузаронида мешавад. Таъсниси комиссияи интихобот, бақаӣдгирии номзадҳо ба вакили ва чорабиниҳои дигар мувофики тартиби мукаррарнамудаи Қонуни мазкур сурат мегиранд. (ҚҶТ аз 16.04.12с., №827)' : ' Если по избирательному округу баллотировалось более двух кандидатов в депутаты и ни один из них не был избран, окружная избирательная комиссия принимает решение о проведении в округе повторного голосования по двум кандидатам в депутаты, получившим наибольшее число голосов. \n Повторное голосование в избирательном округе проводится не позднее чем в двухнедельный срок с соблюдением требований настоящего Закона на тех же избирательных участках. При повторном голосовании считается избранным тот кандидат, который получил наибольшее число голосов избирателей, участвующих в голосовании. Если при повторном голосовании один из кандидатов по какой-либо причине выбывает, голосование проводится по одному кандидату. При этом, чтобы быть избранным, кандидату в депутаты необходимо получить более половины голосов от числа избирателей, принявших участие в голосовании. Если по избирательному округу баллотировалось не более двух кандидатов в депутаты, и пи один из них не был избран, или выборы признаны несостоявшимися или недействительными, пли повторное голосование не позволило определить избранного депутата, избирательная комиссия Джамоата проводит повторные выборы в этом избирательном округе. В этом случае она может принять решение о проведении повторных выборов и об организации окружной комиссии в новом составе. Голосование в данном избирательном округе проводится по спискам избирателей, составленных для основных выборов. \n Повторные выборы проводятся не позднее двух месяцев после проведения основных выборов. Образование избирательной комиссии, регистрация кандидатов в депутаты и другие мероприятия проводятся в порядке, установленном настоящим Законом. (ЗРТ от 16.04.12г., №827)'}
                    </Text>
                    <Text style={styles.listItem}>
                        {language === 'Тоҷики' ? 'Моддан 41\u00B9. Гузаронидани интихоботи вакилон ба ивази вакилони хорҷшуда' : 'Статья 41\u00B9. Проведение выборов депутатов вместо выбывших депутатов'}
                    </Text>
                    <Text style={styles.textItem}>
                        {language === 'Тоҷики' ? ' Дар сурати аз ҷониби маҷлиси дахлдори Ҷамоат беэътибор дониста шудани ваколатҳои вакилони алоҳида, бо сабабҳои дигар пеш аз муҳлат катъ гардидани ваколатҳои вакилон, дар хавзаи дахлдори интихоботи дар муҳлати ду моҳи баъд аз лаҳзаи хориҷ шудани вакил интихоботи нав гузаронида мешавад. Интихобот аз тарафи маҷлиси дахлдори Ҷамоат на дертар аз як моҳи пеш аз гузаронндани он таъйин гардида, бо риояи талаботи Қонуни мазкур ташкил карда мешавад. (ҚҶТ аз 30.05.12с., №827) \n Дар сурати камтар аз як сол қабл аз ба охир расидани муҳлати ваколатхои маҷлиси дахлдори Ҷамоат хориҷ шудани вакил интихоботи вакили дигар ба ивази вакили хориҷ шуда гузаронида намешавад. (ҚҶТ аз 30.05.12с., №827)' : ' В случае признания соответствующим собранием Джамоага полномочий отдельных депутатов недействительными, досрочного прекращения депутатских полномочий по другим причинам в соответствующих избирательных округах в двухмесячный срок с момента выбытия депутата проводятся новые выборы. Выборы назначаются соответствующим собранием Джамоага не позднее одного месяца до их проведения и организуются с соблюдением требований настоящего Закона. \n В случае выбытия депутата менее чем за год до истечения срока полномочий соответствующего собрания Джамоата выборы нового депутата вместо выбывшего не проводятся. (ЗРТ от 16.04.12г., №827)'}
                    </Text>
                    <Text style={styles.listItem}>
                        {language === 'Тоҷики' ? 'Моддаи 42. Бақайдгирии вакилони Ҷамоат' : 'Статья 42. Регистрация депутатов Джамоата'}
                    </Text>
                    <Text style={styles.textItem}>
                        {language === 'Тоҷики' ? ' Комиссияи интихоботи Ҷамоат дар асоси протоколҳои комиссияҳои ҳавзавии интихобот вакилони интихобшударо ба қайд мегирад ва рўйхати онҳоро тартиб дода, дар муддати на дертар аз 3 рўзи баъди интихобот ба Ҷамоат пешниҳод намуда, ба мақомоти маҳаллии ҳокимияти давлатӣ ва аҳолии шаҳрак ва деҳот хабар медиҳад. Ба вакили Ҷамоат шаҳодатномаи вакилӣ ва нишони сарисинагӣ дода мешавад. (ҚҶТ аз 30.05.17с., №1431)' : ' Избирательная комиссия Джамоата на основе протоколов окружных избирательных комиссий регистрирует избранных кандидатов и составляет их список не позднее 3 дней после голосования, представляет их в Джамоат, о чем затем информирует местный исполнительной орган государственной власти и население посёлка и села. Депутату Джамоата выдается депутатское удостоверение и нагрудный знак. (ЗРТ от 30.05.17 г., №1431)'}
                    </Text>
                </View>
            </ScrollView>
        </View>
    );
};

