import React from 'react';
import { Platform, View, Text, StyleSheet, Image, ScrollView } from 'react-native';
import { useLanguage } from '../LanguageContext';

const Section1Page3 = () => {
  const { language } = useLanguage();

  return (
    <View style={Platform.OS === 'web' ? styles.webcontainer : styles.container}>
      <Image
        source={require('../images/parcham.png')}
        style={styles.backgroundImage}
        resizeMode="contain"
      />
      <View style={styles.overlay} />

      <ScrollView contentContainerStyle={styles.scrollContent}>
        <View style={styles.listContainer}>
          <Text style={styles.listItem}>
            {language === 'Тоҷики' ? 'Моддаи 10. Низоми комиссияхои интихобот' : 'Статья 10. Система избирательных комиссий'}
          </Text>
          <Text style={styles.textItem}>
            {language === 'Тоҷики' ? ' Барои ташкил ва гузаронидани интихоботи аъзои Маҷлиси миллӣ ва вакилони Маҷлиси намояндагон комиссияҳои зерини интихобот ташкил карда мешаванд: \n 1) Комиссияи марказии интихобот ва раъйпурсии Ҷумҳурии Тоҷикистон; \n 2) комиссияҳои ҳавзавии интихобот оид ба интихоботи аъзои Маҷлиси миллӣ; \n 3) комиссияҳои ҳавзавии интихобот оид ба интихоботи вакилони Маҷлиси намояндагон; \n 4) комиссияҳои участкавӣ оид ба интихоботи вакилони Маҷлиси намояндагон. \n Комиссияҳои интихобот дар фаъолияти худ ба ҳеҷ мақоми давлатӣ вобаста нестанд. Қарор ва дигар санадҳои комиссияҳои интихобот, ки дар доираи салоҳияташон қабул намудаанд, барои ҳамаи мақомоти давлатӣ, мақомоти худидоракунии шаҳрак ва деҳот, ҳизбҳои сиёсӣ, иттиҳодияҳои ҷамъиятӣ, ташкилотҳо ва шахсони мансабдор, номзадҳо, интихобкунандагон, ҳамчунин барои комиссияҳои поёнии интихобот ҳатмӣ мебошад. Қарори комиссияҳои интихобот, ки хилофи Конститутсияи Ҷумҳурии Тоҷикистон, Қонуни конститутсионии мазкур ва дигар санадҳои меъёрии ҳуқуқии Ҷумҳурии Тоҷикистон мебошанд ё берун аз доираи салоҳияташон қабул карда шудааст, аз тарафи комиссияи болоии интихобот ё суд бекор карда мешавад. Таъсис ва фаъолияти дигар сохторҳо (мақомот, ташкилотҳо), ки мақомоти интихоботиро иваз мекунанд ё пурра, ё қисман вазифаи онҳоро анҷом медиҳанд, ё ба фаъолияти қонунии онҳо халал мерасонанд, ё ғайриқонунӣ ба фаъолияти онҳо дахолат мекунанд, ё мақом, ё салоҳияти онҳоро ба зиммаи худ мегиранд, манъ аст.' : 'Для организации и проведения выборов членов Маджлиси милли и депутатов Маджлиси намояндагон образуются следующие избирательные комиссии: \n 1) Центральная комиссия по выборам и референдумам Республики Таджикистан; \n 2) окружные избирательные комиссии по выборам членов Маджлиси милли; \n 3) окружные избирательные комиссии по выборам депутатов Маджлиси намояндагон; \n 4) участковые избирательные комиссии по выборам депутатов Маджлиси намояндагон. \n Избирательные комиссии в своей деятельности независимы от органов государственной власти. Решения и другие акты избирательных комиссий, принятые в пределах их компетенции, обязательны для всех государственных органов, органов самоуправления посёлков и сёл, политических партий, общественных объединений, организаций и должностных лиц, кандидатов, избирателей, а также нижестоящих избирательных комиссий. \n Решение избирательной комиссии, противоречащее Конституции Республики Таджикистан, настоящему конституционному Закону и другим нормативным правовым актам Республики Таджикистан либо принятое с превышением установленных полномочий, подлежит отмене вышестоящей избирательной комиссией или судом. \n Запрещается учреждение и деятельность других структур (органов, организаций), которые заменяют избирательные органы, полностью или частично осуществляют их функции, мешают их законной деятельности, незаконно вмешиваются в их деятельность, присваивают их статус или компетенцию.'}
          </Text>
          <Text style={styles.listItem}>
            {language === 'Тоҷики' ? 'Моддаи 11. Таъсиси Комиссияи марказии интихобот ва раъйпурсии Ҷумҳурии Тоҷикистон' : 'Статья 11. Учреждение Центральной комиссии по выборам и референдумам Республики Таджикистан'}
          </Text>
          <Text style={styles.textItem}>
            {language === 'Тоҷики' ? ' Раис, муовини раис ва аъзои Комиссияи марказии интихобот ва раъйпурсии Ҷумҳурии Тоҷикистонро бо пешниҳоди Президенти Ҷумҳурии Тоҷикистон Маҷлиси намояндагони Маҷлиси Олии Ҷумҳурии Тоҷикистон интихоб ва бозхонд мекунад. Фаъолияти Комиссияи марказии интихобот ва раъйпурсии Ҷумҳурии Тоҷикистонро қонун танзим менамояд (ҚКҶТ аз 19.07.19 с., №1639).' : 'Председатель, заместитель председателя и члены Центральной комиссии по выборам и референдумам Республики Таджикистан избираются и отзываются по представлению Президента Республики Таджикистан Маджлиси намояндагон Маджлиси Оли Республики Таджикистан. Деятельность Центральной комиссии по выборам и референдумам Республики Таджикистан регулируется законом (КЗРТ от 19.07.19 г., №1639).'}
          </Text>
          <Text style={styles.listItem}>
            {language === 'Тоҷики' ? 'Моддаи 12. Ваколатҳои Комиссияи марказии интихобот ва раъйпурсӣ' : 'Статья 12. Полномочия Центральной комиссии по выборам и референдумам'}
          </Text>
          <Text style={styles.textItem}>
            {language === 'Тоҷики' ? ' Комиссияи марказии интихобот ва раъйпурсӣ дорои ваколатҳои зерин мебошад: \n 1) дар тамоми ҳудуди Ҷумҳурии Тоҷикистон ба иҷрои Қонуни конститутусионии мазкур назорат ва татбиқи якхелаи онро таъмин менамояд, дар доираи салоҳияти худ дастурҳо қабул ва тартиби татбиқи Қонуни конститутсионии мазкурро маънидод мекунад; \n 2) ҳавзаҳои якмандатии интихоботро оид ба интихоботи вакилони Маҷлиси намояндагон ташкил мекунад ва ба онҳо ному рақам мегузорад; \n 3) комиссияҳои ҳавзавии интихоботро оид ба интихоботи аъзои Маҷлиси миллӣ ва вакилони Маҷлиси намояндагон ташкил медиҳад ва доир ба қароргоҳи онҳо маълумот дарҷ мекунад; \n 4) кори комиссияҳои интихоботро ба роҳ мемонад, тартиби ба ҳайати онҳо даровардани тағйиротро муайян мекунад, агар қарорҳои комиссияҳои ҳавзавии интихобот оид ба интихоботи аъзои Маҷлиси миллӣ ва вакилони Маҷлиси намояндагон хилофи Қонуни конститутсионии мазкур бошанд, онҳоро мустақилона, ё бо пешниҳоди Прокурори генералии Ҷумҳурии Тоҷикистон бекор мекунад; \n 5) масъалаҳои ба ҳавзаи интихобот ҳамроҳ намудани участкаҳои интихоботро, ки берун аз ҳудуди Ҷумҳурии Тоҷикистон ташкил карда мешаванд, ҳаллу фасл менамояд; \n 6) ба номзадҳо барои иштирок карданашон дар маъракаи интихобот шароити баробар муҳайё мекунад; \n 7) бо тартиби муқарраргардида маблағро ба комиссияҳои интихобот тақсим мекунад, бо бино, нақлиёт ва воситаҳои алоқа таъмин шудани комиссияҳои интихоботро назорат мекунад ва масъалаҳои дигари таъминоти моддию техникии интихоботро ҳаллу фасл менамояд; \n 8) шакл ва матни бюллетенҳои интихоботро оид ба интихоботи аъзои Маҷлиси миллӣ ва вакилони Маҷлиси намояндагон, рӯйхати интихобкунандагон, протоколи ҷаласаҳои комиссияҳои интихобот, ҳуҷҷатҳои дигари интихобот, намунаи қуттиҳои интихобот ва муҳри комиссияҳои интихобот, тартиби нигоҳ доштани ҳуҷҷатҳои интихоботро муқаррар мекунад; \n 9) бюллетенҳоро барои интихоботи аъзои Маҷлиси миллӣ ва вакилони Маҷлиси намояндагон барои ҳамаи ҳавзаҳои интихобот тайёр мекунад; 10) гузориши роҳбарони вазоратҳо, кумитаҳои давлатӣ, идораҳои давлатии назди Ҳукумати Ҷумҳурии Тоҷикистон, мақомоти иҷроияи маҳаллии ҳокимияти давлатӣ, мақомоти худидоракунии шаҳрак ва деҳот, намояндагони ҳизбҳои сиёсиро аз рӯи масъалаҳои тайёрӣ ва гузаронидани интихобот мешунавад; \n 11) тартиби муайян намудани сатҳи донистани забони давлатиро аз ҷониби номзад ба узви Маҷлиси миллӣ ва вакили Маҷлиси намояндагон муқаррар мекунад; \n 12) тартиби иштироки мушоҳидони миллӣ, мушоҳидони байналмилалӣ, намояндагони воситаҳои ахбори хориҷӣ ва маҳаллиро муқаррар мекунад, онҳоро бо ҳуҷҷатҳои дахлдор таъмин менамояд; \n 13) рӯйхати номзадҳоро аз ҳизбҳои сиёсӣ, ки аз ҳавзаи интихоботии ягонаи умумиҷумҳуриявӣ пешниҳод гардидаанд ва шахсони боэътимоди онҳоро ба қайд мегирад; \n 14) ба номзадҳое, ки аз ҳавзаи интихоботии ягонаи умумиҷумҳуриявӣ ба қайд гирифта шудаанд ва ба шахсони боэътимоди ҳизбҳои сиёсӣ шаҳодатномаи намунаи муқарраргардидаро медиҳад; \n 15) натиҷаи интихоботро дар тамоми Ҷумҳурии Тоҷикистон ҷамъбаст менамояд, вакилони интихобшударо ба қайд мегирад, дар матбуот оид ба ҷамъбасти интихобот ахборот ва рӯйхати аъзоёни интихобшудаи Маҷлиси миллӣ ва вакилони Маҷлиси намояндагонро чоп мекунад; \n 16) масъалаҳои гузаронидани интихоботи такрории аъзои Маҷлиси миллӣ ва вакилони Маҷлиси намояндагонро ҳал мекунад; \n 17) интихоботи аъзои Маҷлиси миллӣ ва вакилони Маҷлиси намояндагонро ба ивази аъзо ва вакилони хориҷшуда таъин намуда, мегузаронад; \n 18) аризаю шикоятҳоро аз хусуси қарору амалҳои (беамалии) комиссияҳои интихобот баррасӣ менамояд ва аз рӯи онҳо қарорҳо қабул мекунад; \n 19) ба бойгониҳо супурдани ҳуҷҷатҳои вобаста ба ташкил ва гузаронидани интихоботро таъмин менамояд; \n 20) мувофиқи Қонуни конститутсионии мазкур ва қонунҳои дигари Ҷумҳурии Тоҷикистон ваколатҳои дигарро иҷро мекунад.' : 'Центральная комиссия по выборам и референдумам: \n 1) осуществляет на всей территории Республики Таджикистан контроль за исполнением настоящего конституционного Закона и обеспечивает его единообразное применение, в пределах своих полномочий издает инструкции и разъясняет настоящий конституционный Закон; \n 2) образует одномандатные избирательные округа по выборам депутатов Маджлиси намояндагон, присваивает им наименования и номера; \n 3) образует окружные избирательные комиссии по выборам членов Маджлиси милли и депутатов Маджлиси намояндагон и публикует сведения о месте их нахождения; \n 4) направляет работу избирательных комиссий, определяет порядок внесения изменений в их состав, если решения окружных избирательных комиссий по выборам Маджлиси милли и депутатов Маджлиси намояндагон противоречат настоящему конституционному Закону, то отменяет их самостоятельно либо по представлению Генерального прокурора Республики Таджикистан; \n 5) решает вопросы о приписке к избирательному округу избирательных участков, образуемых за пределами Республики Таджикистан; \n 6) обеспечивает кандидатам равные условия для участия в избирательной кампании; \n 7) распределяет в установленном порядке денежные средства по окружным избирательным комиссиям, контролирует обеспечение окружных избирательных комиссий помещениями, транспортом и средствами связи, рассматривает другие вопросы материально-технического обеспечения выборов; \n 8) устанавливает образцы и текст избирательных бюллетеней по выборам членов Маджлиси милли и депутатов Маджлиси намояндагон, списков избирателей, протоколов заседаний избирательных комиссий, других избирательных документов, образцы избирательных ящиков и печатей избирательных комиссий, порядок хранения избирательных документов; \n 9) подготавливает бюллетени для выборов членов Маджлиси милли и депутатов Маджлиси намояндагон для всех избирательных округов; \n 10) заслушивает сообщения руководителей министерств, государственных комитетов и государственных органов при Правительстве Республики Таджикистан, местных исполнительных органов государственной власти, органов самоуправления посёлков и сёл, представителей политических партий по вопросам, связанным с подготовкой и проведением выборов; \n 11) устанавливает порядок определения уровня знания государственного языка кандидата в члены Маджлиси милли и депутаты Маджлиси намояндагон; \n 12) устанавливает порядок участия национальных наблюдателей, международных наблюдателей, представителей иностранных и местных средств массовой информации, обеспечивает их соответствующими документами; \n 13) регистрирует списки кандидатов от политических партий и их доверенных лиц, выдвинутых по единому общереспубликанскому избирательному округу; \n 14) выдает кандидатам, зарегистрированным по единому общереспубликанскому избирательному округу и доверенным лицам политических партий, удостоверения установленного образца; \n 15) подводит итоги выборов в целом по Республике Таджикистан, регистрирует избранных депутатов, публикует в печати сообщения об итогах выборов и список избранных членов Маджлиси милли и депутатов Маджлиси намояндагон; \n 16) решает вопросы, связанные с проведением повторных выборов членов Маджлиси милли и депутатов Маджлиси намояндагон; \n 17) назначает и проводит выборы членов Маджлиси милли и депутатов Маджлиси намояндагон вместо выбывших членов и депутатов; \n 18) рассматривает заявления и жалобы на решения и действия (бездействие) избирательных комиссий и выносит по ним решения; \n 19) обеспечивает передачу документации, связанной с проведением и организацией выборов, в архивы; \n 20) осуществляет иные полномочия в соответствии с настоящим конституционным Законом и другими законами Республики Таджикистан.'}
          </Text>
          <Text style={styles.listItem}>
            {language === 'Тоҷики' ? 'Моддаи 13. Ташкили комиссияи ҳавзавии интихобот' : 'Статья 13. Образование окружной избирательной комиссии'}
          </Text>
          <Text style={styles.textItem}>
            {language === 'Тоҷики' ? ' Комиссияҳои ҳавзавии интихобот оид ба интихоботи аъзои Маҷлиси миллӣ ва вакили Маҷлиси намояндагон инҳо мебошанд: \n 1) комиссияи ҳавзавии интихобот оид ба интихоботи аъзои Маҷлиси миллӣ; \n 2) комиссияи ҳавзавии интихобот (Комиссияи марказии интихобот ва раъйпурсӣ) оид ба интихоботи вакили Маҷлиси намояндагон аз ҳавзаи ягонаи умумиҷумҳуриявии интихобот; \n 3) комиссияи ҳавзавии интихобот оид ба интихоботи вакилони Маҷлиси намояндагон аз ҳавзаҳои якмандатии интихобот. \n Комиссияҳои ҳавзавии интихоботи Вилояти Мухтори Кӯҳистони Бадахшон, вилоятҳо, шаҳри Душанбе, шаҳру ноҳияҳои тобеи ҷумҳурӣ дар якҷоягӣ дар ҳайати раис, муовини раис, котиб ва на камтар аз 8 аъзо камаш ду моҳ пеш аз интихоботи аъзои Маҷлиси миллӣ аз тарафи Комиссияи марказии интихобот ва раъйпурсӣ ташкил карда мешаванд. Ин комиссияҳо ҳамчун комиссияҳои ҳавзавии интихобот оид ба интихоботи аъзои Маҷлиси миллӣ амал мекунанд. Комиссияҳои ҳавзавии якмандатии интихоботро оид ба интихоботи вакилони Маҷлиси намояндагон Комиссияи марказии интихобот ва раъйпурсӣ камаш ду моҳ пеш аз интихобот дар ҳайати раис, муовини раис, котиб ва на камтар аз 6 аъзо ташкил медиҳад. Ташкили комиссияҳои ҳавзавии интихобот оид ба интихоботи вакилони Маҷлиси намояндагон бо пешниҳоди мақомоти иҷроияи маҳаллии ҳокимияти давлатӣ сурат мегирад (ҚКҶТ аз 19.07.19 с., №1639). Ташкили комиссияҳои ҳавзавии интихобот оид ба интихоботи аъзои Маҷлиси миллӣ бо пешниҳоди раисони Вилояти Мухтори Кӯҳистони Бадахшон, вилоятҳо, шаҳри Душанбе ва комиссияи ҳавзавии интихоботи аъзои Маҷлиси миллӣ аз шаҳру ноҳияҳои тобеи ҷумҳурӣ бошад, бо пешниҳоди Ҳукумати Ҷумҳурии Тоҷикистон сурат мегирад.' : 'Окружными избирательными комиссиями по выборам членов Маджлиси милли и депутатов Маджлиси намояндагон являются: \n 1) окружная избирательная комиссия по выборам членов Маджлиси милли; \n 2) окружная избирательная комиссия (Центральная комиссия по выборам и референдумам) по выборам депутатов Маджлиси намояндагон по единому общереспубликанскому избирательному округу; \n 3) окружная избирательная комиссия по выборам депутатов Маджлиси намояндагон по одномандатным избирательным округам. \n Центральная комиссия по выборам и референдумам создает не менее чем за два месяца до выборов членов Маджлиси милли окружные избирательные комиссии Горно-Бадахшанской автономной области, областей, города Душанбе, городов и районов республиканского подчинения в составе председателя, заместителя председателя, секретаря и не менее восьми членов комиссии. Эти комиссии функционируют как окружные избирательные комиссии по выборам членов Маджлиси милли. Центральная комиссия по выборам и референдумам не менее чем за два месяца до выборов создает комиссии одномандатных избирательных округов по выборам депутатов Маджлиси намояндагон в составе председателя, заместителя председателя, секретаря и не менее шести членов комиссии. Создание окружных избирательных комиссий по выборам депутатов Маджлиси намояндагон осуществляется по представлению местных исполнительных органов государственной власти (КЗРТ от 19.07.19 г., №1639). Создание окружных избирательных комиссий по выборам членов Маджлиси милли осуществляется по представлению председателей Горно-Бадахшанской автономной области, города Душанбе, а окружная комиссия по выборам членов Маджлиси милли от городов и районов республиканского подчинения по представлению Правительства Республики Таджикистан.'}
          </Text>
          <Text style={styles.listItem}>
            {language === 'Тоҷики' ? 'Моддаи 14. Ваколатҳои комиссияи ҳавзавии интихобот оид ба интихоботи вакили Маҷлиси намояндагон' : 'Статья 14. Полномочия окружной избирательной комиссии по выборам депутатов Маджлиси намояндагон'}
          </Text>
          <Text style={styles.textItem}>
            {language === 'Тоҷики' ? ' Комиссияи ҳавзавии интихобот оид ба интихоботи вакилони Маҷлиси намояндагон дорои ваколатҳои зерин мебошад: \n 1) иҷрои Қонуни конститутсионии мазкурро дар ҳудуди ҳавзаи интихобот назорат мекунад; \n 2) участкаҳои интихоботро ташкил менамояд, онҳоро рақамгузорӣ мекунад, комиссияҳои участкавии интихобот ташкил медиҳад ва фаъолияти онҳоро танзим менамояд; \n 3) ахбори мақомоти иҷроияи маҳаллии ҳокимияти давлатӣ, роҳбарони корхонаҳо, муассисаю ташкилотҳоро аз рӯи масъалаҳои тайёрӣ ва гузаронидани интихобот мешунавад; 4) бо тартиби муқарраргардида маблағро байни комиссияҳои участкавии интихобот тақсим менамояд, бо биною нақлиёт, воситаҳои алоқа таъмин шудани комиссияҳои участкавии интихоботро назорат мекунад ва масъалаҳои дигари дар ҳавзаҳои интихобот аз ҷиҳати моддию техникӣ таъмин кардани интихоботро ҳаллу фасл менамояд; \n 5) ба тартиб додани рӯйхатҳои интихобкунандагон ва барои шиносоии умум пешниҳод кардани он назорат мекунад; \n 6) ба пешбарии номзадҳо ба вакилии Маҷлиси намояндагон мусоидат менамояд; \n 7) дар маъракаи интихобот шароити баробари иштироки номзадҳоро ба вакилии Маҷлиси намояндагон таъмин менамояд; \n 8) ҳуҷҷатҳои дахлдорро аз ҳизбҳои сиёсӣ ва шаҳрвандон (ба таври худпешбарӣ), ки ба вакилӣ номзад пешбарӣ кардаанд, қабул менамояд; \n 9) номзадҳои ба вакилии Маҷлиси намояндагон пешбаришуда ва шахсони боэътимоду мушоҳидони миллии онҳоро ба қайд мегирад ва ба онҳо шаҳодатномаи дахлдор медиҳад, плакатҳои тарҷимаи ҳоли номзадҳоро ба вакилии Маҷлиси намояндагон нашр менамояд; \n 10) мулоқоти номзадҳо ба вакилии Маҷлиси намояндагонро бо интихобкунандагон ташкил мекунад; \n 11) ба комиссияҳои участкавии интихобот бюллетенҳои интихоботиро дастрас мекунад; \n 12) натиҷаи интихоботи ҳавзаро муқаррар мекунад ва ба Комиссияи марказии интихобот ва раъйпурсӣ мефиристад; \n 13) овоздиҳӣ ва интихоботи такрорӣ, ҳамчунин интихоботи вакилони Маҷлиси намояндагон ба ҷойи вакилони хориҷшударо ташкил мекунад; \n 14) арзу шикоятҳоро аз хусуси қарору амали комиссияҳои участкавии интихобот баррасӣ намуда, аз рӯи онҳо қарор қабул мекунад; \n 15) мувофиқи Қонуни конститутсионии мазкур ва дигар санадҳои меъёрии ҳуқуқии Ҷумҳурии Тоҷикистон ваколатҳои дигарро иҷро мекунад.' : 'Окружная избирательная комиссия по выборам депутатов Маджлиси намояндагон обладает следующими полномочиями: \n 1) осуществляет контроль за выполнением настоящего конституционного Закона на территории избирательного округа; \n 2) образовывает избирательные участки, устанавливает их нумерацию, образует участковые избирательные комиссии и координирует их деятельность; \n 3) заслушивает сообщения местных исполнительных органов государственной власти, руководителей предприятий, учреждений и организаций по вопросам, связанным с подготовкой и проведением выборов; \n 4) распределяет в установленном порядке средства между участковыми избирательными комиссиями, контролирует обеспечение участковых избирательных комиссий помещениями, транспортом, средствами связи и рассматривает другие вопросы материально – технического обеспечения выборов в избирательном округе; \n 5) наблюдает за составлением списков избирателей и представлением их для всеобщего ознакомления; \n 6) организует выдвижение кандидатов в депутаты Маджлиси намояндагон; \n 7) обеспечивает равные условия кандидатам в депутаты Маджлиси намояндагон для участия в избирательной кампании; \n 8) принимает соответствующие документы от политических партий, кандидатов в депутаты и граждан (в форме самовыдвижения); \n 9) регистрирует кандидатов в депутаты Маджлиси намояндагон и их доверенных лиц, национальных наблюдателей, выдает им соответствующие удостоверения, обеспечивает издание плакатов с биографическими данными о кандидатах в депутаты Маджлиси намояндагон; \n 10) организует встречи кандидатов в депутаты Маджлиси намояндагон с избирателями; \n 11) доставляет участковым избирательным комиссиям избирательные бюллетени; \n 12) устанавливает и передает в Центральную комиссию по выборам и референдумам результаты выборов по избирательному округу; \n 13) организует проведение повторного голосования и повторных выборов, а также выборов депутатов Маджлиси намояндагон вместо выбывших; \n 14) рассматривает заявления и жалобы на решения и действия участковых избирательных комиссий и принимает по ним решения; \n 15) осуществляет иные полномочия в соответствии с настоящим конституционным Законом и другими нормативными правовыми актами Республики Таджикистан.'}
          </Text>
          <Text style={styles.listItem}>
            {language === 'Тоҷики' ? 'Моддаи 15. Ваколатҳои комиссияи ҳавзавии интихобот оид ба интихоботи аъзои Маҷлиси миллӣ' : 'Статья 15. Полномочия окружной избирательной комиссии по выборам членов Маджлиси милли'}
          </Text>
          <Text style={styles.textItem}>
            {language === 'Тоҷики' ? ' Комиссияи ҳавзавии интихобот оид ба интихоботи аъзои Маҷлиси миллӣ дорои ваколатҳои зерин мебошад: \n 1) ба иҷрои Қонуни конститутсионии мазкур дар ҳудуди ҳавзаи интихобот назорат мекунад; \n 2) гузориши мақомоти иҷроияи маҳаллии ҳокимияти давлатиро аз рӯи масъалаҳои тайёрӣ ва гузаронидани интихобот мешунавад; \n 3) рӯйхати вакилони интихобкунандаро тартиб медиҳад; \n 4) дар иҷлосияҳои Маҷлисҳои вакилони халқи Вилояти Мухтори Кӯҳистони Бадахшон, вилоятҳо, шаҳри Душанбе ва шаҳру ноҳияҳо иштирок мекунад; \n 5) ҳуҷҷатҳои дахлдорро аз мақомоти иҷроияи маҳаллии ҳокимияти давлатӣ қабул менамояд; \n 6) натиҷаи интихоботи ҳавзаро муқаррар мекунад ва ба Комиссияи марказии интихобот ва раъйпурсӣ мефиристад; \n 7) арзу шикоятҳои дохилшударо баррасӣ намуда, аз рӯи онҳо қарор қабул мекунад; \n 8) интихоботи такрориро оид ба интихоботи аъзои Маҷлиси миллӣ ташкил медиҳад; \n 9) мувофиқи Қонуни конститутсионии мазкур ва дигар санадҳои меъёрии ҳуқуқии Ҷумҳурии Тоҷикистон ваколатҳои дигарро иҷро мекунад.' : 'Окружная избирательная комиссия по выборам членов Маджлиси милли обладает следующими полномочиями: \n 1) осуществляет контроль за выполнением настоящего конституционного Закона на территории избирательного округа; \n 2) заслушивает сообщения местных исполнительных органов государственной власти по вопросам, связанным с подготовкой и проведением выборов; \n 3) составляет списки выборщиков; \n 4) участвует в работе сессий Маджлисов народных депутатов Горно – Бадахшанской автономной области, областей, города Душанбе, городов и районов; \n 5) принимает соответствующие документы от местных исполнительных органов государственной власти; \n 6) устанавливает и передает в Центральную комиссию по выборам и референдумам результаты выборов по округу; \n 7) рассматривает поступившие заявления и жалобы, принимает по ним решения; \n 8) организует проведение повторного голосования по выборам членов Маджлиси милли; \n 9) осуществляет иные полномочия в соответствии с настоящим конституционным Законом и другими нормативными правовыми актами Республики Таджикистан.'}
          </Text>
          <Text style={styles.listItem}>
            {language === 'Тоҷики' ? 'Моддаи 16. Ташкили комиссияҳои участкавии интихобот' : 'Статья 16. Образование участковой избирательной комиссии'}
          </Text>
          <Text style={styles.textItem}>
            {language === 'Тоҷики' ? ' Комиссияи участкавии интихоботро оид ба интихоботи вакилони Маҷлиси намояндагон, бо пешниҳоди мақомоти иҷроияи маҳаллии ҳокимияти давлатӣ, комиссияи ҳавзавии интихобот оид ба интихоботи вакилони Маҷлиси намояндагон аз ҳавзаи якмандатии интихобот камаш 45 рӯз пеш аз интихобот дар ҳайати на кам аз 5 ва на зиёда аз 19 нафар, аз ҷумла раис, муовини раис, котиби комиссия ташкил мекунад. Дар мавридҳои зарурӣ ҳайати комиссияи участкавии интихобот дар доираи шумораи муқарраршуда каму зиёд шуда метавонад. Дар назди сафоратхонаҳо ва консулгариҳои Ҷумҳурии Тоҷикистон дар давлатҳои хориҷӣ (минбаъд – намояндагиҳои Ҷумҳурии Тоҷикистон дар давлатҳои хориҷӣ) участкаҳои интихобот бо пешниҳоди Вазорати корҳои хориҷии Ҷумҳурии Тоҷикистон аз тарафи Комиссияи марказии интихобот ва раъйпурсии Ҷумҳурии Тоҷикистон бе назардошти талаботи қисмҳои якум, сеюм, панҷум, шашум ва ҳафтуми моддаи 22 Қонуни конститутсионии мазкур ташкил карда шуда, вазифаи раиси комиссияи участкавии интихоботро роҳбари намояндагии Ҷумҳурии Тоҷикистон дар давлати хориҷӣ иҷро менамояд.' : 'Участковая избирательная комиссия по выборам депутатов в Маджлиси намояндагон образуется по представлению местных исполнительных органов государственной власти окружной избирательной комиссией по выборам депутатов Маджлиси намояндагон по одномандатному избирательному округу не позднее чем за 45 дней до выборов в составе 5 и не более 19 человек, в том числе председателя, заместителя председателя, секретаря комиссии. В необходимых случаях численный состав участковой избирательной комиссии может быть увеличен или уменьшен в пределах установленного количества. При посольствах и консульствах Республики Таджикистан в иностранных государствах (далее – представительства Республики Таджикистан в иностранных государствах) избирательные участки образуются Центральной комиссией по выборам и референдумам Республики Таджикистан по предложению Министерства иностранных дел Республики Таджикистан без учёта требований частей первой, третьей, пятой, шестой и седьмой статьи 22 настоящего конституционного Закона, и обязанности председателя участковой избирательной комиссии выполняет руководитель представительства Республики Таджикистан в иностранном государстве.'}
          </Text>
          <Text style={styles.listItem}>
            {language === 'Тоҷики' ? 'Моддаи 17. Ваколатҳои комиссияи участкавии интихобот' : 'Статья 17. Полномочия участковой избирательной комиссии'}
          </Text>
          <Text style={styles.textItem}>
            {language === 'Тоҷики' ? ' Комиссияи участкавии интихобот дорои ваколатҳои зерин мебошад: \n 1) рӯйхати интихобкунандагони участкаро тартиб медиҳад; \n 2) интихобкунандагонро бо рӯйхати интихобкунандагон шинос менамояд, аризаю шикоятҳоро оид ба саҳву хатоҳои рӯйхат баррасӣ менамояд ва масъалаи ба рӯйхат даровардани тағйироти дахлдорро ҳал мекунад; \n 3) аҳолиро аз қароргоҳи комиссияи участкавии интихобот ва вақти кори он, инчунин аз рӯзи интихобот ва ҷойи овоздиҳӣ бо хабарнома огоҳ мекунад; \n 4) бинои овоздиҳӣ ва қуттию таҷҳизоти дигари интихоботиро омода месозад; \n 5) овоздиҳиро дар рӯзи интихобот дар участкаи интихобот ташкил менамояд; \n 6) бюллетенҳои интихоботро дар асоси рӯйхати интихобкунандагони участкаи интихобот бо пешниҳоди шиноснома (корти идентификатсионӣ) ё ҳуҷҷати дигари тасдиқкунандаи шахсияти интихобкунанда медиҳад; \n 7) шумораи овозҳои дар участкаи интихобот додашударо ҳисоб мекунад; \n 8) арзу шикоятҳоро оид ба масъалаҳои тайёрии интихобот ва ташкили овоздиҳӣ баррасӣ намуда, аз рӯи онҳо қapop қабул менамояд; \n 9) мувофиқи Қонуни конститутсионии мазкур ва дигар санадҳои меъёрии ҳуқуқии Ҷумҳурии Тоҷикистон ваколатҳои дигарро иҷро мекунад.' : 'Участковая избирательная комиссия имеет следующие полномочия: \n 1) составляет списки избирателей по участку;  \n 2) ознакомляет избирателей со списком избирателей, принимает и рассматривает заявления об ошибках и упущениях в списке и решает вопрос о внесении в него соответствующих изменений; \n 3) оповещает население о местонахождении участковой избирательной комиссии, времени её работы, а также о дне выборов и месте голосования посредством извещения; \n 4) обеспечивает подготовку помещения для голосования, избирательных ящиков и другого избирательного оборудования; \n 5) организует голосование на избирательном участке в день выборов; \n 6) выдаёт избирательные бюллетени на основании списка избирателей избирательного участка по предъявлению паспорта (идентификационной карты) или иного документа, удостоверяющего личность избирателя; \n 7) производит подсчет голосов, поданных на избирательном участке; \n 8) рассматривает заявления и жалобы по вопросам подготовки выборов, организации голосования и принимает по ним решения; \n 9) осуществляет иные полномочия в соответствии с настоящим конституционным Законом и другими нормативными правовыми актами Республики Таджикистан.'}
          </Text>
          <Text style={styles.listItem}>
            {language === 'Тоҷики' ? 'Моддаи 18. Узвият дар комиссияҳои интихобот' : 'Статья 18. Членство в избирательных комиссиях'}
          </Text>
          <Text style={styles.textItem}>
            {language === 'Тоҷики' ? ' Узви комиссияи интихобот ҳангоми иҷрои ваколатҳояш бояд бетараф ва холис бошад, набояд дар фаъолияти ҳизби сиёсӣ иштирок кунад, супоришҳои онро иҷро намояд ва дар тарғибу ташвиқи пешазинтихоботии номзадҳои бақайдгирифташуда ширкат варзад (ҚКҶТ аз 19.07.19 с., №1639). Узви комиссияро аз рӯи аризааш оид ба даст кашидан аз ваколат ё дар сурати маҳрум гардидан аз ваколатҳояш аз иҷрои вазифа озод кардан мумкин аст. Мақоме, ки комиссияи интихоботро ташкил кардааст, ҳуқуқ дорад узви комиссияро, ки талабҳои Қонуни конститутсионии мазкурро риоя намекунад, ё вазифаҳояшро мунтазам ба ҷо намеорад, аз ваколатҳояш маҳрум созад. Дар сурати зарурат узви дигари комиссияи интихобот аз рӯи тартиби муқаррарнамудаи Қонуни конститутсионии мазкур таъин карда мешавад. Номзадҳо ба аъзои Маҷлиси миллӣ ва вакили Маҷлиси намояндагон, вакили Маҷлисҳои маҳаллии вакилони халқ, судяҳо, прокурорҳо, кормандони мақомоти ҳифзи ҳуқуқ, шахсони боэътимоди номзадҳо, мушоҳидони миллӣ ва намояндагони воситаҳои ахбори омма наметавонанд аъзои комиссияҳои интихобот бошанд. Шаҳрванде, ки ба аъзои Маҷлиси миллӣ ва вакили Маҷлиси намояндагон номзад пешбарӣ гардидааст ва узви комиссияи интихобот мебошад, аз лаҳзаи номзади аъзои Маҷлиси миллӣ ва вакили Маҷлиси намояндагон ба қайд гирифта шуданаш аз вазифаи дар комиссия доштааш озодгардида ҳисоб меёбад. Як шаҳрванд узви фақат як комиссияи интихобот шуда метавонад.' : 'Член избирательной комиссии должен быть нейтральным и объективным при исполнении своих полномочий, не должен участвовать в деятельности политической партии, исполнять её поручения, участвовать в предвыборной агитации и пропаганде зарегистрированных кандидатов (КЗРТ от 19.07.19 г., №1639). Член комиссии может быть освобожден от исполнения своих обязанностей по личному заявлению или освобожден от обязанностей в случае лишения своих полномочий. Право лишения члена комиссии полномочий принадлежит органу, образовавшему комиссию, при нарушении им требований настоящего конституционного Закона или систематическом игнорировании своих обязанностей. В случае необходимости, новый член избирательной комиссии назначается в порядке, установленном настоящим конституционным Законом. Кандидаты в члены Маджлиси милли и депутаты Маджлиси намояндагон местных Маджлисов народных депутатов, судьи, прокуроры, сотрудники правоохранительных органов, доверенные лица кандидатов, национальные и международные наблюдатели, представители средств массовой информации не могут быть членами избирательных комиссий. Гражданин, выдвинутый кандидатом в члены Маджлиси милли и депутаты Маджлиси намояндагон и состоящий в избирательной комиссии, считается освобожденным от обязанностей в комиссии с момента регистрации его кандидатом в члены Маджлиси милли и депутаты Маджлиси намояндагон. Одно и то же лицо может быть членом только одной избирательной комиссии.'}
          </Text>
          <Text style={styles.listItem}>
            {language === 'Тоҷики' ? 'Моддаи 19. Ташкили кори комиссияҳои интихобот' : 'Статья 19. Организация работы избирательных комиссий'}
          </Text>
          <Text style={styles.textItem}>
            {language === 'Тоҷики' ? ' Ҷаласаи комиссияи интихобот дар сурате босалоҳият шуморида мешавад, ки дар он на камтар аз се ду ҳиссаи ҳайати комиссия иштирок карда бошад. Ҳамаи ҷаласаҳои комиссияҳои интихобот ошкоро гузаронида шуда, барои аҳли ҷомеа ва воситаҳои ахбори омма дастрас мебошанд. Қарори комиссия бо овоздиҳии ошкоро ва аз нисф зиёди аъзои ҳузурдоштаи комиссия қабул мешавад. Узви комиссия, ки бо қарори комиссия розӣ нест, ҳуқуқ дорад фикри махсуси худро хаттӣ баён кунад, ки он ба протоколи маҷлис замима карда мешавад. Дар ҳолати баробар будани овозҳо овози раиси ҷаласа ҳалкунанда аст. Мақомоти давлатӣ ва ҳизбҳои сиёсӣ, корхонаю муассиса, ташкилотҳо ва шахсони мансабдор вазифадоранд, ки ба комиссияҳои интихобот дар иҷрои ваколатҳояшон ёрӣ расонанд, маълумоти барояшон зарурбударо дастраси онҳо кунанд. Комиссияи интихобот ҳуқуқ дорад, ки аз рӯи масъалаҳои тайёрӣ ва гузаронидани интихобот ба мақомоти давлатӣ, ҳизбҳои сиёсӣ ва иттиҳодияҳои ҷамъиятӣ, ба корхонаю муассиса ва ташкилотҳо, ба мансабдорон муроҷиат кунад ва онҳо уҳдадоранд, ки масъалаи ба миён гузошташударо баррасӣ карда, ба комиссияи интихобот дар муҳлати на дертар аз се рӯз ҷавоб диҳанд. Раис, муовини раис, котиб ё дигар узви комиссияи интихобот бо қарори комиссия дар давраи тайёрӣ ва гузаронидани интихобот аз иҷрои вазифаҳои истеҳсолӣ ва ё хидматӣ метавонанд озод бошанд. Дар ин сурат музди миёнаи меҳнат дар ҷойи кори асосиаш нигоҳ дошта мешавад ё музди миёнаи меҳнати ӯ бо тартиби муқарраркардаи Комиссияи марказии интихобот ва раъйпурсӣ аз ҳисоби маблағе, ки барои гузаронидани интихобот ҷудо карда мешавад, пардохт мегардад. Комиссияҳои ҳавзавӣ ва участкавии интихобот фаъолияти худро баъди интихоби аъзои Маҷлиси миллӣ ва вакилони Маҷлиси намояндагон қатъ мекунанд.' : 'Заседание избирательной комиссии является правомочным, если в нем принимает участие не менее двух третей состава комиссии. Все заседания избирательной комиссии проводятся гласно, являются доступными для общества и средств массовой информации. Решение комиссии принимается открытым голосованием более половины от состава присутствующих членов комиссии. Член комиссии, не согласный с её решением, вправе высказать особое мнение, которое в письменной форме прилагается к протоколу. В случае разделения голосов голос председательствующего является решающим. Государственные органы и политические партии, предприятия, учреждения, организации, должностные лица обязаны содействовать избирательным комиссиям в осуществлении ими своих полномочий, предоставлять необходимые для их работы сведения. Избирательная комиссия имеет право обращаться по вопросам, связанным с подготовкой и проведением выборов, в государственные органы, политические партии, общественные объединения, к предприятиям, учреждениям, организациям, должностным лицам, которые обязаны рассмотреть поставленный вопрос и дать избирательной комиссии ответ не позднее чем в трехдневный срок. Председатель, заместитель председателя, секретарь либо другой член избирательной комиссии по решению комиссии освобождается в период подготовки и проведения выборов от выполнения производственных или служебных обязанностей. В этом случае ему сохраняется средняя заработная плата по месту основной работы либо выплачивается средняя заработная плата в порядке, установленном Центральной комиссией по выборам и референдумам, за счет средств, выделяемых на проведение выборов. Окружные и участковые избирательные комиссии прекращают свою деятельность после избрания членов Маджлиси милли и депутатов Маджлиси намояндагон.'}
          </Text>
          <Text style={styles.listItem}>
            {language === 'Тоҷики' ? 'Моддаи 20. Баррасии шикоятҳо аз хусуси қарори комиссияҳои интихобот' : 'Статья 20. Рассмотрение жалоб на решения избирательных комиссий'}
          </Text>
          <Text style={styles.textItem}>
            {language === 'Тоҷики' ? ' Мақомоти ҳизбҳои сиёсӣ, ки номзадҳоро пешбарӣ кардаанд, худи номзадҳо, шахсони боэътимод, мушоҳидон ва интихобкунандагон ба комиссияҳои болоии интихобот ё ба суд аз хусуси қарорҳои комиссияҳои интихобот дар давоми даҳ рӯзи пас аз қабул гардидани қарорҳо шикоят карда метавонанд. Аз хусуси қарорҳои Комиссияи марказии интихобот ва раъйпурсӣ ба Суди Олии Ҷумҳурии Тоҷикистон дар давоми даҳ рӯзи баъд аз қабул шудани қарор шикоят кардан мумкин аст. Шикоят баъди расиданаш дар давоми се рӯз ва агар то рӯзи интихобот камтар аз шаш рӯз монда бошад, бояд фавран баррасӣ карда шавад.' : 'Решения избирательных комиссий могут быть обжалованы органами политических партий, выдвинувшими кандидатов, самими кандидатами, доверенными лицами, наблюдателями и избирателями в вышестоящую избирательную комиссию или в суд в течение десяти дней после принятия решения. Решения Центральной комиссии по выборам и референдумам могут быть обжалованы в Верховный Суд Республики Таджикистан в течение десяти дней после принятия решения. Жалоба должна быть рассмотрена в течение трех дней после её поступления, а если до дня выборов осталось меньше шести дней – незамедлительно.'}
          </Text>
        </View>
      </ScrollView>
    </View>
  );
};

