import React from 'react';
import { Platform, View, Text, StyleSheet, Image, ScrollView } from 'react-native';
import { useLanguage } from '../LanguageContext';

const Section2Page4 = () => {
    const { language } = useLanguage();

    return (
        <View style={Platform.OS === 'web' ? styles.webcontainer : styles.container}>
            <Image
                source={require('../images/parcham.png')}
                style={styles.backgroundImage}
                resizeMode="contain"
            />
            <View style={styles.overlay} />

            <ScrollView contentContainerStyle={styles.scrollContent}>
                <View style={styles.listContainer}>
                    <Text style={styles.listItem}>
                        {language === 'Тоҷики' ? 'Моддаи 9. Системаи комиссияҳои интихобот' : 'Статья 9. Система избирательных комиссий'}
                    </Text>
                    <Text style={styles.textItem}>
                        {language === 'Тоҷики' ? ' Барои ташкил ва гузаронидани интихоботи Маҷлисҳои маҳаллии вакилони халқ, комиссияҳои зерини интихобот таъсис дода мешаванд: \n 1) комиссияи вилоятии интихобот оид ба интихоботи Маҷлиси вакилони халқи Вилояти Мухтори Кӯҳистони Бадахшон, вилоят; \n 2) комиссияи шаҳрии интихобот оиди интихобот ба Маҷлиси вакилони халқи шаҳри Душанбе; \n 3) комиссияи шаҳрии интихобот оид ба интихоботи Маҷлиси вакилони халқи шаҳр; \n 4) комиссияи ноҳиявии интихобот оид ба интихоботи Маҷлиси вакилони халқӣ ноҳия; \n 5) комиссияҳои ҳавзавии интихобот; \n 6) комиссияҳои участкавии интихобот. \n Роҳбарии умумӣ, назорати ташкил ва гузаронидани интихоботи вакилон ба Маҷлисҳои маҳаллии вакилони халқ аз ҷониби Комиссияи марказии интихобот ва раъйпурсии Ҷумҳурии Тоҷикистон амалӣ карда мешавад. (ҚКҶТ аз 26.07.14 с., № 1087)' : ' Для организации и проведения выборов местных Маджлисов народных депутатов образуются следующие избирательные комиссии: \n 1) областная избирательная комиссия по выборам в Маджлис народных депутатов Горно-Бадахшанской автономной области, областей; \n 2) городская избирательная комиссия по выборам в Маджлис народных депутатов города Душанбе; \n 3) городская избирательная комиссия по выборам в городской Маджлис народных депутатов; \n 4) районная избирательная комиссия по выборам в районный Маджлис народных депутатов; \n 5) окружные избирательные комиссии; \n 6) участковые избирательные комиссии. \n Общее руководство, контроль организации и проведения выборов депутатов в местные Маджлисы народных депутатов осуществляется Центральной комиссией по выборам и референдумам Республики Таджикистан. (КЗРТ от 26.07.14 г., № 1087)'}
                    </Text>
                    <Text style={styles.listItem}>
                        {language === 'Тоҷики' ? 'Статья 10. Образование областной, городской, районной избирательных комиссий' : 'Статья 10. Образование областной, городской, районной избирательных комиссий'}
                    </Text>
                    <Text style={styles.textItem}>
                        {language === 'Тоҷики' ? ' Комиссияҳои вилоятӣ, шаҳрӣ ва ноҳиявии интихобот ба Маҷлисҳои маҳаллии вакилони халқро Маҷлисҳои дахлдори вакилони халқ бо пешниҳоди раиси вилоят, шаҳр ва ноҳияи дахлдор на дертар аз рӯзи даҳуми баъди таъин гардидани интихобот дар ҳайати 7-13 аъзои комиссия ташкил менамоянд. (ҚКҶТ аз 27.11.14с.№1133) (ҚКҶТ аз 19.07.19 с., №1640) \n Раис, муовини раис ва котиби комиссия аз ҷумлаи аъзоёни он дар аввалин маҷлиси он интихоб карда мешаванд. Комиссияи вилоятӣ, шаҳрӣ ва ноҳиявии интихобот дар бораи ҳайат, нишони ва рақамҳои телефони худ дар нашрияҳои маҳаллӣ маълумот чоп мекунад.' : ' Областные, городские и районные избирательные комиссии по выборам в местные Маджлисы народных депутатов образуются по представлению председателя соответствующих области, города и района не позднее десяти дней после назначения выборов в составе 7-13 членов комиссии. (КЗРТ от 27.11.14г.№1133) (КЗРТ от 19.07.19 г., №1640) \n Председатель, заместитель председателя и секретарь комиссии избираются из числа ее членов на первом заседании комиссии. Областная, городская, районная избирательные комиссии публикуют в официальных местных печатных органах сведения о своем составе, адрес и номера телефонов комиссии.'}
                    </Text>
                    <Text style={styles.listItem}>
                        {language === 'Тоҷики' ? 'Моддаи 11. Ваколатҳои комиссияи вилоятӣ, шаҳрӣ, ноҳиявии интихобот' : 'Статья 11. Полномочия областной, городской, районной избирательных комиссий'}
                    </Text>
                    <Text style={styles.textItem}>
                        {language === 'Тоҷики' ? ' Комиссияи интихобот ба Маҷлиси вакилони халқи Вилояти Мухтори Кӯҳистони Бадахшон, вилоят, шаҳри Душанбе, шаҳр, ноҳия: \n 1) ба иҷрои Қонуни конститутсионии мазкур дар ҳудуди дахлдор назорат менамояд, татбиқи якхелаи онро таъмин мекунад ва оид ба масъалаҳои ташкили интихобот тавзеҳот медиҳад; (ҚКҶТ аз 30.05.2017 с. №1412) \n 2) оид ба интихоботи Маҷлиси вакилони халқи Вилояти Мухтори Кӯҳистони Бадахшон, вилоят, шаҳри Душанбе, шаҳр ва ноҳия мувофиқан ҳавзаҳои интихоботӣ ташкил медиҳад, онҳоро номгузорӣ ва рақамгузорӣ мекунад; \n 3) комиссияҳои ҳавзавии интихоботро ба Маҷлиси дахлдори вакилони халқ ташкил медиҳад ва дар бораи маҳалли ҷойгиршавии онҳо маълумот чоп мекунад; \n 4) кори комиссияҳои дахлдори интихоботро танзим мекунад, тартиби ба ҳайати онҳо дохил намудани тағйиротро муайян менамояд, метавонад мустақилона ё бо пешниҳоди прокурори Вилояти Мухтори Кӯҳистони Бадахшон, вилоят, шаҳри Душанбе, шаҳр, ноҳия қарорҳои комиссияҳои ҳавзавӣ ва участкавии интихоботро дар сурате, ки хилофи Қонуни конститутсионии мазкур бошанд, бекор кунад; \n 5) ба номзадҳо барои иштирок дар маъракаи интихобот шароити баробарро таъмин менамояд; \n 6) маблағро ба комиссияҳои интихобот тақсим менамояд, бо биною нақлиёт ва воситаҳои алоқа таъмин кардани комиссияҳои интихоботро назорат мекунад ва дигар масъалаҳои таъминоти моддию техникии интихоботро баррасӣ менамояд; \n 7) аз рӯи шакли муқаррарнамудаи Комиссияи марказии интихобот ва раъйпурсӣ тайёр намудани бюллетенҳои интихоботро ба Маҷлисҳои маҳаллии вакилони халқ, руйхати интихобкунандагон, протоколҳои комиссияҳои интихобот, варақаҳои интихоботӣ, мӯҳри худ, дигар ҳуҷҷатҳои интихоботро таъмин мекунад; \n 8) аз комиссияҳои ҳавзавии интихобот протоколҳои қайди номзадҳоро ба вакилӣ гирифта, хамчунин протоколҳои натиҷаи интихоботи вакилони Маҷлиси дахлдорро дар бораи ҳайати номзадҳои вакилони аз қайд гузаштаи Маҷлиси дахлдор ахбор чоп мекунад; \n 9) ахбори намояндагони ҳизбҳои сиёсӣ, мақомоти иҷроияи маҳаллии ҳокимияти давлатӣ, мақомоти худидоракунии шаҳрак ва деҳот ва иттиҳодияҳои ҷамъиятиро оид ба масъалаҳои тайёрӣ ва гузаронидани интихобот мешунавад; \n 10) вакилони интихобшударо ба қайд мегирад, натиҷаи интихоботи Маҷлиси дахлдори вакилони халқро ҷамъбаст мекунад, дар матбуот оиди ҷамъбасти интихобот хабар ва рӯйхати вакилони интихобгардидаро чоп мекунад; \n 11) масъалаҳои гузаронидани овоздиҳӣ ва интихоботи такрориро ҳаллу фасл менамояд; \n 12) интихоботи вакилон ба Маҷлиси дахлдори вакилони халқро ба ҷои вакилони хориҷгардида таъин мекунад ва мегузаронад; \n 13) арзу шикоятҳоро аз хусуси қарору амали комиссияҳои дахлдори интихобот дида мебарояд ва аз рӯи онҳо қарор қабул мекунад; \n 14) ба бойгонӣ супоридани ҳуҷҷатҳои вобаста ба ташкилу гузаронидани интихоботро таъмин мекунад; \n 15) ваколатҳои дигареро, ки Қонуни конститутсионии мазкур пешбинӣ намудааст, амалӣ менамояд. (ҚКҶТ аз 26.07.14 с., № 1087)' : ' Избирательная комиссия по выборам в Маджлис народных депутатов Горно-Бадахшанской автономной области, областная, города Душанбе, городская, районная избирательные комиссии: \n 1) осуществляют на соответствующей территории контроль за исполнением настоящего конституционного Закона, обеспечивают его единообразное применение и дают разъяснения по вопросам организации выборов; \n 2) образуют избирательные округа по выборам в Маджлисы народных депутатов Горно-Бадахшанской автономной области, областей, города Душанбе, городов и районов, присваивают им наименования и номера; \n 3) образуют окружные избирательные комиссии по выборам в соответствующий Маджлис народных депутатов и публикуют сведения о месте их нахождения; \n 4) направляют работу соответствующих избирательных комиссий, определяют порядок внесения изменений в их состав, могут самостоятельно или по представлению прокуроров Горно-Бадахшанской автономной области, области, города Душанбе, города, района отменять решения окружных и участковых избирательных комиссий в случаях, если эти решения противоречат настоящему конституционномуЗакону; \n 5) обеспечивают кандидатам равные условия для участия в избирательной кампании; \n 6) распределяют денежные средства по соответствующим избирательным комиссиям, контролируют обеспечение избирательных комиссий помещениями, транспортом, средствами связи и рассматривают другие вопросы материально-технического обеспечения выборов; \n 7) обеспечивают по установленной Центральной комиссией по выборам и проведению референдумов форме изготовление избирательных бюллетеней по выборам депутатов местных Маджлисов народных депутатов, списков избирателей, протоколов избирательных комиссий, избирательных листов, своей печати, других избирательных документов; \n 8) получают от окружных избирательных комиссий протоколы о регистрации кандидатов в депутаты, а также протоколы о результатах выборов депутатов соответствующего Маджлиса, публикуют сообщения о составе зарегистрированных кандидатов в депутаты соответствующего Маджлиса; \n 9) заслушивают сообщения представителей политических партий, местных исполнительных органов государственной власти и органов самоуправления посёлков и сел и общественных объединений по вопросам, связанным с подготовкой и проведением выборов; \n 10) регистрируют избранных депутатов, подводят итоги выборов в соответствующий Маджлис народных депутатов, публикуют в печати сообщение об итогах выборов и список избранных депутатов; \n 11) решают вопросы, связанные с проведением повторного голосования и повторных выборов; \n 12) назначают выборы депутатов соответствующего Маджлиса народных депутатов вместо выбывших депутатов и обеспечивают их проведение; \n 13) рассматривают заявления и жалобы на решения и действия соответствующих избирательных комиссий и принимают по ним решения; \n 14) обеспечивают передачу документации, связанной с организацией и проведением выборов, в архив; \n 15) осуществляет иные полномочия, предусмотренные настоящим конституционным Законом. (КЗРТ от 26.07.14 г., № 1087)'}
                    </Text>
                    <Text style={styles.listItem}>
                        {language === 'Тоҷики' ? 'Моддаи 12. Ташкили комиссияи ҳавзавии интихобот' : 'Статья 12. Образование окружной избирательной комиссии'}
                    </Text>
                    <Text style={styles.textItem}>
                        {language === 'Тоҷики' ? ' Комиссияи ҳавзавии интихобот аз тарафи комиссияҳои дахлдори вилоятӣ, шаҳрӣ ва ноҳиявии интихобот бо пешниҳоди раиси вилоят, шаҳр ва ноҳияи дахлдор на дертар аз 55 рӯз пеш аз интихобот дар ҳайати раис, муовини раис, котиб ва на камтар аз 8 аъзо ташкил карда мешавад. (ҚКҶТ аз 27.11.14с.№1133) (ҚКҶТ аз 19.07.19 с., №1640)' : ' Окружная избирательная комиссия образуется соответствующей избирательной комиссией областной, городской и районной не позднее чем за 55 дней до выборов по представлению председателя соответствующих области, города и района в составе председателя, заместителя председателя, секретаря и не менее 8 членов комиссии. (КЗРТ от 27.11.14г.№1133) (КЗРТ от 19.07.19 г., №1640)'}
                    </Text>
                    <Text style={styles.listItem}>
                        {language === 'Тоҷики' ? 'Моддаи 13. Ваколатҳои комиссияи ҳавзавии интихобот' : 'Статья 13. Полномочия окружной избирательной комиссии'}
                    </Text>
                    <Text style={styles.textItem}>
                        {language === 'Тоҷики' ? ' Комиссияи ҳавзавии интихобот: \n 1) иҷрои талаботи Қонуни конститутсионии мазкурро дар ҳудуди ҳавзаи интихобот назорат мекунад; \n 2) ба интихобкунандагон дар бораи маҳалли участкаҳои интихобот хабар медиҳад; \n 3) ба номзадҳо барои иштирок дар маъракаи интихоботи шароити баробарро таъмин мекунад; \n 4) номзадҳои ба вакилӣ пешбаригардида, шахсони боэътимоди онҳоро ба қайд мегирад ва ба онҳо шаҳодатномаи дахлдор медиҳад; \n 5) барои ташкили вохӯриҳои номзадҳо ба вакилӣ ва интихобкунандагон мусоидат мекунад; \n 6) ахбори комиссияҳои участкавӣ, намояндагони ҳизбҳои сиёсӣ, мақомоти худидоракунии шаҳрак ва деҳот, роҳбарони ташкилотҳо, ҳамчунин мақомоти иттиҳодияҳои ҷамъиятиро вобаста ба масъалаҳои тайёрии интихобот ва гузаронидани он мешунавад; \n 7) аз тарафи комиссияҳои участкавии интихобот тартиб додан ва барои шиносоии умум пешниҳод кардани рӯйхати интихобкунандагонро назорат менамояд; \n 8) матни бюллетени интихоботро аз рӯи ҳавзаи интихобот тасдиқ мекунад ва участкаҳои интихоботро бо бюллетенҳои интихобот таъмин менамояд; \n 9) натиҷаи интихоботи ҳавзаро муқаррар ва ба комиссияи дахлдори вилоятӣ, шаҳрӣ ноҳиявии интихобот месупорад; \n 10) натиҷаи интихоботро оид ба ҳавзаи интихобот муқаррар ва чоп карда, ба вакили интихобшуда шаҳодатнома медиҳад; \n 11) овоздиҳии такрорӣ ва интихоботи такрорӣ, ҳамчунин интихоботи вакилонро ба ивази вакилони хориҷшуда ташкил менамояд; \n 12) арзу шикоятҳоро аз хусуси қарорҳо ва фаъолияти комиссияҳои участкавии интихобот баррасӣ намуда, доир ба онҳо қарорҳо қабул мекунад; \n 13) ваколатҳои дигарро тибқи Қонуни конститутсионии мазкур ва дигар санадҳои меъёрии ҳуқуқии Ҷумҳурии Тоҷикистон амалӣ менамояд. (ҚКҶТ аз 26.07.14 с., № 1087)' : ' Окружная избирательная комиссия: \n 1) осуществляет контроль исполнения требований настоящего конституционного Закона на территории избирательного округа; \n 2) информирует избирателей о местонахождении избирательных участков; \n 3) обеспечивает равные условия кандидатам в депутаты для участия в избирательной кампании; \n 4) регистрирует выдвинутых кандидатов в депутаты, их доверенных лиц и выдает им соответствующие удостоверения; \n 5) содействует организации встреч кандидатов в депутаты с избирателями; \n 6) заслушивает сообщения участковых комиссий, представителей политических партий, органов самоуправления поселков и сел и руководителей организаций, а также органов общественных объединений по вопросам, связанным с подготовкой и проведением выборов; \n 7) наблюдает за составлением списков избирателей участковыми избирательными комиссиями и представлением их для всеобщего ознакомления; \n 8) утверждает текст избирательного бюллетеня по избирательному округу и обеспечивает избирательными бюллетенями участковые избирательные комиссии; \n 9) устанавливает и передает в соответствующую областную, городскую, районную избирательную комиссию результаты выборов по избирательному округу; \n 10) устанавливает и публикует в печати результаты выборов по избирательному округу, выдает удостоверение избранному депутату; \n 11) организует проведение повторного голосования и повторных выборов, а также выборов депутатов вместо выбывших; \n 12) рассматривает заявления и жалобы на решения и действия участковых избирательных комиссий и принимает по ним решения; \n 13) осуществляет другие полномочия в соответствии с настоящим конституционным Законом и другими нормативными правовыми актами Республики Таджикистан. (КЗРТ от 26.07.14 г., № 1087)'}
                    </Text>
                    <Text style={styles.listItem}>
                        {language === 'Тоҷики' ? 'Моддаи 14. Ташкил кардани комиссияи участкавии интихобот' : 'Статья 14. Образование участковой избирательной комиссии'}
                    </Text>
                    <Text style={styles.textItem}>
                        {language === 'Тоҷики' ? ' Комиссияи участкавии интихоботро комиссияи шаҳрӣ, ноҳиявии интихобот 45 рӯз пеш аз интихобот дар ҳайати 5-19 аъзо, аз ҷумла раис, муовини раис, котиб ташкил мекунад. Агар комиссия дар ҳайати 7 нафар ташкил шавад, раис ва котиб интихоб карда мешавад. \n Дар сурати зарурат ҳайати аъзои комиссияи интихобот метавонад зиёд ва кам шавад.' : ' Участковая избирательная комиссия образуется городской, районной избирательной комиссией не позднее чем за 45 дней до выборов в составе 5-19 членов, в том числе председателя, заместителя председателя и секретаря. Если комиссия образуется в составе до 7 человек, то избираются председатель и секретарь. \n В необходимых случаях численный состав избирательной комиссии может быть увеличен или уменьшен.'}
                    </Text>
                    <Text style={styles.listItem}>
                        {language === 'Тоҷики' ? 'Моддаи 15. Ваколатҳои комиссияҳои участкавии интихобот' : 'Статья 15. Полномочия участковой избирательной комиссии'}
                    </Text>
                    <Text style={styles.textItem}>
                        {language === 'Тоҷики' ? ' Комиссияи участкавии интихобот: \n 1) рӯйхати интихобкунандагони участкаро тартиб медиҳад; \n 2) интихобкунандагонро бо рӯйхати иитихобкунандагон шинос мекунад, аризаҳоро дар бораи саҳву ғалатҳои рӯйхат қабул ва баррасӣ менамояд ва масъалаи ба он дохил намудани тағйироти дахлдорро ҳал мекунад; \n 3) аз интихобкунандагоне, ки дар рӯзи интихобот барои дар ҷои истиқомат будан ва дар овоздиҳӣ иштирок кардан имконият надоранд, лифофаҳои сарбастаи дорои варақаҳои интихоботро қабул мекунад; \n 4) аҳолиро дар бораи маҳалли воқеъгаштаи комиссияи участкавии интихобот, вақти кори он, инчунин рӯзи интихобот ва ҷои овоздиҳӣ хабардор мекунад; \n 5) бинои овоздиҳӣ ва қуттиҳои интихобот ва дигар таҷҳизоти интихоботро тайёр мекунад; \n 6) овоздиҳиро дар рӯзи интихобот дар участкаи интихобот ташкил менамояд; \n 7) овозҳои дар участкаи интихобот додашударо ҳисоб мекунад; \n 8) арзу шикоятҳоро доир ба масъалаҳои тайёрӣ ба интихобот ва ташкили овоздиҳӣ баррасӣ карда, оид ба онҳо қарорҳо қабул мекунад; \n 9) ваколатҳои дигарро тибқи Қонуни конститутсионии мазкур ва дигар санадҳои меъёрии ҳуқуқии Ҷумҳурии Тоҷикистон амалӣ менамояд. (ҚКҶТ аз 26.07.14 с., № 1087)' : ' Участковая избирательная комиссия: \n 1) составляет списки избирателей по участку; \n 2) проводит ознакомление избирателей со списком избирателей, принимает и рассматривает заявления об ошибках и неточностях в списке и решает вопрос о внесении в него соответствующих изменений; \n 3) принимает от избирателей, не имеющих возможности в день выборов находиться по месту своего жительства и участвовать в голосовании, запечатанные конверты с избирательными листами; \n 4) оповещает население о местонахождении участковой избирательной комиссии, времени ее работы, а также о дне выборов и месте голосования; \n 5) обеспечивает подготовку помещения для голосования, избирательных ящиков и другого избирательного оборудования; \n 6) организует на избирательном участке голосование в день выборов; \n 7) производит подсчет голосов, поданных на избирательном участке; \n 8) рассматривает заявления и жалобы по вопросам подготовки выборов, организации голосования и принимает по ним решения; \n 9) осуществляет другие полномочия в соответствии с настоящим конституционным Законом и другими нормативными правовыми актами Республики Таджикистан. (КЗРТ от 26.07.14 г., № 1087)'}
                    </Text>
                    <Text style={styles.listItem}>
                        {language === 'Тоҷики' ? 'Моддаи 16. Узвият дар комиссияҳои интихобот' : 'Статья 16. Членство в избирательных комиссиях'}
                    </Text>
                    <Text style={styles.textItem}>
                        {language === 'Тоҷики' ? ' Узви комиссияи интихобот ҳангоми иҷрои ваколатҳояш бояд бетараф ва холис бошад, набояд дар фаъолияти ҳизби сиёсӣ иштирок кунад, супоришҳои онро иҷро намояд ва дар тарғибу ташвиқоти пешазинтихоботии номзадҳои бақайдгирифташуда ширкат варзад (ҚКҶТ аз 19.07.19 с., №1640). \n Узви комиссияро аз руи аризаи шахсияш, ҳамчунин дар ҳолати маҳрум шудан аз ваколатҳояш аз иҷрои вазифа озод кардан мумкин аст. \n Дар ҳолати талабҳои ҳамин Қонунро риоя накардан ва мудом ба вазифаҳои худ беэътиноӣ намудани узви комиссия ҳуқуқи аз ваколатҳояш маҳрум кардани ӯ ба мақоме тааллуқ дорад, ки ин комиссияро таъсис додааст. \n Дар сурати зарурат узви нави комиссияи интихобот мутобиқи тартиби муқаррарнамудаи ҳамин Қонун таъйин карда мешавад. \n Номзад ба вакилии Маҷлиси маҳаллии вакилони халқ, узви комиссияи вилоятӣ, шаҳрӣ, ноҳиявии интихобот, инчунин комиссияи ҳавзавӣ ё участкавии интихобот, ки овоздиҳиро дар ҳамон ҳавза ё участкаи интихоботӣ ташкил мекунад, буда наметавонад. \n Як шахс метавонад узви танҳо як комиссияи интихобот бошад. (ҚКҶТ аз 26.07.14 с., № 1087)' : ' Член избирательной комиссии должен быть нейтральным и беспристрастным при исполнении своих полномочий, не должен участвовать в деятельности политической партии, исполнять её поручения, участвовать в предвыборной агитации и пропаганде зарегистрированных кандидатов в выборах (КЗРТ от 19.07.19 г., №1640). \n Член комиссии может быть освобожден от исполнения своих обязанностей по личному заявлению, а также в случае лишения полномочий. \n Право лишения полномочий члена комиссии принадлежит органу, образовавшему комиссию, при нарушении им требований настоящего Закона или систематическом игнорировании своих обязанностей. \n В случае необходимости новый член избирательной комиссии назначается в порядке, установленном настоящим Законом. \n Кандидат в депутаты местного Маджлиса народных депутатов не может быть членом областной, городской, районной избирательной комиссии, а также окружной или участковой избирательной комиссии, организующей голосование на данном избирательном округе либо участке. \n Одно и то же лицо может быть членом только одной избирательной комиссии. (КЗРТ от 26.07.14 г., № 1087)'}
                    </Text>
                    <Text style={styles.listItem}>
                        {language === 'Тоҷики' ? 'Моддаи 17. Ташкили кори комиссияҳои интихобот' : 'Статья 17. Организация работы избирательных комиссий'}
                    </Text>
                    <Text style={styles.textItem}>
                        {language === 'Тоҷики' ? ' Раис, муовини раис ва котиби комиссияи интихобот дар маҷлиси якуми комиссияи дахлдор интихоб карда мешаванд. \n Маҷлиси комиссияи интихобот дар сурате салоҳиятдор мебошад, ки дар маҷлис на камтар аз се ду ҳиссаи ҳайати комиссия иштирок намояд. Қарори комиссия ба тариқи овоздиҳии ошкоро бо овози аксарияти ҳайати умумии аъзои хузурдоштаи комиссия қабул карда мешавад. Аъзои комиссия, ки бо қарори он розӣ нестанд, ҳуқуқ доранд ақидаи махсуси худро баён намоянд, ки он дар шакли хаттӣ ба протокол замима карда мешавад. Дар сурати баробарии овозҳо овози раис ҳалкунанда мебошад. (ҚКҶТ аз 30.05.2017 с. №1412) \n Комиссияҳои интихоботӣ дар фаъолияти худ ба ҳеҷ мақомоти давлатӣ вобаста нестанд. Иҷрои қарори комиссияи интихобот, ки дар доираи ваколати он қабул шудааст, барои комиссияҳои поёнии интихобот, ҳамчунин мақомоти дахлдори маҳаллии ҳокимияти давлатӣ, ҳизбҳои сиёсӣ ва ташкилотҳо ҳатмӣ мебошад. \n Мақомоти давлатӣ ва мақомоти иттиҳодияҳои ҷамъиятӣ, корхонаҳо, муассисаҳо, ташкилотҳо, шахсони мансабдор вазифадоранд, ки ба комиссияи интихобот барои амалӣ гардонидани ваколатҳояшон кӯмак расонанд, маълумотҳои барои корашон зарурро муҳайё намоянд. \n Мақомоти давлатӣ, ташкилотҳои давлатӣ барои тайёри ва гузаронидани интихобот биноҳо, таҷҳизот, воситаҳои нақлиёт ва маводи заруриро ройгон ба ихтиёри комиссияҳои интихобот вогузор менамоянд. \n Биноҳо ва таҷхизоти корхонаҳо ва ташкилотҳои ғайридавлатӣ барои кори комиссияҳои интихобот дар асоси иҷора аз ҳисоби буҷаи ҷумҳуриявӣ истифода бурда мешаванд. \n Комиссияи интихобот хуқуқ дорад оид ба масъалаҳои тайёрӣ ва гузаронидани интихобот ба мақомоти дахлдори давлатӣ ва мақомоти иттиҳодияҳои ҷамъиятӣ, ба ташкилотҳо, шахсони мансабдор муроҷиат намояд ва онҳо вазифадор ҳастанд, ки масъалаи ба миён гузоштаро баррасӣ намуда, дар мӯҳлати на дертар аз се рӯз ба комиссияи интихобот ҷавоб диҳанд. (ҚКҶТ аз 30.05.2017 с., №1412) \n Раис, муовини раис, котиб ё дигар аъзои комиссияи интихобот бо қарори он дар давраи тайёрӣ ва гузаронидани интихобот мумкин аст аз иҷрои вазифаҳои истеҳсолӣ ё хизматӣ озод карда шаванд, ки дар ин ҳол маоши миёнаи онҳо аз ҳисоби маблағи барои гузаронидани интихобот ҷудокарда дода мешавад. \n Комиссияҳои ҳавзавӣ ва участкавии интихобот баъд аз он, ки Маҷлиси дахлдори вакилони халқ ваколатҳои вакилонро оид ба ҳавзаҳои дахлдори интихоботӣ тасдиқ кард, фаъолияти худро катъ менамоянд.' : ' Председатель, заместитель председателя и секретарь избирательной комиссии избираются на первом заседании соответствующей комиссии. \n Заседание избирательной комиссии является правомочным, если в нем принимает участие не менее двух третей состава комиссии на заседании. Решение комиссии принимается открытым голосованием большинством голосов от общего состава присутствующих членов комиссии. Члены комиссии, не согласные с ее решением, вправе высказать особое мнение, которое в письменной форме прилагается к протоколу. В случае равного деления голосов голос председательствующего является решающим. \n Решение избирательной комиссии, принятое в пределах ее полномочий, обязательно для исполнения нижестоящими избирательными комиссиями, а также соответствующими местными органами государственной власти, политическими партиями, учреждениями и организациями. \n Избирательные комиссии в своей деятельности не зависят от каких – либо государственных органов. Решение избирательной комиссии, принятое в пределах ее полномочий, обязательно для исполнения нижестоящими избирательными комиссиями, а также соответствующими местными органами государственной власти, политическими партиями и организациями. \n Государственные органы и органы общественных объединений, предприятия, учреждения, организации, должностные лица обязаны содействовать избирательным комиссиям в осуществлении ими своих полномочий, предоставлять необходимые для их работы сведения. \n Государственные органы и организации бесплатно предоставляют в распоряжение избирательных комиссий необходимые для подготовки и проведения выборов помещения, оборудование, транспортные средства и необходимые материалы. \n Помещения и оборудование предприятий негосударственных организаций для избирательных комиссий используются на арендных условиях за счет бюджета республики. \n Избирательная комиссия имеет право обращаться по вопросам, связанным с подготовкой и проведением выборов, в соответствующие местные органы государственной власти и органы общественных объединений, к организациям, должностным лицам, которые обязаны рассмотреть поставленный вопрос и дать избирательной комиссии ответ не позднее чем в трехдневный срок. Председатель, заместитель председателя, секретарь либо другой член избирательной комиссии по ее решению может освобождаться в период подготовки и проведения выборов от выполнения производственных или служебных обязанностей с сохранением средней заработной платы за счет средств, выделяемых на проведение выборов. \n Окружные и участковые избирательные комиссии прекращают свою деятельность, после утверждения соответствующим Маджлисом народных депутатов полномочий депутатов по соответствующим избирательным округам. \n Избирательная комиссия Горно-Бадахшанской автономной области, областные, города Душанбе городские и районные избирательные комиссии прекращают свою деятельность после избрания депутатов во всех избирательных округах по выборам в соответствующий Маджлис народных депутатов. (КЗРТ от 26.07.14 г., № 1087)'}
                    </Text>
                    <Text style={styles.listItem}>
                        {language === 'Тоҷики' ? 'Моддаи 18. Баррасии шикоят аз қарори комиссияҳои интихобот' : 'Статья 18. Рассмотрение жалоб на решения избирательных комиссий'}
                    </Text>
                    <Text style={styles.textItem}>
                        {language === 'Тоҷики' ? ' Мақомоти ҳизбҳои сиёсӣ, ки номзадҳоро ба вакилӣ пешбарӣ намудаанд, номзадҳо ба вакилӣ, шахсони боэътимод, нозирон ва интихобкунандагон аз қарори комиссияи интихобот дар давоми даҳ рӯзи баъд аз қабули қарор ба комиссияи болоии интихобот ё суд шикоят карда метавонанд. Шикоят бояд дар давоми се рӯзи баъд аз расиданаш ва агар то рӯзи интихобот камтар аз шаш руз монда бошад, фавран баррасӣ карда шавад.' : ' Решения избирательных комиссий могут быть обжалованы органами политических партий, выдвинувшими кандидатов в депутаты, кандидатами в депутаты, доверенными лицами, наблюдателями и избирателями в вышестоящую избирательную комиссию или в суд в течение десяти дней после принятия решения. Жалоба должна быть рассмотрена в течение трех дней после ее поступления, а если до дня выборов осталось меньше шести дней, незамедлительно.'}
                    </Text>
                </View>
            </ScrollView>
        </View>
    );
};

