import React from 'react';
import { Platform, View, Text, StyleSheet, Image, ScrollView } from 'react-native';
import { useLanguage } from '../LanguageContext';

const Section7Page2 = () => {
    const { language } = useLanguage();

    return (
        <View style={Platform.OS === 'web' ? styles.webcontainer : styles.container}>
            <Image
                source={require('../images/parcham.png')}
                style={styles.backgroundImage}
                resizeMode="contain"
            />
            <View style={styles.overlay} />

            <ScrollView contentContainerStyle={styles.scrollContent}>
                <View style={styles.listContainer}>
                    <Text style={styles.listItem}>
                        {language === 'Тоҷики' ? 'I. МУҚАРРАРОТИ УМУМӢ' : 'I. ОБЩИЕ ПОЛОЖЕНИЯ'}
                    </Text>
                    <Text style={styles.textItem}>
                        {language === 'Тоҷики' ? ' 1. Интихоботи вакилони Маҷлиси намояндагони Маҷлиси Олии Ҷумҳурии Тоҷикистон дар асоси ҳуқуқи умумӣ, баробар ва бевосита, бо овоздиҳии пинҳонӣ ва низоми омехтаи интихобот сурат мегирад. \n 2. Вакили Маҷлиси намояндагони Маҷлиси Олии Ҷумҳурии Тоҷикистон шахсе интихоб шуда метавонад, ки танҳо шаҳрвандии Ҷумҳурии Тоҷикистонро дошта, синни ӯ аз 30 кам набуда, дорои таҳсилоти олӣ бошад ва забони давлатиро донад. \n 3. Шаҳрвандони зерин ба вакилии Маҷлиси намояндагони Маҷлиси Олии Ҷумҳурии Тоҷикистон ҳуқуқи номзад шуданро надоранд: \n а) шаҳрвандоне, ки ба талаботи муқарраркардаи Конститутсияи Ҷумҳурии Тоҷикистон ва Қонуни конститутсионии Ҷумҳурии Тоҷикистон «Дар бораи интихоботи Маҷлиси Олии Ҷумҳурии Тоҷикистон» ҷавобгӯ нестанд; \n б) шаҳрвандоне, ки забони давлатиро намедонанд; \n в) шаҳрвандоне, ки дар ҳудуди ҷумҳурӣ камтар аз 5 соли охир истиқомат доранд; \n г) шахсоне, ки шаҳрвандияшон қатъ гардидааст ё шаҳрвандии давлати дигарро соҳиб шудаанд; \n д) шаҳрвандоне, ки суд онҳоро ғайри қобили амал донистааст ё бо ҳукми суд дар ҷойҳои маҳрумӣ аз озодӣ нигоҳ дошта мешаванд ва ё бо қарори суд дар муассисаҳои табобати маҷбурӣ мебошанд; \n е) хизматчиёни ҳарбии Қувваҳои Мусаллаҳи Ҷумҳурии Тоҷикистон, дигар қӯшунҳо ва сохторҳои ҳарбӣ, вазорату идораҳое, ки дар онҳо қонун хизмати ҳарбиро пешбинӣ намудааст, шахсони ҳайати қаторӣ ва роҳбарикунандаи мақомоти корҳои дохилӣ, мақомоти гумрук ва Агентии назорати маводи нашъаовари назди Президенти Ҷумҳурии Тоҷикистон; \n ё) ходимони касбии ташкилотҳо ва иттиҳодияҳои динӣ ҳангоми дар вазифа буданашон; \n ж) шаҳрвандоне, ки барои содир кардани ҷиноятҳои барқасдонаи вазнин ва махсусан вазнин суд шудаанд, новобаста ба гузаштани мӯҳлат ва бардошта шудани доғи судиашон; \n з) шаҳрвандоне, ки доғи судиашон бардошта нашудааст; \n и) шаҳрвандоне, ки барои содир кардани ҷиноятҳои ба муқобили асосҳои сохти конститутсионӣ ва амнияти давлат ва дигар ҷиноятҳои барқасдонаи вазнин ва ё махсусан вазнин аз тарафи мақомоти таҳқиқу тафтишот гумонбар шудаанд ё нисбати онҳо кофтуков эълон карда шудааст, ба истиснои иштироккунандагони муқовимати сиёсию низомӣ, ки шомили авф гардидаанд. \n 4. Ба мардону занон барои иштирок дар раванди интихобот ҳуқуқҳои баробари интихобот ва кафолатҳои он дода мешавад.' : ' 1. Выборы депутатов Маджлиси намояндагон (Маджлиси Оли Республики Таджикистан) проводятся на основе всеобщего, равного и прямого избирательного права при тайном голосовании и смешанной избирательной системе. \n 2. Депутатом Маджлиси намояндагон Маджлиси Оли Республики Таджикистан может быть избран только гражданин Республики Таджикистан, достигший возраста не менее 30 лет, имеющий высшее образование и владеющий государственным языком. \n 3. Следующие граждане не имеют права выдвигаться в качестве кандидатов в депутаты Маджлиси намояндагон Маджлиси Оли Республики Таджикистан: \n а) граждане, которые не соответствуют требованиям, установленным Конституцией Республики Таджикистан и Конституционным законом Республики Таджикистан «О выборах Маджлиси Оли Республики Таджикистан»; \n б) граждане, не владеющие государственным языком; \n в) граждане, которые не проживали на территории Республики в течение последних 5 лет; г) лица, утратившие гражданство или имеющие гражданство другого государства; \n д) граждане, признанные судом недееспособными, или содержащиеся по приговору суда в местах лишения свободы, а также лица, находящиеся на принудительном лечении по решению суда; \n е) военнослужащие Вооружённых Сил Республики Таджикистан, других воинских формирований и силовых структур, а также сотрудники министерств и ведомств, где предусмотрена военная служба, лица рядового и руководящего состава органов внутренних дел, таможенных органов и Агентства по контролю за наркотиками при Президенте Республики Таджикистан; \n ё) профессиональные служители религиозных организаций и объединений, занимающие свои должности; \n ж) граждане, осуждённые за умышленные тяжкие и особо тяжкие преступления, независимо от истечения срока давности и снятия судимости; \n з) граждане, у которых судимость не снята; \n и) граждане, подозреваемые или обвиняемые органами дознания и следствия в совершении преступлений против основ конституционного строя и государственной безопасности, а также других умышленных тяжких и особо тяжких преступлений, находящиеся в розыске, за исключением участников политико-военного противостояния, подпавших под амнистию. Мужчины и женщины имеют равные избирательные права и гарантии для участия в избирательном процессе.'}
                    </Text>
                    <Text style={styles.listItem}>
                        {language === 'Тоҷики' ? 'II. РОҲҲОИ ПЕШБАРӢ НАМУДАНИ НОМЗАДҲО БА ВАКИЛИИ МАҶЛИСИ НАМОЯНДАГОН' : 'II. СПОСОБЫ ВЫДВИЖЕНИЯ КАНДИДАТОВ В ДЕПУТАТЫ МАДЖЛИСИ НАМОЯНДАГОН'}
                    </Text>
                    <Text style={styles.textItem}>
                        {language === 'Тоҷики' ? ' 5. Барои интихоботи вакилони Маҷлиси намояндагони Маҷлиси Олии Ҷумҳурии Тоҷикистон дар ҳудуди Ҷумҳурии Тоҷикистон як ҳавзаи ягонаи умумиҷумҳуриявӣ ва 41 ҳавзаи ҳудудии якмандатаи интихоботӣ ташкил карда мешавад. \n 6. Ба ҳавзаи ягонаи умумиҷумҳуриявӣ танҳо ҳизбҳои сиёсӣ номзад пешниҳод карда метавонанд. \n 7. Ба 41 ҳавзаи ҳудудии якмандатаи интихоботӣ ҳизбҳои сиёсӣ, инчунин шаҳрвандон бо роҳи худпешбарӣ ҳуқуқ доранд номзад пешниҳод кунанд. \n 8. Номзади худпешбар бояд донад: а) Пешбарии номзадҳо ба вакилии Маҷлиси намояндагон дар ҳавзаҳои якмандатаи интихобот 60 рӯз пеш аз интихобот оғоз шуда, 45 рӯз пеш аз интихобот анҷом меёбад; б) Ҷамъоварии на камтар аз 500 (панҷсад) имзои интихобкунандагони ҳавзаи якмандатаи интихоботии дахлдор барои тарафдории худ дар варақаҳои имзогузорӣ, ки намунаашро Комиссияи марказии интихобот ва раъйпурсӣ муайян намудааст. Дар варақаҳои имзогузорӣ насаб, ном ва номи падари номзад ва шахси ташаббускори номзад, ҷойи истиқомат, силсила ва рақами шиноснома ё ҳуҷҷати дигаре, ки шахсияти ташаббускорро тасдиқ мекунад; рақамҳои тартибӣ, насаб, ном ва номи падар, рӯз, моҳ ва соли таваллуд, шиноснома ё ҳуҷҷати дигаре, ки шахсияти имзогузорандаро тасдиқ мекунад (шиносномаи хизматӣ, шаҳодатномаи ронандагӣ ва корти идентификатсионӣ), силсила ва рақами он ҳуҷҷат, ҷойи истиқомат, имзои шахси имзогузоранда ва таърихи имзои варақа сабт мегарданд. Ҳар интихобкунанда бояд ба тарафдории фақат як номзад аз ҳавзаи маҳалли истиқомати худ имзо гузорад. \n 9. Варақаи имзогузорӣ аз комиссияи ҳавзавии ҳудудии якмандатии интихобот дастрас карда мешавад.' : ' 5. Для выборов депутатов Маджлиси намояндагон Маджлиси Оли Республики Таджикистан на территории Республики Таджикистан создаются один общереспубликанский избирательный округ и 41 территориальный одномандатный избирательный округ. \n 6. В общереспубликанском избирательном округе кандидатов могут выдвигать только политические партии. \n 7. В 41 территориальном одномандатном избирательном округе право выдвигать кандидатов имеют как политические партии, так и граждане путем самовыдвижения. 8. Кандидат самовыдвиженец должен знать: \n а) Выдвижение кандидатов в депутаты Маджлиси намояндагон в одномандатных избирательных округах начинается за 60 дней до выборов и завершается за 45 дней до выборов; \n б) Сбор не менее 500 (пятисот) подписей избирателей соответствующего одномандатного избирательного округа в поддержку своей кандидатуры на подписных листах, форма которых утверждена Центральной избирательной комиссией и референдумом. В подписных листах указываются фамилия, имя и отчество кандидата и инициатора выдвижения, место жительства, серия и номер паспорта или другого документа, удостоверяющего личность инициатора; порядковый номер, фамилия, имя и отчество, дата, месяц и год рождения избирателя, паспорт или другой документ, удостоверяющий личность подписывающего (служебный паспорт, водительское удостоверение или идентификационная карта), серия и номер документа, место жительства, подпись подписавшегося лица и дата подписи. Каждый избиратель имеет право поставить подпись только за одного кандидата из округа, в котором он проживает. \n 9. Подписной лист выдается окружной избирательной комиссией соответствующего одномандатного округа.'}
                    </Text>
                    <Text style={styles.listItem}>
                        {language === 'Тоҷики' ? 'III. ГАРАВИ ИНТИХОБОТӢ' : 'III. ИЗБИРАТЕЛЬНЫЙ ЗАЛОГ'}
                    </Text>
                    <Text style={styles.textItem}>
                        {language === 'Тоҷики' ? ' 10. Номзад ба вакили Маҷлиси намояндагон дар ҳавзаҳои якмандатаи интихобот ва баъди аз тарафи комиссияи ҳавзавии интихобот муайян намудани мувофиқати ӯ ба талаботи Конститутсияи Ҷумҳурии Тоҷикистон ва Қонуни конститутсионии Ҷумҳурии Тоҷикистон «Дар бораи интихоботи Маҷлиси Олии Ҷумҳурии Тоҷикистон» пеш аз бақайдгирӣ аз маблағи шахсии худ ба суратҳисоби муваққатии махсуси Корхонаи воҳиди давлатии Бонки амонатгузории Ҷумҳурии Тоҷикистон «Амонатбонк» ва шуъбаҳои он ба миқдори 100 (сад) нишондиҳанда барои ҳисобҳо гарави интихоботӣ месупорад. Ҳамзамон расиди супоридани гарави интихоботро фавран ба комиссияи дахлдори интихобот дастрас мекунад. Дар ин бора аз ҷониби бонки мазкур фавран ба Комиссияи марказии интихобот ва раъйпурсии Ҷумҳурии Тоҷикистон хабар дода мешавад. \n 11. Дар сурати ба даст овардани на кам аз 10 фоизи овозҳои интихобкунандагони ҳавзаи интихоботии дахлдори дар овоздиҳӣ иштироккарда гарави интихоботӣ ба номзад баргардонида мешавад. Ҳангоми бекор кардани қарори пешбарии номзад аз тарафи ҳизби сиёсӣ ё пас гирифтани номзадӣ аз ҷониби номзад гарави интихоботӣ бо қарори Комиссияи марказии интихобот ва раъйпурсии Ҷумҳурии Тоҷикистон ба буҷети ҷумҳуриявӣ мегузарад.' : ' 10. Кандидат в депутаты Маджлиси намояндагон в одномандатных избирательных округах, после подтверждения избирательной окружной комиссией его соответствия требованиям Конституции Республики Таджикистан и Конституционного закона Республики Таджикистан «О выборах в Маджлиси Оли Республики Таджикистан», до регистрации вносит из личных средств избирательный залог в размере 100 (ста) расчетных показателей на специальный временный счет Государственного унитарного предприятия "Сберегательный банк Республики Таджикистан «Амонатбонк»" и его отделений. Одновременно квитанция о внесении избирательного залога незамедлительно предоставляется в соответствующую избирательную комиссию. Банк незамедлительно уведомляет Центральную комиссию по выборам и референдумам Республики Таджикистан о внесении залога. \n 11. Избирательный залог возвращается кандидату, если он получает не менее 10% голосов избирателей соответствующего избирательного округа, принявших участие в голосовании. В случае аннулирования решения о выдвижении кандидата политической партией или отзыва кандидатуры самим кандидатом избирательный залог по решению Центральной комиссии по выборам и референдумам Республики Таджикистан передается в республиканский бюджет.'}
                    </Text>
                    <Text style={styles.listItem}>
                        {language === 'Тоҷики' ? 'IV. БАҚАЙДГИРИИ НОМЗАДҲОИ ХУДПЕШБАР БА ВАКИЛИИ МАҶЛИСИ НАМОЯНДАГОН' : 'IV. РЕГИСТРАЦИЯ САМОВЫДВИЖЕНЦЕВ В КАНДИДАТЫ НА ДЕПУТАТЫ МАДЖЛИСИ НАМОЯНДАГОН'}
                    </Text>
                    <Text style={styles.textItem}>
                        {language === 'Тоҷики' ? ' 12. Барои бақайдгирии номзад бо роҳи худпешбарӣ ба вакилии Маҷлиси намояндагон ба комиссияи ҳавзавии ҳудудии якмандатии интихобот ҳуҷҷатҳои зерин пешниҳод карда мешаванд: \n а) аризаи номзад оид ба розигии вай дар бораи ба овоздиҳӣ мондани номзадии худ якҷоя бо варақаҳои имзогирӣ; дар ариза ӯҳдадории номзад оид ба қатъи фаъолияти кории худ дар сурате, ки он бо мандати вакилӣ номувофиқ бошад, бояд зикр гардад; \n б) маълумот оид ба тарҷумаи ҳоли номзад бо зикри насаб, ном ва номи падар, санаи таваллуд, ҷойи кор, мансаби ишғолкардааш (машғулият) ва маҳалли зист; \n в) нусхаи шиносномаи (корти идентификатсионии) номзад; \n г) нусхаи ҳуҷҷати расмие, ки доштани таҳсилоти олии номзадро тасдиқ мекунад; \n д) маълумотнома аз ҷойи истиқомат оид ба муҳлати истиқомати номзад дар ҳудуди ҷумҳурӣ; \n е) хулосаи Комиссияи марказии интихобот ва раъйпурсии Ҷумҳурии Тоҷикистон дар хусуси муайян намудани сатҳи донистани забони давлатӣ аз ҷониби номзад; \n ё) маълумот дар бораи ҳаҷм ва манбаи даромади номзад; \n ж) маълумот дар бораи амволе, ки ба моликияти номзад мансуб аст; \n з) маълумоти тиббӣ дар бораи солимии рӯҳӣ; \n и) ҳуҷҷат дар бораи гарави интихоботӣ; \n й) маълумот дар бораи ба ҷавобгарии ҷиноятӣ кашида нашудан, маҳкум нашудан ва доғи судӣ надоштан. Маълумоти мазкур бо талаби комиссияи ҳавзавии якмандатии интихобот аз ҷониби мақомоти дахлдори корҳои дохилӣ пешниҳод мегардад. \n 13. Маълумот дар бораи ҳаҷм ва манбаи даромади шаҳрванд, ки номзад мебошад, дар намуди нусхаи эъломия дар бораи даромади яксолаи пеш аз соли таъйин шудани интихобот (бо қайди мақомоти андоз) пешниҳод карда мешавад. Агар шаҳрванд-номзад мувофиқи қонунгузории Ҷумҳурии Тоҷикистон дар бораи андозҳо барои пешниҳод кардани эъломия дар бораи даромад ӯҳдадор набошад, оид ба ҳаҷми умумии даромад дар як соли пеш аз таъйин шудани интихобот аз ташкилоте, ки даромад гирифтааст, маълумотнома пешниҳод менамояд. Маълумотнома дар бораи амволе, ки ба ҳуқуқи моликияти номзад мансуб аст, дар шакли муқарраркардаи Комиссияи марказии интихобот ва раъйпурсии Ҷумҳурии Тоҷикистон пешниҳод карда мешавад. \n 14. Комиссияи ҳавзавии ҳудудии якмандатии интихобот ба талаботи Қонуни конститутсионии Ҷумҳурии Тоҷикистон «Дар бораи интихоботи Маҷлиси Олии Ҷумҳурии Тоҷикистон» мувофиқат доштани тартиби пешбарии номзад бо роҳи худпешбариро месанҷад. Дар ин маврид дурустии имзоҳои интихобкунандагон, ки ба тарафдории номзад ҷамъ карда шудаанд, санҷида мешавад. Тамоми варақаҳо ё як қисми онҳо, ки интихобан гирифта шудаанд, мавриди санҷиш қарор мегиранд. Дар сурати ошкор шудани нодурустии имзоҳо, ки боиси аз 500 (панҷсад) кам шудани шумораи имзоҳои ҷамъоваришуда мегардад ва ё пешниҳоди маълумоти нодуруст оид ба даромадҳо ва ё амволе, ки ба моликияти номзад мансуб аст, бақайдгирии номзад рад карда мешавад. Нусхаи қарори комиссияи ҳавзавии ҳудудии якмандатии интихобот дар бораи рад кардани бақайдгирӣ бо баёни асосҳои радкунӣ ба номзад бо роҳи худпешбарӣ дар давоми як шабонарӯз дода мешавад. Номзад бо роҳи худпешбарӣ вобаста ба қарори комиссияи ҳавзавии ҳудудии якмандатии интихобот дар бораи рад кардани бақайдгирии номзад метавонад дар давоми 10 рӯзи пас аз қабул гардидани қарор ба Комиссияи марказии интихобот ва раъйпурсии Ҷумҳурии Тоҷикистон ва ё мақомоти судӣ муроҷиат намояд. Баррасии муроҷиат аз ҷониби мақомоти судӣ бо тартиби муқаррарнамудаи боби 25 Кодекси мурофиавии граждании Ҷумҳурии Тоҷикистон амалӣ мегардад. \n 15. Номзад бо роҳи худпешбарӣ фақат дар як ҳавзаи интихобот ба қайд гирифта мешавад. \n 16. Маълумот дар бораи номзадҳои бақайдгирифташуда аз рӯзи бақайдгирӣ бо замимаи пурраи ҳуҷҷатҳои бақайдгирӣ ба Комиссияи марказии интихобот ва раъйпурсии Ҷумҳурии Тоҷикистон пешниҳод карда мешавад. \n 17. Комиссияи ҳавзавии ҳудудии якмандатии интихобот дар давоми як ҳафтаи баъди бақайдгирии номзадҳо дар ҳавзаҳои ҳудудии якмандатии интихоботӣ дар матбуоти ҳудуди ҳавзаи интихоботи дахлдор дар бораи ба қайд гирифта шудани онҳо бо зикри насаб, ном ва номи падар, соли таваллуд, ҳизбият, мансаб (шуғл), ҷойи кор ва маҳалли зисти номзади худпешбар ахбор чоп мекунад. \n 18. Комиссияи дахлдори интихобот дар давоми се рӯз баъди бақайдгирӣ ба номзад шаҳодатномаи дахлдори номзадиро дар шакли муқаррарнамудаи Комиссияи марказии интихобот ва раъйпурсии Ҷумҳурии Тоҷикистон месупорад.' : ' 12. Для регистрации кандидата в депутаты Маджлиси намояндагон путем самовыдвижения в территориальную окружную избирательную комиссию предоставляются следующие документы: \n а) заявление кандидата о согласии на выдвижение своей кандидатуры на голосование вместе с листами для сбора подписей; в заявлении должно быть указано обязательство кандидата прекратить свою профессиональную деятельность, если она несовместима с мандатом депутата; \n б) сведения о биографии кандидата с указанием фамилии, имени и отчества, даты рождения, места работы, занимаемой должности (занятости) и места жительства; \n в) копия паспорта (идентификационной карты) кандидата; \n г) копия официального документа, подтверждающего наличие высшего образования у кандидата; \n д) справка с места жительства о сроке проживания кандидата на территории республики; е) заключение Центральной комиссии по выборам и референдумам Республики Таджикистан о владении \n кандидатом государственным языком; \n ё) информация о размере и источнике доходов кандидата; \n ж) сведения об имуществе, принадлежащем кандидату на праве собственности; \n з) медицинская справка о психическом здоровье; \n и) документ о внесении избирательного залога; \n й) справка об отсутствии привлечения к уголовной ответственности и судимости. Данные предоставляются по запросу окружной избирательной комиссии соответствующими органами внутренних дел. \n 13. Сведения о размере и источнике доходов гражданина, являющегося кандидатом, предоставляются в виде копии декларации о доходах за год, предшествующий году назначения выборов (с отметкой налогового органа). Если гражданин-кандидат в соответствии с законодательством Республики Таджикистан о налогах не обязан предоставлять декларацию о доходах, он предоставляет справку о совокупном доходе за год, предшествующий назначению выборов, из организации, где он получал доход. Справка о принадлежащем кандидату на праве собственности имуществе предоставляется в форме, установленной Центральной комиссией по выборам и референдумам Республики Таджикистан. \n 14. Территориальная окружная избирательная комиссия проверяет соответствие порядка самовыдвижения кандидата требованиям Конституционного закона Республики Таджикистан «О выборах в Маджлиси Оли Республики Таджикистан». При этом проверяется достоверность подписей избирателей, собранных в поддержку кандидата. Все листы или их часть, выбранные случайным образом, подвергаются проверке. В случае выявления недостоверных подписей, в результате чего общее количество собранных подписей становится менее 500 (пятисот), либо предоставления недостоверной информации о доходах или имуществе, принадлежащем кандидату, регистрация кандидата отклоняется. Копия решения территориальной окружной избирательной комиссии об отказе в регистрации с указанием причин отказа предоставляется самовыдвиженцу в течение суток. Самовыдвиженец имеет право обжаловать решение территориальной окружной избирательной комиссии об отказе в регистрации в Центральную комиссию по выборам и референдумам Республики Таджикистан или в судебные органы в течение 10 дней с момента принятия решения. Рассмотрение обращения в судебных органах осуществляется в порядке, установленном главой 25 Гражданского процессуального кодекса Республики Таджикистан. \n 15. Самовыдвиженец может быть зарегистрирован только в одном избирательном округе. \n 16. Сведения о зарегистрированных кандидатах с приложением всех регистрационных документов предоставляются в Центральную комиссию по выборам и референдумам Республики Таджикистан со дня регистрации. \n 17. Территориальная окружная избирательная комиссия в течение недели после регистрации кандидатов публикует в печатных изданиях соответствующего избирательного округа информацию о зарегистрированных кандидатах с указанием фамилии, имени, отчества, года рождения, партийной принадлежности, занимаемой должности (занятости), места работы и места жительства самовыдвиженца. \n 18. Соответствующая избирательная комиссия в течение трех дней после регистрации вручает кандидату удостоверение установленного Центральной комиссией по выборам и референдумам Республики Таджикистан образца.'}
                    </Text>
                    <Text style={styles.listItem}>
                        {language === 'Тоҷики' ? 'V. ВАЗЪИ ҲУҚУҚИИ НОМЗАДҲО БА ВАКИЛИИ МАҶЛИСИ НАМОЯНДАГОН' : 'V. ПРАВОВОЙ СТАТУС КАНДИДАТОВ В ДЕПУТАТЫ МАДЖЛИСИ НАМОЯНДАГОН'}
                    </Text>
                    <Text style={styles.textItem}>
                        {language === 'Тоҷики' ? ' 19. Ҳамаи номзадҳо ба вакилии Маҷлиси намояндагон дорои ҳуқуқ ва уҳдадориҳои баробар мебошанд. \n 20. Маъмурияти корхона, муассисаю ташкилот, ки дар онҳо номзадҳои ба қайд гирифташуда кор мекунанд, аз рӯзи бақайдгирии номзад аз тарафи комиссияи дахлдори интихобот то рӯзи эълони расмии натиҷаи умумии интихобот уҳдадоранд дар асоси аризаи номзад ӯро аз иҷрои вазифаҳои хизматӣ дар ҳар вақт ва ҳар рӯз дар давоми ин муҳлат бо нигоҳ доштани музди миёнаи меҳнат аз ҳисоби ташкилот, муассисаю корхона озод намояд. \n 21. Дар вақти гузаронидани интихобот номзади бақайдгирифташударо бо ташаббуси маъмурият (корфармо) аз кор (вазифа) озод кардан ё бе розигиаш ӯро ба кори (вазифаи) дигар гузаронидан мумкин нест. \n 22. Вақти иштироки номзади бақайдгирифташуда дар интихобот ба собиқаи кори номзад дохил карда мешавад. \n 23. Номзади бақайдгирифташударо бе розигии Комиссияи марказии интихобот ва раъйпурсии Ҷумҳурии Тоҷикистон ҳабс кардан, дастгир кардан, маҷбуран овардан, кофтуков кардан мумкин нест, ба истиснои дастгир шудани ӯ дар ҷои содир намудани ҷиноят. \n 24. Номзадҳо аз лаҳзаи ба қайд гирифта шуданашон дар маъракаи интихобот дар асосҳои баробар иштирок мекунанд, дар гузаронидани маҷлисҳои пеш аз интихобот ва маҷлисҳои дигар, истифодаи имконияти воситаҳои ахбори омма, аз ҷумла телевизион ва радио, ҳуқуқи баробар доранд. \n 25. Номзад метавонад бо барномаи фаъолияти ояндаи худ баромад кунад. Барномаи номзад набояд хилофи Конститутсияи Ҷумҳурии Тоҷикистон бошад.' : ' 30. Все кандидаты в депутаты Маджлиси намояндагон обладают равными правами и обязанностями. \n 31. Администрация предприятий, учреждений и организаций, где работают зарегистрированные кандидаты, обязана с момента регистрации кандидата избирательной комиссией до официального объявления итогов выборов на основании заявления кандидата освобождать его от выполнения служебных обязанностей в любое время и на любой день в течение указанного срока с сохранением среднего заработка за счет предприятия, учреждения или организации. \n 32. Во время проведения выборов запрещается по инициативе администрации (работодателя) увольнять зарегистрированного кандидата с работы (должности) или переводить его на другую работу (должность) без его согласия. \n 33. Время участия зарегистрированного кандидата в выборах включается в его трудовой стаж. \n 34. Зарегистрированного кандидата нельзя задерживать, арестовывать, подвергать принудительному приводу или обыску без согласия Центральной комиссии по выборам и референдумам Республики Таджикистан, за исключением случаев его задержания на месте совершения преступления. \n 35. С момента регистрации кандидаты участвуют в избирательной кампании на равных основаниях, имеют равные права на проведение предвыборных собраний и других мероприятий, а также на использование средств массовой информации, включая телевидение и радио. \n 36. Кандидат имеет право выступить с программой своей будущей деятельности. Программа кандидата не должна противоречить Конституции Республики Таджикистан.'}
                    </Text>
                    <Text style={styles.listItem}>
                        {language === 'Тоҷики' ? 'VI. ШАХСОНИ БОЭЪТИМОДИ НОМЗАДҲО БА ВАКИЛИИ МАҶЛИСИ НАМОЯНДАГОН' : 'VI. ДОВЕРЕННЫЕ ЛИЦА КАНДИДАТОВ В ДЕПУТАТЫ МАДЖЛИСИ НАМОЯНДАГОН'}
                    </Text>
                    <Text style={styles.textItem}>
                        {language === 'Тоҷики' ? ' 26. Номзадҳо бо роҳи худпешбарӣ ба комиссияи ҳавзавии якмандатаи интихобот то 5 нафар шахси боэътимод пешниҳод менамоянд. \n 27. Барои бақайдгирии шахсони боэътимод ба комиссияҳои дахлдори ҳавзавии интихобот ҳуҷҷатҳои зерин пешниҳод карда мешаванд: \n а) аризаи номзад оид ба розигии шахси боэътимод будан; \n б) маълумот оид ба ҷойи кор, мансаби ишғолкарда (машғулият) ва маҳалли зисти шахсони боэътимод; \n в) нусхаи шиносномаи (корти идентификатсионии) шахси боэътимод; \n г) маълумоти тиббӣ дар бораи солимии рӯҳии шахси боэътимод.' : ' 26. Кандидаты, выдвигающиеся путем самовыдвижения, могут предложить в окружную избирательную комиссию до 5 доверенных лиц. \n 27. Для регистрации доверенных лиц в соответствующие окружные избирательные комиссии предоставляются следующие документы: \n а) заявление кандидата о согласии доверенного лица выполнять свои обязанности; \n б) сведения о месте работы, занимаемой должности (занятости) и месте жительства доверенных лиц; \n в) копия паспорта (идентификационной карты) доверенного лица; \n г) медицинская справка о психическом здоровье доверенного лица.'}
                    </Text>
                    <Text style={styles.listItem}>
                        {language === 'Тоҷики' ? 'VII. БОЗПАС ГИРИФТАНИ НОМЗАДӢ ВА БЕКОР КАРДАНИ ҚАРОРИ БАҚАЙДГИРИИ НОМЗАД БА ВАКИЛӢ' : 'VII. ОТЗЫВ КАНДИДАТУРЫ И ОТМЕНА РЕШЕНИЯ О РЕГИСТРАЦИИ КАНДИДАТА В ДЕПУТАТЫ'}
                    </Text>
                    <Text style={styles.textItem}>
                        {language === 'Тоҷики' ? ' 28. Бекор кардани қарори бақайдгирии номзад ба вакилӣ дар ҳолатҳои зерин сурат мегирад: \n аз ҷониби номзад вайрон кардани меъёрҳои муқаррарнамудаи Конститутсия ва Қонуни конститутсионии Ҷумҳурии Тоҷикистон «Дар бораи интихоботи Маҷлиси Олии Ҷумҳурии Тоҷикистон»; дар ҳолати ихтиёран аз номзадии худ даст кашидани номзад ба вакилӣ. Дар ин ҳолат номзад бо аризаи хаттӣ ба комиссияи дахлдори интихоботӣ муроҷиат мекунад. Аризаи хаттӣ дар ин маврид ифодаи муроҷиати расмӣ ва иродаи номзад мебошад. 29. Аз рӯйи ин масъала бо тартибе, ки барои пешбарии номзад пешбинӣ шудааст, қарор қабул карда шуда, ба комиссияи дахлдори ҳавзавии интихобот пешниҳод мегардад. Ин номзадро комиссияи дахлдори ҳавзавии интихобот аз мақоми номзадӣ маҳрум месозад. \n 30. Номзад бо роҳи худпешбарӣ метавонад дар муҳлати на дертар аз 10 рӯз то рӯзи интихобот ба комиссияи ҳавзавии ҳудудии якмандатии интихобот ариза навишта, номзадиашро бозпас гирад. \n 31. Комиссияи ҳавзавии ҳудудии якмандатии интихобот, ки номзади бо роҳи худпешбарӣ пешниҳодшударо ба қайд гирифтааст, дар бораи маҳрум сохтан аз мақоми номзадӣ қарор қабул намуда, дар бораи қарори мазкур ба Комиссияи марказии интихобот ва раъйпурсии Ҷумҳурии Тоҷикистон (фавран), ба интихобкунандагони ҳавзаи дахлдори интихобот ва ба номзад бо роҳи худпешбарӣ хабар медиҳад.' : ' 28. Отмена решения о регистрации кандидата в депутаты осуществляется в следующих случаях: \n нарушение кандидатом норм, установленных Конституцией и Конституционным законом Республики Таджикистан «О выборах в Маджлиси Оли Республики Таджикистан»; добровольный отказ кандидата от участия в выборах. В этом случае кандидат подает письменное заявление в соответствующую избирательную комиссию. Письменное заявление считается официальным обращением и выражением воли кандидата. 29. По данному вопросу принимается решение в порядке, предусмотренном для выдвижения кандидатов, и направляется в соответствующую окружную избирательную комиссию. Комиссия лишает кандидата статуса. \n 30. Кандидат, выдвигающийся путем самовыдвижения, может не позднее чем за 10 дней до дня выборов подать заявление в территориальную окружную избирательную комиссию с просьбой об отзыве своей кандидатуры. \n 31. Территориальная окружная избирательная комиссия, зарегистрировавшая кандидата-самовыдвиженца, принимает решение о лишении его статуса и уведомляет об этом Центральную избирательную комиссию Республики Таджикистан (незамедлительно), избирателей соответствующего округа и самого кандидата.'}
                    </Text>
                </View>
            </ScrollView>
        </View>
    );
};

