import React from 'react';
import { Platform, View, Text, StyleSheet, Image, ScrollView } from 'react-native';
import { useLanguage } from '../LanguageContext';

const Section2Page6 = () => {
    const { language } = useLanguage();

    return (
        <View style={Platform.OS === 'web' ? styles.webcontainer : styles.container}>
            <Image
                source={require('../images/parcham.png')}
                style={styles.backgroundImage}
                resizeMode="contain"
            />
            <View style={styles.overlay} />

            <ScrollView contentContainerStyle={styles.scrollContent}>
                <View style={styles.listContainer}>
                    <Text style={styles.listItem}>
                        {language === 'Тоҷики' ? 'Моддаи 23. Таъйин кардани интихоботи вакилон' : 'Статья 23. Назначение выборов депутатов'}
                    </Text>
                    <Text style={styles.textItem}>
                        {language === 'Тоҷики' ? ' Интихоботи вакилон ба Маҷлиси вакилони халқи Вилояти Мухтори Куҳистони Бадахшон, вилоят, шаҳри Душанбе, шаҳр ва ноҳия одатан дар як вақт аз тарафи Президенти Ҷумҳурии Тоҷикистон камаш 75 рӯз пеш аз хатми мӯҳлати ваколати вакилон таъйин карда мешавад. \n Дар бораи рӯзи интихобот на дертар аз се рӯзи баъди таъйин кардани он дар матбуот ва дигар воситаҳои ахбори омма хабар дода мешавад. (ҚКҶТ аз 26.07.14 с., № 1087)' : ' Выборы депутатов в Маджлис народных депутатов Горно-Бадахшанской автономной области, областей, города Душанбе, городов и районов проводятся, как правило, одновременно и назначаются Президентом Республики Таджикистан не позднее чем за семьдесят пять дней до истечения срока полномочий депутатов. \n О дне выборов не позднее трех дней после их назначения сообщается в печати и других средствах массовой информации. (КЗРТ от 26.07.14 г., № 1087)'}
                    </Text>
                    <Text style={styles.listItem}>
                        {language === 'Тоҷики' ? 'Моддаи 24. Ҳуқуқи пешбарӣ кардани номзадҳо ба вакилӣ' : 'Статья 24. Право на выдвижение кандидатов в депутаты'}
                    </Text>
                    <Text style={styles.textItem}>
                        {language === 'Тоҷики' ? ' Барои пешбарӣ намудани номзадҳои вакилони халқ ба Маҷлисҳои вакилони халқи вилоятҳо ҳизбҳои сиёсӣ ва ҳамчунин шаҳрвандон аз тариқи худпешбарӣ ҳуқуқ доранд. \n Интихобот ба Маҷлисҳои вакилони халқи Вилояти Мухтори Куҳистони Бадахшон ва шаҳри Душанбе ба тартибе гузаронида мешавад, ки барои Маҷлисҳои вакилони халқи вилоятӣ пешбинӣ шудааст. \n Барои пешбарӣ намудани номзадҳои вакилони халқ ба Маҷлисҳои вакилони халқи шаҳрӣ ва ноҳиявӣ ҳизбҳои сиёсӣ ва ҳамчунин шаҳрвандон аз тариқи худпешбарӣ ҳуқуқ доранд. Ҳизбҳои сиёсӣ ҳуқуқ доранд шахсонеро, ки аъзои ҳизби сиёсӣ нестанд, ба вакилии Маҷлиси маҳаллии вакилони халқ номзад пешниҳод намоянд. \n Ҳизби сиёсӣ ба шарте, ки аз тарафи Вазорати адлияи Ҷумҳурии Тоҷикистон бо тартиби муқарраркардаи Қонун ба қайд гирифта шуда бошад, номзадро ба вакилӣ пешбарӣ карда метавонад. (ҚКҶТ аз 26.07.14с., № 1087)' : ' Право на выдвижение кандидатов в депутаты областного Маджлиса народных депутатов принадлежит политическим партиям, а также гражданам путем самовыдвижения. \n Выборы в Маджлисы народных депутатов Горно-Бадахшанской автономной области и города Душанбе проводятся в порядке, предусмотренном для областных Маджлисов народных депутатов. \n Право на выдвижение кандидатов в депутаты городских, районных Маджлисов народных депутатов принадлежит политическим партиям, а также гражданам путем самовыдвижения. Политические партии вправе выдвигать в депутаты местных Маджлисов народных депутатов кандидатов, не являющихся членами политических партий. \n Политическая партия может выдвигать кандидатов в депутаты при условии, что она зарегистрирована Министерством юстиции Республики Таджикистан в установленном законом порядке. (КЗРТ от 26.07.14 г., № 1087)'}
                    </Text>
                    <Text style={styles.listItem}>
                        {language === 'Тоҷики' ? 'Моддаи 25. Тартиби пешбарии номзадҳо ба вакилӣ' : 'Статья 25. Порядок выдвижения кандидатов в депутаты'}
                    </Text>
                    <Text style={styles.textItem}>
                        {language === 'Тоҷики' ? ' Пешбарӣ намудани номзадҳо ба вакили 50 рӯз пеш аз интихобот оғоз ёфта, 35 рӯз то интихобот хатм мегардад. \n Пешбарии номзадҳо ба вакилии Маҷлиси вакилони халқи вилоятҳо аз тарафи мақомоти шаҳрӣ ва ноҳиявии ҳизбҳои сиёсӣ (конференсияҳо, пленумҳо) сурат мегирад. \n Пешбарии номзадҳо ба Маҷлисҳои вакилони халқи шаҳрию ноҳиявӣ аз тарафи мақомоти дахлдори шаҳрию ноҳиявии ҳизбҳои сиёсӣ (конференсияҳо, пленумҳо) сурат мегирад. \n Аз мақомоти ҳизбҳои сиёсии шаҳрию ноҳиявӣ як нафари номзад ба вакилии маҷлиси вилоятӣ, шаҳрӣ ва ноҳиявӣ ба ҳар як ҳавзае, ки дар ҳудуди шаҳр, ноҳия воқеъанд, пешбарӣ карда мешавад. \n Тартиби хостагирии номзадҳоро худи хизбҳои сиёсӣ муайян мекунанд. \n Дар бораи пешбарии номзад ба вакилӣ аз ҳизбҳои сиёсӣ протокол тартиб дода мешавад. \n Дар бораи қарори қабулшуда ба номзад ба вакилӣ дар мӯҳлати ду рӯз хабар дода мешавад. \n Дар сурати худпешбарӣ номзад имзои сад нафар интихобкунандагонро аз ҳавзаи мазкур бо якҷоягии ариза пешниҳод менамояд. (ҚКҶТ аз 30.05.2017 с. №1412)' : ' Выдвижение кандидатов в депутаты начинается за пятьдесят дней и заканчивается за тридцать пять дней до выборов. \n Выдвижение кандидатов в депутаты областных Маджлисов народных депутатов осуществляется городскими, районными органами политических партий (на конференциях, пленумах). \n Выдвижение кандидатов в городские, районные Маджлисы народных депутатов осуществляется соответствующими городскими, районными органами политических партий (на конференциях, пленумах). \n От городского, районного органа политической партии выдвигается по одному кандидату в областной, городской и районный Маджлис народных депутатов в любом из округов, находящихся на соответствующей территории города, района. \n Порядок подбора кандидатов определяется самими политическими партиями. \n О выдвижении кандидатов в депутаты от политических партий составляются протоколы. \n О принятом решении сообщается кандидату в депутаты в двухдневный срок. \n В случае самовыдвижения кандидат представляет 100 подписей избирателей от данного округа вместе с заявлением.'}
                    </Text>
                    <Text style={styles.listItem}>
                        {language === 'Тоҷики' ? 'Моддаи 26. Талабот аз номзадҳо ба вакилӣ' : 'Статья 26. Требования, предъявляемые к кандидатам в депутаты'}
                    </Text>
                    <Text style={styles.textItem}>
                        {language === 'Тоҷики' ? ' Ба вакилии Маҷлисҳои маҳаллии вакилони халқи Вилояти Мухтори Кӯҳистони Бадахшон, вилоятҳо, шаҳри Душанбе, шаҳр ва ноҳияҳо номзадии шахсе пешниҳод карда мешавад, ки танҳо шаҳрвандии Ҷумҳурии Тоҷикистонро дошта, забони давлатиро донад, дар ҳудуди вилоят, шаҳр ва ноҳияи дахлдор кор ва ё зиндагӣ кунад. (ҚКҶТ аз 30.05.2017 с. №1412) (ҚКҶТ аз 1.08.12с.№898) \n Аъзои Ҳукумати Ҷумҳурии Тоҷикистон, судяҳо, прокурорҳо, роҳбарони мақомоти иҷроияи марказии ҳокимияти давлатӣ ва муовинони онҳо, роҳбарони сохторҳои ҳудудии мақомоти иҷроияи марказии ҳокимияти давлатӣ ва муовинони онҳо, инчунин роҳбарони сохторҳои мақомоти иҷроияи маҳаллии ҳокимияти давлатӣ дар як вақт вакили Маҷлисҳои маҳаллии вакилони халқи дахлдор буда наметавонанд ва ба шарте ҳамчун номзад ба қайд гирифта мешаванд, ки дар аризаи худ ҳангоми вакил интихоб шудан аз вазифа рафтанашонро изҳор карда бошанд. (ҚКҶТ аз 27.11.14с.№1133) \n Ходимони касбии ташкилотҳо ва иттиҳодияҳои динӣ баъди истеъфо аз вазифаашон ба қайд гирифта мешаванд. \n Шаҳрвандони зерин ҳуқуқи ба Маҷлисҳои маҳаллии вакилони халқ номзад шуданро надоранд: \n 1) шаҳрвандоне, ки ба талаботи муқарраркардаи Конститутсияи Ҷумҳурии Тоҷикистон ва Қонуни конститутсионии мазкур ҷавоб дода наметавонанд; (ҚКҶТ аз 30.05.2017 с. №1412) \n 2) шаҳрвандоне, ки суд онҳоро ғайри қобили амал донистааст ё бо ҳукми суд дар ҷойҳои маҳрумӣ аз озодӣ нигоҳ дошта мешаванд ва ё бо қарори суд дар муассисаҳои табобати маҷбурӣ мебошанд; \n 3) хизматчиёни ҳарбии Қувваҳои Мусаллаҳ, дигар қӯшуну сохторҳои ҳарбӣ, вазорату идораҳо, ки дар онҳо Қонун хизмати ҳарбиро пешбинӣ намудааст, шахсони ҳайати қаторӣ ва роҳбарикунандаи мақомоти корҳои дохилӣ, мақомоти гумрук ва Агентии назорати маводи нашъовари назди Президенти Ҷумҳурии Тоҷикистон; \n 4) шаҳрвандоне, ки барои содир кардани ҷиноятҳои барқасдонаи вазнин ва махсусан вазнин маҳкум шудаанд, новобаста аз гузаштани мӯҳлат ва бардошта шудани доғи судиашон; \n 5) шаҳрвандоне, ки доғи судиашон бардошта нашудааст; \n 6) шахсоне, ки шаҳрвандиашон қатъ гардидааст ё шаҳрвандии давлати дигарро соҳиб шудаанд; (ҚКҶТ аз 30.05.2017 с. №1412) \n 7) шаҳрвандоне, ки барои содир кардани ҷиноятҳои ба муқобили асосҳои сохти конститусионӣ ва амнияти давлат ва дигар ҷиноятҳои барқасдонаи вазнин ва ё махсусан вазнин аз тарафи мақомоти таҳқиқу тафтишот гумонбар шудаанд ё нисбати онҳо кофтуков эълон карда шудааст, ба истиснои иштироккунандагони муқовимати сиёсию низомӣ, ки нисбати онҳо авф татбиқ карда шудааст. (ҚКҶТ аз 26.07.14 с., № 1087)' : ' Кандидатом в депутаты местных Маджлисов народных депутатов Горно-Бадахшанской автономной области, областей, города Душанбе, городов и районов выдвигается лицо, имеющее только гражданство Республики Таджикистан, владеющее государственным языком, работающее или проживающее на территории соответствующей области, города или района. (КЗРТ от 1.08.12г. №898), (КЗРТ от 30.05.2017г. №1412) \n Члены Правительства Республики Таджикистан, судьи, прокуроры, руководители центральных исполнительных органов государственной власти и их заместители, руководители территориальных структур центральных исполнительных органов государственной власти и их заместители, а также руководители структур местных исполнительных органов государственной власти одновременно не могут быть депутатами соответствующих местных Маджлисов народных депутатов и регистрируются как кандидат при условии подачи ими заявления об освобождении от занимаемой должности в случае избрания депутатом. (КЗРТ от 27.11.14г.№1133) \n Профессиональные служители религиозных организаций и объединений подлежат регистрации после отставки от должности. \n Граждане, не имеющие права быть кандидатами в депутаты местных Маджлисов народных депутатов: \n 1) граждане, не отвечающие установленным требованиям Конституции Республики Таджикистан и настоящего конституционного Закона; \n 2) граждане, признанные судом недееспособными или по приговору суда находящиеся в местах лишения свободы либо находящиеся в учреждениях принудительного лечения по решению суда; \n 3) военнослужащие Вооруженных Сил, других войск и воинских формирований, министерств и ведомств, в которых Законом предусмотрена военная служба, лица рядового и начальствующего состава органов внутренних дел, таможенных органов и Агентства по контролю за наркотиками при Президенте Республики Таджикистан; \n 4) граждане, осужденные за совершение тяжких и особо тяжких умышленных преступлений, независимо от окончания срока и снятия судимости; \n 5) граждане, у которых не снята судимость; \n 6) лица, гражданство которых прекращено, или они приобрели гражданство другого государства; (КЗРТ от 30.05.2017 г. №1412) \n 7) граждане, подозреваемые органами дознания и следствия, а также находящиеся в розыске за совершение преступления против основ конституционного строя и безопасности государства или другие тяжкие и особо тяжкие преступления, за исключением участников военно-политического противостояния, в отношении которых применена амнистия. (КЗРТ от 26.07.14 г., № 1087)'}
                    </Text>
                    <Text style={styles.listItem}>
                        {language === 'Тоҷики' ? 'Моддаи 27. Бақайдгирии номзадҳо ба вакилӣ' : 'Статья 27. Регистрация кандидатов в депутаты'}
                    </Text>
                    <Text style={styles.textItem}>
                        {language === 'Тоҷики' ? ' Комиссияи ҳавзавии интихобот номзадҳоро, ки аз тарафи ҳизбҳои сиёсӣ ё ба таври худпешбарӣ пешниҳод шудаанд ба қайд мегирад. \n Бақайдгирии номзадҳо ба вакилӣ 35 рӯз пеш оғоз ёфта, 20 рӯз пеш аз интихобот ба охир мерасад. \n Қарор дар бораи бақайдгирии номзадҳо ба вакилӣ дар сурати мавҷуд будани ҳуҷҷатҳои зерин қабул карда мешавад: \n 1) қарори мақоми шаҳрӣ, ноҳиявии ҳизби сиёсӣ дар бораи пешбарӣ кардани номзадҳо ба вакилӣ; \n 2) ариза дар бораи аз вазифа рафтан дар сурати вакил интихоб шуданашон барои шахсоне, ки дар қисми дуюми моддаи 26-и ҳамин Қонун зикр ёфтаанд. \n Комиссияи ҳавзавии интихобот дар бораи ба қайд гирифтани номзадҳо ба вакилӣ протокол тартиб дода, онро ҳамроҳи аризаи номзад ба вакилӣ, ки розӣ шудааст номзадиаш дар ҳамон ҳавзаи интихобот ба овоз монда шавад, ба комиссияи дахлдори вилоятӣ, шаҳрӣ, ноҳиявии интихобот пешниҳод мекунад. \n Ҳамаи номзадҳо ба вакилӣ, ки бо риояи талаботи ҳамин Қонун пешбарӣ шудаанд, ҳатман бояд ба қайд гирифта шаванд. Дар сурати рад шудани бақайдгирии номзад, комиссияи дахлдори интихобот вазифадор аст, ки дар давоми як шабонарӯз ба номзад ва ҳизби сиёсӣ нусхаи қарори комиссияи интихоботро бо баёни асосҳои радкунӣ пешниҳод намояд. Аз хусуси рад карда шудани бақайдгирии номзад ба вакилӣ ҳизбҳои сиёсӣ, ки номзад ба вакилӣ пешбарӣ намудаанд ва номзади мустақил дар мӯҳлати се рӯз ба комиссияи интихоботии болои шикоят карда метавонанд. \n Номзад ба вакилӣ аъзои комиссияи дахлдори интихоботии вилоятӣ, шаҳрӣ, ноҳиявӣ, инчунин комиссияҳои ҳавзавӣ ё участкавии интихобот, ки овоздиҳиро дар ҳамон ҳавзаи интихобот ташкил мекунанд, буда наметавонад. Шахсе, ки номзад ба вакилӣ пешбарӣ шудааст ва аъзои яке аз ин комиссияҳо мебошад, аз вақти ба қайд гирифта шудани номзадиаш ба вакилӣ аз вазифаи аъзои комиссия озод карда мешавад. Комиссияи ҳавзавии интихобот на дертар аз рӯзи чоруми баъди ба қайд гирифтани номзадҳо ба вакилӣ дар бораи бақайдгирӣ бо нишон додани ному насаб, соли таваллуд, вазифа (шуғл), хизбият, ҷои кор ва истиқомати ҳар кадом номзад ба вакилӣ ахбор нашр менамояд. \n Номзадҳои ба вакилии дар яке аз ҳавзаҳои интихоботи Маҷлиси маҳаллӣ ба қайд гирифташуда дар дигар ҳавзаи интихобот, аз ҷумла дар сурати пас гирифтани номзадиашон дар ҳавзаи интихоботие, ки аввал ба қайд гирифта шуда буданд, барои бақайдгирӣ ба ҳавзаи дигари интихобот пешниҳод шуда наметавонанд. \n Ба ҳар кадоми номзади ба қайд гирифташуда, комиссияи ҳавзавии интихобот дар давоми се рӯзи баъди ба қайд гирифтан шаҳодатнома месупоранд. (ҚКҶТ аз 26.07.14 с., № 1087)' : ' Окружная избирательная комиссия регистрирует кандидатов в депутаты, выдвинутых политическими партиями или выдвинутых путем самовыдвижения. \n Регистрация кандидатов в депутаты начинается за тридцать пять дней, заканчивается за 20 дней до дня выборов. \n Решение о регистрации кандидатов в депутаты принимается при наличии следующих документов: решения городского, районного органа политической партии о выдвижении кандидатов в депутаты; заявления об освобождении от занимаемой должности для лиц, указанных в части второй статьи 26 настоящего Закона, в случае избрания их депутатами. \n О регистрации кандидатов в депутаты окружная избирательная комиссия составляет протокол, который вместе с заявлением кандидата в депутаты о согласии баллотироваться по данному избирательному округу представляется в соответствующую областную, городскую, районную избирательную комиссию. \n Все кандидаты в депутаты, выдвинутые с соблюдением требований настоящего Закона, в обязательном порядке должны быть зарегистрированы. В случае отказа в регистрации кандидата, соответствующая избирательная комиссия обязана в течение суток представить кандидату и политической партии копию решения избирательной комиссии с изложением оснований для отказа. Отказ в регистрации кандидата в депутаты может быть обжалован политическими партиями, выдвинувшими кандидата в депутаты, независимыми кандидатами в трехдневный срок соответственно в вышестоящую избирательную комиссию. \n Кандидат в депутаты не может состоять в соответствующей областной, городской, районной избирательной комиссии, а также в окружной или участковой избирательных комиссиях, организующих голосование по данному избирательному округу. Лицо, выдвинутое кандидатом в депутаты и состоящее в одной из указанных комиссий, освобождается от обязанностей в комиссии с момента регистрации его кандидатом в депутаты. \n Окружная избирательная комиссия не позднее чем на четвертый день после регистрации кандидатов в депутаты публикует сообщение о регистрации с указанием фамилии, имени, отчества, года рождения, занимаемой должности (занятия), партийности, места работы и жительства каждого кандидата в депутаты. \n Кандидаты в депутаты, зарегистрированные в одном избирательном округе местного Маджлиса народных депутатов не могут представляться к регистрации в другом избирательном округе, в том числе и в случае снятия своей кандидатуры в избирательном округе, где он был зарегистрирован первоначально. \n Каждому зарегистрированному кандидату в депутаты окружная избирательная комиссия в течение трех дней после регистрации выдает удостоверение. (КЗРТ от 26.07.14 г., № 1087)'}
                    </Text>
                    <Text style={styles.listItem}>
                        {language === 'Тоҷики' ? 'Моддаи 28. Бекор кардани қарори пешбарии номзад ба вакилӣ. Номзадии худро пас гирифтани номзад' : 'Статья 28. Отмена решения о выдвижении кандидатов в депутаты. Снятие кандидатом своей кандидатуры'}
                    </Text>
                    <Text style={styles.textItem}>
                        {language === 'Тоҷики' ? ' Мақоми ҳизбӣ сиёсӣ, ки номзад ба вакилӣ пешбарӣ кардааст, ҳуқуқ дорад ҳар вақт то интихобот қарори худро дар бораи пешбарии номзад ба вакили бекор кунад. Қарор дар хусуси ин масъала бо тартибе, ки барои пешбарии номзад ба вакилӣ пешбинӣ шудааст, қабул карда, ба комиссияи дахлдори ҳавзавии интихобот пешниҳод карда мешавад. Номзад ба вакилӣ ҳар вақт то интихобот номзадии худро пас гирифта, бо ариза дар ин бора ба комиссияи дахлдори ҳавзавии интихобот муроҷиат карда метавонад. \n Комиссияи интихоботие, ки номзадро ба вакилӣ ба қайд гирифтааст, дар бораи бекор кардани қарори пешбарии номзад ба вакилӣ ё дар бораи аз номзадӣ даст кашидани номзад ба маълумоти аҳолии ҳавзаи интихобот, мақоми ҳизби сиёсӣ, ки номзад ба вакилиро пешбарӣ намудааст ва номзади мустақил мерасонад.' : ' Орган политической партии, выдвинувший кандидата в депутаты, имеет право в любое время до выборов отменить свое решение о выдвижении кандидата в депутаты. Решение по этому вопросу принимается в порядке, предусмотренном для выдвижения кандидатов в депутаты, и представляется в соответствующую окружную избирательную комиссию. \n Кандидат в депутаты в любое время до выборов может снять свою кандидатуру, обратившись с заявлением об этом в соответствующую окружную избирательную комиссию. \n Об отмене решения о выдвижении кандидата в депутаты или о снятии кандидатом своей кандидатуры избирательная комиссия, зарегистрировавшая кандидата в депутаты, доводит до сведения населения избирательного округа, органа политической партии, выдвинувших кандидата, независимого кандидата в депутаты.'}
                    </Text>
                    <Text style={styles.listItem}>
                        {language === 'Тоҷики' ? 'Моддаи 29. Тартиби пешбарии номзадҳо ба ҷои номзадҳои хориҷшуда' : 'Статья 29. Порядок выдвижения кандидатов в депутаты вместо выбывших'}
                    </Text>
                    <Text style={styles.textItem}>
                        {language === 'Тоҷики' ? ' Дар сурати хориҷ шудани номзадҳо ба вакилӣ баъди итмоми мӯҳлати қайди номзадҳо ба вакилӣ, агар дар ҳавзаи интихоботӣ дигар номзадҳо набошанд, комиссияи ҳавзавии интихобот бо иҷозати комиссияи дахлдори интихоботии вилояти, шаҳрӣ, ноҳиявӣ ба мақомоти ҳизбҳои сиёсӣ ва интихобкунандагони ҳавзаи интихоботӣ муроҷиат намуда, таклиф мекунад, ки номзадҳои нав ба вакилӣ пешбарӣ карда шаванд. \n Дар сурати понздаҳ рӯз пеш аз cap шудани интихобот хориҷ шудани номзадҳо ба вакилӣ интихоботи вакил аз ҳавзаи дахлдори интихобот дар давоми ду моҳи баъди интихоботи асосӣ гузаронида мешавад.' : ' В случае выбытия кандидатов в депутаты после окончания срока регистрации кандидатов в депутаты, если в избирательном округе не остается других кандидатов, окружная избирательная комиссия с разрешения соответствующей областной, городской, районной избирательной комиссии обращается к органам политических партий и избирателям избирательного округа с предложением выдвинуть новых кандидатов в депутаты. \n При выбытии кандидатов в депутаты менее чем за пятнадцать дней до выборов выборы депутата от соответствующего избирательного округа проводится в двухмесячный срок после основных выборов.'}
                    </Text>
                    <Text style={styles.listItem}>
                    {language === 'Тоҷики' ? 'Моддаи 30. Бюллетени интихобот' : 'Статья 30. Избирательный бюллетень'}
                </Text>
                <Text style={styles.textItem}>
                    {language === 'Тоҷики' ? ' Ба бюллетени интихобот бо тартиби алифбо ному насаби номзад ба вакилӣ, соли таваллуд, ҳизбият, вазифа (шуғл), ҷои кор ва истиқомат, мақоме, ки ӯро номзад ба вакилӣ пешбарӣ намудааст, нишон дода мешавад. Ба бюллетени интихобот баъд аз рӯйхати номзадҳо сатри алоҳида бо ифодаи «муқобили ҳамаи номзадҳо» нишон дода мешавад. Бюллетенҳои интихобот ба забони давлатӣ, инчунин бо қарори комиссияи дахлдори интихоботи вилоятӣ, шаҳрӣ, ноҳиявӣ бо забонҳое, ки аксарияти кулли аҳолии ҳавзаи интихоботӣ истифода мебаранд, чоп карда мешавад. (ҚКҶТ аз 27.11.14с.№1133) \n Комиссияҳои участкавии интихобот бюллетенҳоро аз комиссияи ҳавзавӣ на дертар аз се рӯзи то интихобот мегиранд. Додану гирифтани бюллетенҳоро раис ё моувини раис ё котиби комиссияи дахлдори ҳавзавӣ ва участкавӣ бо имзои худ тасдиқ менамоянд. \n Биное, ки бюллетенҳои интихобот мавҷуданд, муҳр зада, барои посбонӣ ба мақомоти корҳои дохилӣ супорида мешавад.' : ' В избирательный бюллетень в алфавитном порядке вносятся фамилия, имя, отчество кандидата в депутаты с указанием года рождения, партийности, занимаемой должности (занятия), места работы и жительства и органа, выдвинувшего его кандидатом в депутаты. В избирательном бюллетене после списка кандидатов указывается отдельная строка «против всех кандидатов». Избирательные бюллетени печатаются на государственном языке, а также по решению соответствующей областной, городской, районной избирательной комиссии на языках, которыми пользуется большинство населения избирательного округа. (КЗРТ от 27.11.14г.№1133) \n Участковые избирательные комиссии получают бюллетени от окружной комиссии не позднее трех дней до выборов. Выдачу и получение бюллетеней председатель либо заместитель председателя или секретарь соответствующих окружной и участковой комиссий подтверждают своей подписью. \n Помещение, в котором находятся избирательные бюллетени, опечатывается и сдается под охрану органов внутренних дел.'}
                </Text>
                </View>
            </ScrollView>
        </View>
    );
};

