const Section3Page3 = () => {
  const { language } = useLanguage();

  return (
    <View style={Platform.OS === 'web' ? styles.webcontainer : styles.container}>
      <Image
        source={require('../images/parcham.png')}
        style={styles.backgroundImage}
        resizeMode="contain"
      />
      <View style={styles.overlay} />

      <ScrollView contentContainerStyle={styles.scrollContent}>
        <View style={styles.listContainer}>
          <Text style={styles.listItem}>
            {language === 'Тоҷики' ? 'Моддаи 8. Тартиби ташкили Ҷамоат' : 'Статья 8. Порядок формирования Джамоата'}
          </Text>
          <Text style={styles.textItem}>
            {language === 'Тоҷики' ? ' Ҷамоат аз вакилон иборат буда, онҳо дар асоси принсипҳои умумӣ, баробар, мустақим ва бо овоздиҳии пинҳонӣ ба мўҳлати 5 сол интихоб мешаванд. \n Шумораи вакилони Ҷамоат дар маҷлиси Ҷамоат муайян карда мешавад, ки онҳо аз 15 нафар кам ва аз 40 нафар зиёд буда наметавонанд. Ҳар як деҳа, новобаста аз шумораи интихобкунандагонаш, дар Ҷамоат бояд ҳадди ақал як нафар вакил дошта бошад. (ҚҶТ аз 30.05.17с., №1431) \n Ҷамоат дар сурати интихоб шудани на камтар аз се ду ҳиссаи шумораи умумии вакилон салоҳиятнок мебошад. \n Фаъолияти Ҷамоат дар рўзи оғози маҷлиси якуми Ҷамоати нав интихобшуда қатъ мегардад. \n Ҳангоми барҳамдиҳии воҳиди маъмурию ҳудудӣ Ҷамоат пароканда мешавад ва ваколати вакилони Ҷамоат қатъ мегардад. (ҚҶТ аз 30.05.17с., №1431) \n Ҳангоми азнавташкилдиҳии (ҳамроҳшавии, муттаҳидшавии, табдилдиҳии, тақсимшавии ва ҷудошавии) воҳидҳои маъмурию ҳудудӣ вакили Ҷамоат вакили Ҷамоати азнавташкилшудае мебошад, ки ба ҳайати он аксарияти интихобкунандагони вакил дохил мешаванд ва то оғози фаъолияти Ҷамоати нав интихобшуда ваколати худро нигоҳ медорад. (ҚҶТ аз 30.05.17с., №1431) \n Ҳангоми ташкили шаҳрак ё деҳоти нав масъалаи муайян намудани шумораи вакилони Ҷамоат, ташкили ҳавзаҳои интихобот, таъйини интихобот ва таъсиси комиссияи интихоботи Ҷамоат мутобиқи талаботи Қонуни мазкур аз ҷониби Маҷлиси вакилони халқи шаҳр ё ноҳияи дахлдор ҳал карда мешавад. (ҚҶТ аз 30.05.17с., №1431)' : ' Джамоат состоит из депутатов, которые избираются на основе всеобщих и равных принципов прямого и тайного голосования сроком на 5 лет. \n Количество депутатов Джамоата определяется на собрании Джамоата, которое не может быть менее 15 и более 40. Каждое село, независимо от числа избирателей, должно иметь не менее одного депутата в Джамоате. (ЗРТ от 30.05.17 г., №1431) \n Джамоат считается полномочным в случае избрания не менее двух третей от общего числа его депутатов. \n Деятельность Джамоата прекращается в день начала первого собрания Джамоата нового созыва. \n При ликвидации административно-территориальной единицы Джамоат распускается и полномочия депутатов Джамоата прекращаются. \n При преобразовании (объединении, соединении, преобразовании, разделении и выделении) административно-территориальных единиц депутат Джамоата является депутатом вновь организованного Джамоата, в состав которого входят большинство избирателей депутата, и до начала деятельности вновь избранного Джамоата сохраняет свои полномочия. \n При образовании нового посёлка или села решением вопросов определения количества депутатов Джамоата, образования избирательных округов, назначения выборов, а также учреждения избирательной комиссии Джамоата занимается Маджлис народных депутатов соответствующего города или района в соответствии с требованиями настоящего Закона. (ЗРТ от 30.05.17 г., №1431)'}
          </Text>
          <Text style={styles.listItem}>
            {language === 'Тоҷики' ? 'Моддаи 9. Шакли фаъолияти мақомоти худидоракунии шаҳрак ва деҳот' : 'Статья 9. Формы деятельности органов самоуправления посёлков и сёл'}
          </Text>
          <Text style={styles.textItem}>
            {language === 'Тоҷики' ? ' Шакли асосии фаъолияти мақомоти худидоракунии шаҳрак ва деҳот маҷлиси Ҷамоат мебошад, ки соле на камтар аз чор маротиба даъват карда мешавад. Маҷлиси якуми Ҷамоати навинтихобшударо комиссияи интихоботи Ҷамоат дар муддати на дертар аз ду ҳафтаи баъди интихоби вакилони Ҷамоат даъват менамояд. \n Маҷлиси Ҷамоат дар сурати иштироки аз се ду ҳиссаи шумораи умумии вакилони Ҷамоат салоҳиятнок мебошад. \n Маҷлиси якуми Ҷамоатро раиси комиссияи интихоботи Ҷамоат мекушояд ва то интихоб шудани раиси Ҷамоат ба он раисӣ намуда, дар овоздиҳии вакилони Ҷамоат иштирок намекунад. \n Ҷараёни маҷлиси Ҷамоат дар шакли протокол ба расмият дароварда шуда, ба он раисикунанда имзо мегузорад. \n Маҷлиси Ҷамоат ошкоро гузаронида шуда, дар он намояндагони ташкилотҳо, иттиҳодияҳои ҷамъиятӣ, ҳизбҳои сиёсӣ, воситаҳои ахбори омма ва шаҳрвандоне, ки дар ҳудуди шаҳрак ва деҳоти дахлдор зиндагӣ мекунанд, бо мувофиқаи раиси Ҷамоат метавонанд иштирок намоянд. \n Тартиби фаъолияти Ҷамоат ва мақомоти дигари онро дастури Ҷамоат, ки аз тарафи Ҷамоат қабул мегардад, муайян мекунад. (ҚҶТ аз 23.11.15 с., №1243)' : ' Основной формой деятельности органов самоуправления посёлков и сёл является собрание Джамоата, которое созывается не менее четырех раз в году. Первое собрание вновь избранного Джамоата созывается избирательной комиссией Джамоата в срок не позднее двух недель после выбора депутатов Джамоата. \n Собрание Джамоата считается полномочным в том случае, если на нем присутствуют две трети от общего числа депутатов Джамоата. \n Первое собрание Джамоата открывает председатель Избирательной комиссии Джамоата, который до избрания председателя Джамоата председательствует на нем и не участвует в голосовании депутатов Джамоата. \n Ход собрания Джамоата оформляется в форме протокола и подписывается председательствующим. \n Собрание Джамоата проводится открыто и по согласованию с председателем Джамоата в нем могут участвовать представители организаций, общественных объединений, политических партий, средств массовой информации и граждане, проживающие на территории соответствуюшего Джамоата. \n Порядок деятельности Джамоата и других его органов определяется регламентом Джамоата, принятым Джамоатом. (ЗРТ от 23.11.15 г., № 1243)'}
          </Text>
          <Text style={styles.listItem}>
            {language === 'Тоҷики' ? 'Моддаи 9\u00B9. Санадҳои Ҷамоат' : 'Статья 9\u00B9. Акты Джамоата'}
          </Text>
          <Text style={styles.textItem}>
            {language === 'Тоҷики' ? ' Ҷамоат дар доираи ваколати худ қарор қабул мекунад. \nn Қарорҳои Ҷамоат, ки дорои хусусияти умумиҳатмӣ мебошанд, бо тартиби муқаррарнамудаи санадҳои меъёрии ҳуқуқии Ҷумҳурии Тоҷикистон ба Вазорати адлияи Ҷумҳурии Тоҷикистон барои санҷиш ва бақайдгирии давлатӣ фиристода мешаванд. \nn Тартиби интишори расмӣ ва мавриди амал қарор додани санадҳои Ҷамоатро Президенти Ҷумҳурии Тоҷикистон муқаррар менамояд. \nn Қарорҳои Ҷамоат дар ҳудуди шаҳрак ва деҳот амал намуда, иҷрои онҳо барои шахсони воқеӣ ва ҳуқуқӣ ҳатмӣ мебошад. (ҚҶТ аз 23.11.15 с., №1243)' : ' Джамоат в пределах своих полномочий принимает постановление. \nn Постановления Джамоата, имеющие общеобязательный характер, направляются в порядке, установленном нормативными правовыми актами Республики Таджикистан, в Министерство юстиции Республики Таджикистан для проверки и государственной регистрации. \nn Порядок официального опубликования и введения в действие актов Джамоата определяется Президентом Республики Таджикистан. \nn Постановления Джамоата действуют в пределах посёлков и сёл и их выполнение для физических и юридических лиц является обязательным. (ЗРТ от 23.11.15 г., № 1243)'}
          </Text>
          <Text style={styles.listItem}>
            {language === 'Тоҷики' ? 'Моддаи 10. Ваколатҳо Ҷамоат дар соҳаи ташкили фаъолияти он' : 'Статья 10. Полномочия Джамоата в сфере организации его деятельности'}
          </Text>
          <Text style={styles.textItem}>
            {language === 'Тоҷики' ? ' Ҷамоат дар соҳаи ташкили фаъолияти худ ваколатҳои зерин дорад: (ҚҶТ аз 30.05.17с., №1431) \n 1) интихоб ва озод намудани раиси Ҷамоат, муовини ў (муовинони ў) ва котиби Ҷамоат; \n 2) таъсиси комиссияҳои Ҷамоат; \n 3) қабули дастури Ҷамоат; \n 4) қабули буҷети шаҳрак ва деҳот, ҳангоми зарурат ворид намудани тағийру иловаҳо ба он, назорати иҷро ва тасдиқи ҳисоботи иҷрои буҷет; \n 5) таъйин ва гузаронидани овоздиҳии аҳолии шаҳрак ва деҳот оид ба масъалаҳои аҳамияти маҳаллидошта; (ҚҶТ аз 30.05.17с., №1431) \n 6) ирсоли пешниҳод оид ба тағйири воҳидҳои маъмурию ҳудудӣ, номгузорӣ, иваз кардани номи деҳаю кўчаҳо, майдону дигар объектҳо бо тартиби муайяннамудаи санадҳои меъёрии ҳуқуқӣ; (ҚҶТ аз 30.05.17с., №1431) \n 7) ҳамкорӣ бо мақомоти худидоракунии шаҳраку деҳоти дигар ва таъсиси ассотсиатсияҳо бо онҳо; \n 8) шунидани ҳисоботи раиси Ҷамоат; \n 9) шунидани гузориши роҳбарони ташкилотҳо, мақомоти худфаъолияти ҷамъиятӣ ва дигар шахсони ҳуқуқии дар ҳудуди шаҳрак ва деҳот воқеъбуда оид ба масъалаҳои ваколатҳои Ҷамоат; (ҚҶТ аз 16.04.12с., №827; (ҚҶТ аз 30.05.17с., №1431) \n 10) тасдиқ ё бекор кардани қарорҳои Шўрои Ҷамоат ва қарорҳои раиси Ҷамоат; (ҚҶТ аз 30.05.17с., №1431) \n 11) ҳалли дигар масъалаҳое, ки санадҳои меъёрии ҳуқуқӣ пешбинӣ кардаанд.' : ' Джамоат в сфере организации его деятельности имеет следующие полномочия: \n 1) выбирает и освобождает председателя Джамоата, его заместителя (заместителей) и секретаря Джамоата; \n 2) учреждает комиссии Джамоата; \n 3) принимает регламент Джамоата; \n 4) принимает бюджет посёлка и села, при необходимости, вносит в него изменения и дополнения, контролирует исполнение и утверждает отчет по его исполнению; \n 5) назначает и проводит голосование для населения посёлка и села по вопросам местного значения; \n 6) вносит предложения по изменению административно-территориальных единиц, присвоению названий, переименованию сёл и улиц, площадей и других объектов в порядке, установленном нормативными правовыми актами; \n  7) сотрудничает с другими органами самоуправления посёлков и сёл и создает с ними ассоциации; \n 8) заслушивает отчет председателя Джамоата; \n 9) заслушивает доклад руководителей организаций, органов общественной самодеятельности и других юридических лиц, расположенных на территории посёлка и села, по вопросам полномочий Джамоата; (ЗРТ от 30.05.17 г., №1431) \n 10) утверждает или признает недействительными решения совета Джамоата и постановления председателя Джамоата; \n 11) решает другие вопросы, предусмотренные нормативными правовыми актами.'}
          </Text>
          <Text style={styles.listItem}>
            {language === 'Тоҷики' ? 'Моддаи 11. Ваколатҳои Ҷамоат дар соҳаи иқтисод ва молия' : 'Статья 11. Полномочия Джамоата в сфере экономики и финансов'}
          </Text>
          <Text style={styles.textItem}>
            {language === 'Тоҷики' ? ' Ҷамоат дар соҳаи иқтисод ва молия ваколатҳои зерин дорад: \n 1) таҳия ва тасдиқи барномаҳои рушди иқтисодию иҷтимоии шаҳрак ва деҳот; \n 2) тартиб додан ва пешбурди китобҳои хоҷагидорӣ; (ҚҶТ аз 30.05.17с., №1431) \n 3) тасдиқи сохтор, сметаи хароҷот ва ҷадвали воҳидҳои кории Ҷамоат бо пешниҳоди Шўрои Ҷамоат ва дар доираи меъёри муқаррарнамудаи Ҳукумати Ҷумҳурии Тоҷикистон тасдиқ намудани он; (ҚҶТ аз 30.05.17с., №1431) \n 4) назорати истифода ва ҳифзи заминҳои наздиҳавлигӣ ва заминҳои хоҷагиҳои иловагии ёрирасони шахсӣ; \n 5) мутобиқи қонунгузорӣ таъсис додани корхонаҳои коммуналй ва дигар корхонаҳо, кооперативҳо, устохонаҳо, сехҳои ҳунарҳои халқӣ ва дигар ташкилотҳо; (ҚҶТ аз 16.04.12с., №827) \n 6) дастгирӣ намудани ташаббусҳо оид ба рушди иқтисодию иҷтимоии шаҳрак ва деҳот; \n 7) ҳалли дигар масъалаҳо мутобиқи санадҳои меъёрии ҳуқуқии Ҷумҳурии Тоҷикистон; \n 8) назорати истифода ва ҳифзи заминҳои ҳудуди шаҳрак ва деҳот; (ҚҶТ аз 30.05.17с., №1431) \n 9) идоракунии молу мулки шаҳрак ва деҳот; (ҚҶТ аз 30.05.17с., №1431) \n 10) назорати иҷрои буҷети Ҷамоат. (ҚҶТ аз 30.05.17с., №1431)' : ' Джамоат в сфере экономики и финансов имеет следующие полномочия: \n 1) разработка и утверждение программ социально-экономического развития посёлков и сёл; \n 2) составление и ведение хозяйственных книг; (ЗРТ от 30.05.17 г., №1431) \n 3) утверждение структуры, сметы расходов, штатного расписания Джамоата по представлению совета Джамоата в пределах норм, установленных Правительством Республики Таджикистан; \n 4) контроль за использованием и сохранением приусадебных и дополнительных личных подсобных земель; \n 5) учреждение коммунальных и других предприятий, кооперативов, мастерских, цехов по народным промыслам и других организаций в соответствии с законодательством; (ЗРТ от 16.04.12г., №827) \n 6) поддержка инициатив по социально-экономическому развитию посёлков и сёл; \n 7) решение других вопросов в соответствии с нормативными правовыми актами; \n 8) контроль за использованием и охраной земель территории посёлков и сёл; (ЗРТ от 30.05.17 г., №1431) \n 9) управление имуществом посёлков и сёл; (ЗРТ от 30.05.17 г., №1431) \n 10) контроль выполнения бюджета Джамоата; (ЗРТ от 30.05.17 г., №1431)'}
          </Text>
          <Text style={styles.listItem}>
            {language === 'Тоҷики' ? 'Моддаи 12. Ваколатҳои Ҷамоат дар соҳаи қонуният, тартиботи ҷамъиятӣ ва мудофиа' : 'Статья 12. Полномочия Джамоата в сфере законности, общественного порядка и обороны'}
          </Text>
          <Text style={styles.textItem}>
            {language === 'Тоҷики' ? ' Ҷамоат дар соҳаи қонуният, тартиботи ҷамъиятӣ ва мудофиа салоҳиятҳои зерин дорад: \n 1) ташкили таъмини иҷрои қонунҳо ва дигар санадҳои меъёрии ҳуқуқӣ дар доираи салоҳияти Ҷамоат; \n 2) ҷалби аҳолӣ барои гузаронидани чорабиниҳои мудофиаи гражданӣ ва бартараф намудани оқибатҳои офатҳои табиӣ; \n 3) андешидани тадбирҳо дар доираи салоҳияти Ҷамоат оид ба таъмини тартиботи ҷамъиятӣ дар ҳудуди шаҳрак ва деҳот ва пешгирии ҳолатҳои ҳуқуқвайронкунӣ. (ҚҶТ аз 30.05.17с., №1431)' : ' Джамоат в сфере законности, общественного порядка и обороны имеет следующие полномочия: \n 1) организация обеспечения исполнения законов и других нормативных правовых актов в пределах полномочий Джамоата; \n 2) привлечение населения к проведению мероприятий по гражданской обороне и устранению последствий стихийных бедствий; \n 3) принятие мер по обеспечению общественного порядка на территории посёлков и сёл и предотвращению случаев правонарушения в пределах полномочий Джамоата. (ЗРТ от 30.05.17 г., №1431)'}
          </Text>
          <Text style={styles.listItem}>
            {language === 'Тоҷики' ? 'Моддаи 13. Салоҳияти Ҷамоат дар соҳаи ҳифзи иҷтимоӣ, ободонӣ ва ҳалли масъалаҳои муҳити зист' : 'Статья 13. Полномочия Джамоата в сфере социальной защиты, благоустройства и охраны окружающей среды'}
          </Text>
          <Text style={styles.textItem}>
            {language === 'Тоҷики' ? ' Ҷамоат дар соҳаи ҳифзи иҷтимоӣ, ободонӣ ва ҳифзи муҳити зист ваколатҳои зерин дорад: (ҚҶТ аз 30.05.17с., №1431) \n  1) иштирок дар таҳия ва амалӣ намудани барномаи мусоидат ба шуғли аҳолӣ ва андешидани чораҳо оид ба танзими раванди муҳоҷират; (ҚҶТ аз 30.05.17с., №1431) \n 2) назорати танзими анъана ва ҷашну маросимҳо; \n 3) андешидани чораҳо оид ба беҳбудии шароити манзилию маишии аҳолӣ, ҳифзи саломатӣ, муҳофизати муҳити зист; \n 4)ҳалли масъалаҳои аҳамияти маҳаллидошта (нигоҳдорӣ ва ободонии роҳҳо, кўчаҳо, майдонҳо, муассисаҳои фарҳангӣ, манбаъҳо ва шабакаҳои обтаъминкунӣ, бозорҳои маҳаллӣ, инчунин нигоҳдории қабристонҳо ва таъмини тозагии маҳал аз партовҳо) ва тасдиқи қоидаҳо оид ба амалӣ намудани онҳо. (ҚҶТ аз 16.04.12с., №827; (ҚҶТ аз 30.05.17с., №1431)' : ' Джамоат в сфере социальной защиты, благоустройства и охраны окружающей среды имеет следующие полномочия: \n 1) участие в разработке и осуществлении программ содействия занятости населения и принятие мер по регулированию миграционных процессов; (ЗРТ от 30.05.17 г., №1431) \ 2) контроль за упорядочением традиций, торжеств и обрядов; \n 3) принятие мер по улучшению жилищно–бытовых условий населения, охраны здоровья и защиты окружающей среды; \n 4) решение вопросов местного значения (содержание и благоустройство дорог, улиц, площадей, культурных учреждений, источников и сетей водоснабжения, местных рынков, а также содержание кладбищ и обеспечение чистоты местности от мусора) и утверждение правил по их осуществлению. (ЗРТ от 30.05.17 г., №1431)'}
          </Text>
          <Text style={styles.listItem}>
            {language === 'Тоҷики' ? 'Моддаи 14. Ваколатҳои алоҳидаи вогузоршавандаи давлатӣ ба мақомоти худидоракунии шаҳрак ва деҳот' : 'Статья 14. Отдельные государственные полномочия, возлагаемые на органы самоуправления посёлков и сёл'}
          </Text>
          <Text style={styles.textItem}>
            {language === 'Тоҷики' ? ' Ба ваколатҳои алоҳидаи давлатӣ, ки ба мақомоти худидоракунии шаҳрак ва деҳот вогузошта мешаванд, инҳо метавонанд дохил шаванд: \n 1) мусоидат дар мустаҳкам намудани қонуният ва тартиботи ҷамъиятӣ; \n 2) назорати иҷрои қоидаҳои низоми шиносномавӣ ва мутобиқи тартиби муқарраргардида бақайдгирӣ ва азқайдбарории шаҳрвандон; \n 3) баҳисобгирии оилаҳои камбизоат ва ташкили дастгирии иҷтимоии онҳо; \n 4) сабти асноди ҳолати шаҳрвандӣ мутобиқи қонунгузорӣ; \n 5) анҷом додани амалиёти алоҳидаи нотариалӣ мутобиқи меъёрҳои пешбининамудаи Қонуни Ҷумҳурии Тоҷикистон «Дар бораи нотариати давлатӣ»; \n 6) ба шаҳрвандон додани ҳуҷҷатҳои тасдиқкунандаи ҷои зист, вазъи оилавӣ ва ҳолатҳои дигар; \n 7) мусоидат дар ташкили даъват ба хизмати ҳарбӣ ва дигар чорабиниҳои ҳарбӣ; \n 8) таъмини чораҳои зарурӣ барои ҳифзи зироатҳои кишоварзӣ, пешгирии касалиҳои ҳайвонот, муҳофизати ҷангал ва буттазорҳо; \n 9) таҳия ва татбиқи чорабиниҳо оид ба муҳофизати муҳити зист; \n 10) мусоидат ба ҷамъоварии андоз ва дигар пардохтҳо; \n 11) мусоидат ба таъмини ҳимояи ҳуқуқи истеъмолкунандагон; \n 12) дигар ваколатҳое, ки мутобиқи санадҳои меъёрии ҳуқуқии Ҷумҳурии Тоҷикистон вогузошта шудаанд. \n Вогузории ваколатҳои алоҳидаи давлатӣ ба мақомоти худидоракунии шаҳрак ва деҳот дар асоси санадҳои меъёрии ҳуқуқӣ ва шартномаи тарафҳо бо маблағгузории ин ваколатҳо сурат мегирад. Иҷрои босифати шартнома ваколат ва масъулияти тарафҳо мебошад. (ҚҶТ аз 30.05.17с., №1431)' : ' К отдельным государственным полномочиям, возлагаемым на органы самоуправления посёлков и сёл, могут входить: \n 1) содействие в укреплении законности и правопорядка; \n 2) контроль за исполнением правил паспортного режима и в соответствии с установленным порядком регистрации прибытия и выбытия граждан; \n 3) учет малообеспеченных семей и организация их социальной поддержки; \n 4) регистрация актов гражданского состояния в соответствии с законодательством; \n 5) осуществление отдельных нотариальных действий в соответствии с нормами Закона Республики Таджикистан «О государственном нотариате»; \n 6) выдача гражданам документов, подтверждающих их место жительства, семейное и иное положение; \n 7) содействие в организации призыва на военную службу и других военных мероприятий; \n 8) обеспечение необходимых мер по защите сельскохозяйственных угодий, предотвращению болезней скота, защите лесов и кустарников; \n 9) разработка и реализация мероприятий по защите окружающей среды; \n 10) содействие сбору налогов и других платежей; \n 11) содействие обеспечению защите прав потребителей; \n 12) другие полномочия, возложенные нормативными правовыми актами. \n Делегирование отдельных государственных полномочий на органы самоуправления посёлков и сёл производится на основании нормативных правовых актов и по договору сторон с финансированием этих полномочий. Качественное выполнение договора является полномочиями и ответственностью сторон. (ЗРТ от 30.05.17 г., №1431)'}
          </Text>
          <Text style={styles.listItem}>
            {language === 'Тоҷики' ? 'Моддаи 15. Вакили Ҷамоат ва ҳуқуқу вазифаҳои он' : 'Статья 15. Депутат Джамоата и его права и обязанности'}
          </Text>
          <Text style={styles.textItem}>
            {language === 'Тоҷики' ? ' Вакили Ҷамоат намояндаи халқ буда, ҳуқуқ дорад фикри худро озодона баён кунад, мувофиқи иродаи худ овоз диҳад, манфиати интихобкунандагонро ҳимоя намояд. \n Вакили Ҷамоат вазифадор аст дар маҷлиси Ҷамоат иштирок намуда, барои иҷрои қарорҳои Ҷамоат мусоидат кунад, мунтазам ба интихобкунандагон аз фаъолияти худ ҳисобот диҳад. \n Вакили Ҷамоат фаъолияти хизматиашро (кориашро) қатъ накарда, ваколати худро амалӣ мегардонад. ў ҳангоми иштирок дар маҷлисҳои Ҷамоат ва мақомоти он аз иҷрои вазифаҳои хизматӣ (корӣ) бо нигоҳ доштани музди мехнат озод карда мешавад. \n Ваколати вакили Ҷамоат дар ҳолатҳои зерин пеш аз мўҳлат қатъ мегардад: \n 1) дар асоси аризаи ў; \n 2) эътибори қонунӣ пайдо кардани ҳалномаи суд дар хусуси ғайри қобили амал донистани ў; (ҚҶТ аз 30.05.17с., №1431) \n 3) эътибори қонунӣ пайдо кардани ҳукми айбдоркунандаи суд; (ҚҶТ аз 30.05.17с., №1431) \n 4) қатъи шаҳрвандии Ҷумҳурии Тоҷикистон ё соҳиб шудан ба шаҳрвандии давлати дигар; (ҚҶТ аз 30.05.17с., №1431) \n 5) пеш аз мўҳлат пароканда шудани Ҷамоат; \n 6) бе сабаби узрнок се маротиба пай дар пай иштирок накардан дар маҷлиси Ҷамоат; \n 7) ҳангоми вафот; \n 8) дигар намудани ҷои зисти доимӣ берун аз ҳудуди шаҳрак ва деҳот. (ҚҶТ аз 30.05.17с., №1431) \n Масъалаи пеш аз муҳлат катъ намудани ваколати вакили Ҷамоат дар маҷлиси Ҷамоат баррасӣ гардида, он бо қарори Ҷамоат бо тарафдории на камтар аз се ду ҳиссаи шумораи умумии вакилони Ҷамоат қабул карда мешавад. (ҚҶТ аз 16.04.12с., №827; (ҚҶТ аз 30.05.17с., №1431)' : ' Депутат Джамоата, являясь народным представителем, имеет право свободно выражать свое мнение, голосовать по своей воле и защищать интересы избирателей. \n Депутат Джамоата обязан принимать участие на собраниях Джамоата, способствовать исполнению решений Джамоата, систематически отчитываться о своей деятельности перед избирателями. \n Депутат Джамоата осуществляет свои полномочия, не прекращая своей служебной (функциональной) деятельности. На период участия на собраниях Джамоата и его органов он освобождается от исполнения служебных (функциональных) обязанностей с сохранением заработной платы. \n Полномочия депутата Джамоата могут быть прекращены досрочно при следующих обстоятельствах: \n 1) на основании личного заявления; \n 2) при вступлении в законную силу решения суда о признании его недееспособным; \n 3) при вступлении в законную силу обвинительного приговора суда; \n 4) при прекращении гражданства Республики Таджикистан или приобретении гражданства другого государства; (ЗРТ от 30.05.17 г., №1431) \n 5) при досрочном роспуске Джамоата; \n 6) при отсутствии на собрании Джамоата более трех раз подряд по неуважительным причинам; \n 7) в случае смерти; \n 8) в случае переезда на постоянное место жительства за пределы посёлка и села. (ЗРТ от 30.05.17 г., №1431) \n Вопрос о досрочном прекращении полномочий депутата Джамоата рассматривается собранием Джамоата и принимается постановлением Джамоата при поддержке не менее двух третей голосов от общего числа депутатов Джамоата. (ЗРТ от 16.04.12г., №827; (ЗРТ от 30.05.17 г., №1431)'}
          </Text>
          <Text style={styles.listItem}>
            {language === 'Тоҷики' ? 'Моддаи 16. Шўрои Ҷамоат' : 'Статья 16. Совет Джамоата'}
          </Text>
          <Text style={styles.textItem}>
            {language === 'Тоҷики' ? ' Шўрои Ҷамоат мақомоти коллегиалии Ҷамоат буда, дар ҳайати 7 - 11 нафар ташкил карда мешавад. \n Ба ҳайати Шўрои Ҷамоат раиси Ҷамоат, муовини ў (муовинони ў), котиби Ҷамоат дохил мешаванд. Маҷлиси Ҷамоат ба ҳайати Шўрои Ҷамоат дигар вакилонро низ интихоб карда метавонад. (ҚҶТ аз 30.05.17с., №1431) \n Шўрои Ҷамоат ҳар моҳ на камтар аз як маротиба даъват карда мешавад.' : ' Совет Джамоата является коллегиальным органом Джамоата и образуется в количестве 7-11 человек. \n В совет Джамоата входят председатель Джамоата, его заместитель (его заместители), секретарь Джамоата. Собрание Джамоата вправе включать в состав совета Джамоата и других депутатов. \n  Совет Джамоата созывается ежемесячно не менее одного раза.'}
          </Text>
          <Text style={styles.listItem}>
            {language === 'Тоҷики' ? 'Моддаи 17. Ваколатҳои Шўрои Ҷамоат (ҚҶТ аз 30.05.17с., №1431)' : 'Статья 17. Полномочия Совета Джамоата'}
          </Text>
          <Text style={styles.textItem}>
            {language === 'Тоҷики' ? ' Шўрои Ҷамоат: \n 1) маҷлиси Ҷамоатро даъват мекунад ва рўзномаи онро тартиб медиҳад; \n 2) фаъолияти комиссияҳои Ҷамоатро ҳамоҳанг месозад ва барои самаранокии кори онҳо мусоидат менамояд; \n 3) барои ба вазифа таъйин ва аз вазифа озод намудани роҳбарони корхонаю ташкилотҳои мансуби моликияти коммуналии шаҳр ё ноҳия, ки дар ҳудуди Ҷамоат ҷойгиранд, розигӣ медиҳад; (ҚҶТ аз 30.05.17с., №1431) \n 4) роҳбарони корхонаю ташкилотҳоеро, ки аз буҷети Ҷамоат маблағгузорӣ карда мешаванд ё Ҷамоат онҳоро таъсис додааст, бо пешниҳоди раиси Ҷамоат ба вазифа таъйин ва аз вазифа озод мекунад ва барои тасдиқ ба маҷлиси Ҷамоат пешниҳод мекунад; (ҚҶТ аз 30.05.17с., №1431) \n 5) назорати иҷрои қарорҳои Ҷамоатро ташкил мекунад; \n 6) ба баррасии роҳбарони мақомоти дахлдори ҳокимияти давлатӣ оид ба шахсони мансабдори онҳо ва дигар ташкилотҳо, ки дар ҳудуди шаҳрак ва деҳот фаъолият менамоянд, дар сурати аз ҷониби онҳо иҷро накардани талаботи қонунҳо, санадҳои меъёрии ҳуқуқии Ҷумҳурии Тоҷикистон, санадҳои мақомоти ҳокимияти давлатӣ ва мақомоти худидоракунии шаҳрак ва деҳот пешниҳод ирсол менамояд; \n Шўрои Ҷамоат оид ба масъалаҳои салоҳияти худ қарор қабул мекунад, ки ба он раиси Ҷамоат имзо мегузорад.' : ' Совет Джамоата: \n 1) созывает собрание Джамоата и составляет его повестку дня; \n 2) координирует деятельность комиссий Джамоата и содействует эффективности их работ; \n 3) дает согласие на назначение на должность и освобождение от должности руководителей предприятий и организаций, относящихся к коммунальной собственности города или района, находящихся на территории Джамоата; \n 4) по представлению председателя Джамоата назначает на должность и освобождает от должности руководителей предприятий и организаций, финансируемых из бюджета Джамоата или учрежденных Джамоатом, и представляет для утверждения на собрание Джамоата; \n 5) организует контроль исполнения решений Джамоата; \n 6) вносит предложение на рассмотрение руководителей соответствующих органов государственной власти об их должностных лицах и других организациях, действующих на территории посёлка или села в случае невыполнения ими требований законов, нормативных правовых актов Республики Таджикистан, актов органов государственной власти и органов самоуправления посёлков и сёл. \n Совет Джамоата по вопросам своей компетенции принимает решение, которое подписывается председателем Джамоата. (ЗРТ от 16.04.12г., №827)'}
          </Text>
          <Text style={styles.listItem}>
            {language === 'Тоҷики' ? 'Моддаи 18. Комиссияҳои Ҷамоат' : 'Статья 18. Комиссии Джамоата'}
          </Text>
          <Text style={styles.textItem}>
            {language === 'Тоҷики' ? ' Ҷамоат бо мақсади баррасии пешакӣ ва тайёр намудани масъалаҳо, инчунин назорати иҷрои қарорҳои Ҷамоат, Шўрои Ҷамоат ва мақомоти ҳокимияти давлатӣ аз ҳайати вакилони Ҷамоат комиссияҳо таъсис медиҳад. (ҚҶТ аз 30.05.17с., №1431) \n Тартиби интихоб ва фаъолияти комиссияҳоро дастури Ҷамоат муайян мекунад.' : ' С целью предварительного рассмотрения и подготовки вопросов, а также для организации контроля за исполнением решений Джамоата, совета Джамоата и органов государственной власти Джамоат создает комиссии из числа депутатов Джамоата. \ Порядок избрания и деятельность комиссий определяет регламент Джамоата.'}
          </Text>
          <Text style={styles.listItem}>
            {language === 'Тоҷики' ? 'Моддаи 19. Тартиби интихоб ва озод намудани раис, муовини (муовинони) раис ва котиби Ҷамоат' : ' Статья 19. Порядок избрания и освобождения председателя, заместителя (заместителей) председателя и секретаря Джамоата'}
          </Text>
          <Text style={styles.textItem}>
            {language === 'Тоҷики' ? ' Раиси Ҷамоат дар маҷлиси Ҷамоат аз ҳайати вакилони он бо тарафдории аксарияти шумораи умумии вакилони Ҷамоат ба вазифа интихоб ва аз вазифа озод карда мешавад. \n Номзадии раиси Ҷамоатро раиси шаҳр, ноҳия пешниҳод менамояд. \n Муовини (муовинони) раис ва котиби Ҷамоат аз ҳайати вакилони Ҷамоат бо пешниҳоди раиси Ҷамоат дар маҷлиси Ҷамоат бо тарафдории аксарияти шумораи умумии вакилони Ҷамоат ба вазифа интихоб ва аз вазифа озод карда мешавад. \n Номзадии раис, муовини ў (муовинони ў) ва котиби Ҷамоат бояд ба талаботи Қонуни Ҷумҳурии Тоҷикистон «Дар бораи хизмати давлатӣ» мувофиқат намояд. \n Раис, муовини ў (муовинони ў) ва котиби Ҷамоат дар мавридҳои зерин пеш аз мўҳлат аз вазифа озод карда мешаванд: \n 1) дар асоси аризаи шахсӣ; (ҚҶТ аз 30.05.17с., №1431) \n 2) эътибори қонунӣ пайдо кардани даромадани ҳалномаи суд дар хусуси ғайри қобили амал донистани ў; (ҚҶТ аз 30.05.17с., №1431) \n 3) эътибори қонунӣ пайдо кардани ҳукми айбдоркунандаи суд; (ҚҶТ аз 30.05.17с., №1431) \n 4) мунтазам иҷро накардани ўҳдадориҳои хизматӣ; \n 5) қатъи шаҳрвандии Ҷумҳурии Тоҷикистон ё соҳиб шудан ба шаҳрвандии давлати дигар; (ҚҶТ аз 30.05.17с., №1431) \n 6) дигар асосҳои муқаррарнамудаи қонунгузории Ҷумҳурии Тоҷикистон; \n 7) изҳори нобоварӣ намудани аз се ду ҳиссаи вакилони Ҷамоат; (ҚҶТ аз 30.05.17с., №1431)' : ' Председатель Джамоата избирается на должность из числа его депутатов на собрании Джамоата при поддержке большинства от общего числа депутатов Джамоата. \n Кандидатуру председателя представляет председатель города, района. \n Заместитель (заместители) председателя и секретарь Джамоата из числа депутатов Джамоата по представлению председателя Джамоата избираются на должность и освобождаются от должности на собрании Джамоата при поддержке большинства от общего числа депутатов Джамоата. \n Кандидатуры председателя, его заместителя (его заместителей) и секретаря Джамоата должны соответствовать требованиям Закона Республики Таджикистан «О государственной службе». редседатель, его заместитель (его заместители) и секретарь Джамоата могут быть досрочно освобождены от должности при следующих обстоятельствах: \n 1) на основании личного заявления; (ЗРТ от 30.05.17 г., №1431) \n 2) при вступлении в законную силу решения суда о признании его недееспособным; \n 3) при вступлении в законную силу обвинительного приговора суда; \n 4) при постоянном невыполнении служебных обязанностей; \n 5) при прекращении гражданства Республики Таджикистан или приобретении гражданства другого государства; (ЗРТ от 30.05.17 г., №1431) \n 6) других основаниях, установленных законодательством Республики Таджикистан. \n 7) при выражении недоверия двумя третьями части депутатов Джамоата; (ЗРТ от 30.05.17 г., №1431)'}
          </Text>

          <Text style={styles.listItem}>
            {language === 'Тоҷики' ? 'Моддаи 20. Ваколатҳои раиси Ҷамоат' : 'Статья 20. Полномочия председателя Джамоата'}
          </Text>
          <Text style={styles.textItem}>
            {language === 'Тоҷики' ? ' Раиси Ҷамоат: \n 1) иҷрои талаботи Конститутсияи Ҷумҳурии Тоҷикистон, қонунҳо, дигар санадҳои меъёрии ҳуқуқӣ, санадҳои мақомоти ҳокимияти давлатӣ ва Ҷамоатро таъмин мекунад; (ҚҶТ аз 30.05.17с., №1431) \n 2) тартиби манфиатҳои давлатӣ ва муқаддасоти миллиро дар ҳудуди шаҳрак ва деҳот амалӣ менамояд; (ҚҶТ аз 30.05.17с., №1431) \n 3) ба фаъолияти Ҷамоат роҳбарӣ ва дар ҷаласаи Шўрои Ҷамоат раисӣ мекунад ва ба санадҳои онҳо имзо мегузорад; \n 4) Ҷамоатро дар муносибат бо мақомоти давлатӣ, ташкилотҳо ва иттиҳодияҳои ҷамъиятӣ намояндагӣ мекунад; \n 5) шахси воқеӣ ва намояндаи шахси ҳуқуқиро қабул мекунад, муроҷиати онҳоро баррасӣ менамояд, доир ба ҳалли онҳо дар доираи салоҳияти худ чораҳои дахлдор меандешад; (ҚҶТ аз 30.05.17с., №1431) \n 6) кормандони Ҷамоатротибқи талаботи қонунгузории Ҷумҳурии Тоҷикистон ба кор қабул ва аз кор озод менамояд; (ҚҶТ аз 30.05.17с., №1431) \n 7) соле на камтар аз як маротиба аз фаъолияти худ ба Ҷамоат ҳисобот медиҳад.' : ' Председатель Джамоата: \n 1) обеспечивает исполнение требований Конституции Республики Таджикистан, законов, других нормативных правовых актов, актов органов государственной власти и Джамоата; \n 2) координирует дела по пропаганде государственных интересов и национальных достояний на территории посёлков и сёл; (ЗРТ от 30.05.17 г., №1431) \n 3) руководит деятельностью Джамоата и председательствует на заседании совета Джамоата, подписывает акты, принятые ими; \n 4) представляет Джамоат в отношениях с государственными органами, организациями, общественными объединениями; \n 5) принимает физических и представителей юридических лиц, рассматривает их обращения, в пределах своих полномочий принимает соответствующие меры по их решению; (ЗРТ от 30.05.17 г., №1431) \n 6) принимает на работу и освобождает от должности сотрудников Джамоата в соответствии с требованиями законодательства Республики Таджикистан; (ЗРТ от 30.05.17 г., №1431) \n 7) в течение года не менее одного раза отчитывается о своей деятельности перед Джамоатом.'}
          </Text>
          <Text style={styles.listItem}>
            {language === 'Тоҷики' ? 'Моддаи 21. Ваколатхои раиси Ҷамоат дар соҳаи иқтисод, буҷет ва молия' : 'Статья 21. Полномочия председателя Джамоата в сфере экономики, бюджета и финансов'}
          </Text>
          <Text style={styles.textItem}>
            {language === 'Тоҷики' ? ' Дар соҳаи иқтисод, буҷет ва молия раиси Ҷамоат: \n 1) лоиҳаи буҷети шаҳрак ва деҳотро дар мувофиқа бо мақомоти молияи ноҳия, шаҳр таҳия намуда, барои қабул пешниҳод менамояд ва оид ба иҷрои он ҳисобот медиҳад; \n 1) ҷалби сармоягузориро мутобиқи санадҳои меъёрии ҳуқуқӣ ташкил мекунад; \n 1) сари вақт аз аҳолӣ ҷамъоварӣ намудани маблағҳои андоз, суғурта ва дигар пардохтҳоро ташкил мекунад; \n 1) баҳисобгирии соҳибкорони инфиродӣ, хоҷагиҳои деҳқонӣ, ширкатҳои хоҷагидорӣ, кооперативҳои истеҳсолӣ ва дигар ташкилотҳоро ба роҳ мемонад; \n 1) баҳисобгирии аҳолӣ, саршумори чорвои аҳолӣ ва дигар ҳисоботи муқарраршудаи омориро ташкил мекунад. \n 1) корхонаҳои коммуналӣ ва дигар корхонаҳо, кооперативҳо, устохонаҳо, сехҳои ҳунарҳои халқӣ ва дигар ташкилотҳоро мутобиқи қонунгузорӣ метавонад таъсис диҳад.' : ' В сфере экономики, бюджета и финансов председатель Джамоата: \n 1) разрабатывает проект бюджета посёлка и села по согласованию с финансовыми органами района, города, представляет его для принятия и отчитывается о его исполнении; \n 2) организует привлечение инвестиций в соответствии с нормативными правовыми актами; \n 3) организует своевременное поступление от населения налоговых, страховых и других платежей; \n 4) ведет учет индивидуальных предпринимателей, дехканских хозяйств, хозяйственных товариществ, производственных кооперативов и других организаций; \n 5) организует учет населения, численности скота населения и другие определённые статистические учеты; \n 6) вправе учреждать коммунальные и другие предприятия, кооперативы, мастерские, цеха по народным промыслам и другие организации в соответствии с законодательством. (ЗРТ от 16.04.2012г., №827)'}
          </Text>
          <Text style={styles.listItem}>
            {language === 'Тоҷики' ? 'Моддаи 22. Ваколатҳои раиси Ҷамоат дар соҳаи қонуният, тартиботи ҷамъиятӣ ва мудофиа' : 'Статья 22. Полномочия председателя Джамоата в сфере законности, общественного порядка и обороны'}
          </Text>
          <Text style={styles.textItem}>
            {language === 'Тоҷики' ? ' Дар соҳаи қонуният, тартиботи ҷамъиятӣ ва мудофиа раиси Ҷамоат: \n 1) сабти асноди ҳолати шаҳрвандиро мутобиқи қонунгузорӣ ташкил мекунад; \n 2) амалиёти алоҳидаи нотариалиро тибқи меъёрҳои пешбининамудаи Қонуни Ҷумҳурии Тоҷикистон «Дар бораи нотариати давлатӣ» таъмин менамояд; \n 3) ҳуҷҷатҳои тасдиқкунандаи ҷои зист, вазъи оилавӣ ва дигар ҳолатҳоро ба шаҳрвандон медиҳад; \n 4) оид ба ҳифзи молу мулки бесоҳиб, ки ба ихтиёри давлат мегузарад, тадбирҳо меандешад; \n 5) оид ба таъмини тартиботи ҷамъиятӣ дар ҳудуди шаҳрак ва деҳот, бақайдгирӣ ва азқайдбарории шаҳрвандон, пешгирии ҳолатҳои ҳуқуқвайронкунӣ дар доираи ваколатҳои Ҷамоат тадбирҳо меандешад; (ҚҶТ аз 30.05.17с., №1431) \n 6) ба ташкил ва гузаронидани тадбирҳои оммавию сиёсӣ, маънавию маърифатӣ роҳбарӣ намуда, ба мақомоти маҳаллии ҳокимияти давлатӣ дар ин самт мусоидат мекунад; \n 7) ба даъвати ўҳдадорони ҳарбӣ ва даъватшавандагон мусоидат менамояд, аҳолиро барои гузаронидани чорабиниҳои мудофиаи гражданӣ ва ҳолатҳои фавқуллода ҷалб мекунад.' : ' В сфере законности, общественного порядка и обороны председатель Джамоата: \n 1) в соответствии с законодательством организует регистрацию актов гражданского состояния; \n 2) обеспечивает осуществление отдельных нотариальных действий в соответствии с нормами, предусмотренными Законом Республики Таджикистан «О государственном нотариате»; \n 3) выдает гражданам документы, подтверждающие их место жительства, семейное и иное положение; \n 4) принимает меры по защите бесхозяйного имущества, переходящего в распоряжение государства; \n 5) принимает меры по обеспечению общественного порядка в пределах посёлков и сёл, регистрации прибытия и выбытия граждан и предотвращению случаев правонарушения в пределах полномочий Джамоата; (ЗРТ от 16.04.12г., №827; (ЗРТ от 30.05.17 г., №1431) \n 6) руководит проведением массово-политических, культурно - просветительных мероприятий, оказывает содействие в этом направлении местным органам государственной власти; \n 7) содействует в призыве военнообязанных и допризывников, привлекает население для проведения соответствующих мероприятий по гражданской обороне и чрезвычайным ситуациям.'}
          </Text>
          <Text style={styles.listItem}>
            {language === 'Тоҷики' ? 'Моддаи 23. Ваколатҳои раиси Ҷамоат дар соҳаи ҳифзи иҷтимоӣ, ободонӣ ва муҳити зист' : 'Статья 23. Полномочия председателя Джамоата в сфере социальной защиты, благоустройства и охраны окружающей среды'}
          </Text>
          <Text style={styles.textItem}>
            {language === 'Тоҷики' ? ' Дар соҳаи ҳифзи иҷтимоӣ, ободонӣ ва муҳити зист раиси Ҷамоат: \n 1) баҳисобгирии оилаҳои камбизоат ва дастгирии иҷтимоии онҳоро ташкил менамояд; \n 2) оид ба беҳтар намудани шароити моддию маишии маъюбон, оилаҳои камбизоат ва шахсоне, ки аз офатҳои табиӣ зарар дидаанд, мусоидат менамояд; \n 3) дар бобати беҳбудии шароити манзилию маишии аҳолӣ, ҳифзи муҳити зист чораҳо меандешад; \n 4) ободонии майдону гулгаштҳо ва қабристонҳоро ташкил мекунад; \n 5) аҳолӣ, ташкилотҳои ҳудуди шаҳрак ва деҳотро дар ободонӣ ва сабзу хуррамгардонӣ ҷалб менамояд; \n 6) назорати вазъи санитарии маҳалҳо, манбаъҳои обтаъминкунӣ, муассисаҳои таълимию фарҳангӣ, тандурустӣ ва дигар иншоотҳо, инчунин риояи қоидаҳои байториро ташкил менамояд; \n 7) назорати истифода ва ҳифзи замини ҳудуди шаҳрак ва деҳотро мутобиқи санадҳои меъёрии ҳуқуқӣ ташкил мекунад ва масъалаҳои вобаста ба заминро ба муҳокимаи Ҷамоат пешниҳод менамояд; \n 8) фаъолияти комиссияро оид ба иҷрои Қонуни Ҷумҳурии Тоҷикистон «Дар бораи танзими анъана ва ҷашну маросимҳо дар Ҷумҳурии Тоҷикистон» назорат мекунад ва барои фаъолияти мунтазами комиссияи мазкур дар шаҳрак ва деҳот шароит муҳаё менамояд.' : ' В сфере социальной защиты, благоустройства и охраны окружающей среды председатель Джамоата: \n 1) организует учёт малообеспеченных семей и их социальную поддержку; \n 2) содействует улучшению материальных и бытовых условий инвалидов, малоимущих семей и лиц, пострадавших от стихийных бедствий; \n 3) принимает меры по улучшению жилищно - бытовых условий населения и защите окружающей среды; \n 4) организует работы по благоустройству площадей, скверов и кладбищ; \n 5) привлекает население, организации, находящиеся на территории поселка и села, к благоустройству и озеленению территории; \n 6) организует контроль санитарного состояния местности, источников водоснабжения, учебных и культурных учреждений, учреждений здравоохранения, других объектов, а также соблюдения правил ветеринарии; \n 7) организует контроль использования и защиты земель территории посёлков и сёл в соответствии с нормативными правовыми актами и выносит земельные вопросы на рассмотрение Джамоата; \n 8) ведет контроль деятельности комиссии по исполнению Закона Республики Таджикистан «Об упорядочении традиций, торжеств и обрядов в Республике Таджикистан», создает условия для постоянной деятельности данной комиссии в посёлке и селе.'}
          </Text>
          <Text style={styles.listItem}>
            {language === 'Тоҷики' ? 'Моддаи 24. Дастгоҳи Ҷамоат' : 'Статья 24. Аппарат Джамоата'}
          </Text>
          <Text style={styles.textItem}>
            {language === 'Тоҷики' ? ' Ҷамоат дорои дастгоҳ мебошад, ки он аз тарафи раиси Ҷамоат ташкил ва роҳбарӣ карда мешавад. \n Сохтор, шумораи кормандон ва низомномаи дастгоҳи Ҷамоат тибқи талаботи санадҳои меъёрии ҳуқуқии Ҷумҳурии Тоҷикистон аз тарафи маҷлиси Ҷамоат бо пешниҳоди раиси Ҷамоат тасдиқ карда мешаванд. (ҚҶТ аз 30.05.17с., №1431) \n Низомномаи намунавии дастгоҳи Ҷамоат аз ҷониби Ҳукумати Ҷумҳурии Тоҷикистон тасдиқ карда мешавад (ҚҶТ аз 04.04.2019 с., № 1598). \n Маблағгузории дастгоҳи Ҷамоат дар доираи буҷети пешбинишудаи шаҳрак ва деҳот сурат мегирад (ҚҶТ аз 04.04.2019 с., № 1598).' : ' Джамоат имеет свой аппарат, который организует и руководит председатель Джамоата. \n Структура, численность сотрудников и положение аппарата Джамоата утверждаются в соответствии с требованиями нормативных правовых актов Республики Таджикистан собранием Джамоата по представлению председателя Джамоата. (ЗРТ от 30.05.17 г., №1431) \n Примерное Положение аппарата Джамоата утверждается Правительством Республики Таджикистан (ЗРТ от 04.04.2019 г., № 1598). \n Финансирование аппарата Джамоата осуществляется в пределах, предусмотренных бюджетом посёлка и села (ЗРТ от 04.04.2019 г., № 1598).'}
          </Text>
        </View>
      </ScrollView>
    </View>
  );
};

